.agriTechExpo {

	.section-contact-detail {
		position: relative;
		display: block;
		padding: 40px 0;
		background: #F7F7F7;
		.contact-inner {
			display: block;
			width: 100%;
			margin: 0 auto;
			overflow: hidden;
		}
		.contact-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			color: #242343;
			text-align: center;
			.title-name {
				position: relative;
				display: block;
				font-family: 'Averta-Bold';
				font-size: 26px;
				line-height: 31px;
				margin-bottom: 20px;
				padding: 0 0 25px;
				text-transform: uppercase;
				&:before {
					content: "";
					position: absolute;
					width: 104px;
					height: 19px;
					background-image: url("images/bg-title.svg");
					background-repeat: no-repeat;
					background-position: top center;
					background-size: 100% 100%;
					bottom: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					pointer-events: none;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.col-left {
			width: 50%;
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			padding-right: 80px;
			font-size: 16px;
			line-height: 1.5;
			color: #666666;
			.contact-title {
				margin-bottom: 40px;
				font-family: 'Averta-Bold';
				font-size: 24px;
				line-height: 34px;
				text-transform: uppercase;
				color: #242343;
				span {
					padding-bottom: 8px;
					border-bottom: 1px solid #C9C9C9;
				}
			}
			.contact-title-2 {
				font-family: 'Averta-Semibold';
				font-size: 18px;
				line-height: 22px;
				text-transform: uppercase;
				color: #242343;
			}
			.contact-info {
				font-family: Arial;
				p {
					display: table;
					margin-bottom: 8px;
					span {
						white-space: nowrap;
						display: inline-block;
					}
				}
				i, svg {
					display: inline-block;
					display: table-cell;
					width: 60px;
					padding-left: 0;
					padding-top: 5px;
					color: #0BB451;
					font-size: 16px;
					text-align: center;
				}
			}
		}
		.col-right {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 50%;
			padding: 20px;
			background-color: #0BB451;
			background: linear-gradient(298.44deg, #0BB451 0%, #109E4C 52.39%);
			background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='515' height='483' viewBox='0 0 515 483' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='515' height='483' fill='url(%23paint0_linear)'/%3e%3cg opacity='0.1' clip-path='url(%23clip0)'%3e%3cpath d='M154.944 91H55.8232L-85 483H23.7729L105.445 217.294L187.242 483H295.891L154.944 91Z' fill='%23242343'/%3e%3cpath d='M-20.1668 333.177C-20.1668 333.177 -16.5709 376.323 -40.7503 372.696C-66.9136 368.819 -66.0457 341.055 -60.2178 331.426C-54.2659 321.796 -31.2025 289.03 -47.3221 266.519C-47.3221 266.519 9.09652 291.406 -8.88305 368.319C-8.88305 368.319 16.6603 309.915 55.8434 296.284C95.0264 282.902 131.606 290.531 157.149 285.903C182.692 281.401 204.392 276.774 230.307 257.64C230.307 257.64 234.895 318.795 216.668 344.557C195.092 375.072 175.005 396.082 111.022 413.091C46.9156 430.099 -34.0544 419.969 -36.4104 483C-36.4104 483 -39.0143 443.23 -20.0428 415.467C-1.07124 387.703 25.5881 335.428 110.03 313.667C110.03 313.667 48.0316 291.156 -13.5949 384.952C-13.7189 384.952 -8.75904 351.561 -20.1668 333.177Z' fill='white'/%3e%3c/g%3e%3cdefs%3e%3clinearGradient id='paint0_linear' x1='515' y1='483' x2='203.064' y2='302.826' gradientUnits='userSpaceOnUse'%3e%3cstop stop-color='%230BB451'/%3e%3cstop offset='1' stop-color='%23109E4C'/%3e%3c/linearGradient%3e%3cclipPath id='clip0'%3e%3crect width='296' height='392' fill='white' transform='translate(0 91)'/%3e%3c/clipPath%3e%3c/defs%3e%3c/svg%3e");
			background-repeat: no-repeat;
			background-size: cover;
			.contact-form {
				position: relative;
				display: block;
				width: 100%;
				font-size: 16px;
				line-height: 1.5;
				color: #242732;
				::-webkit-input-placeholder { /* Edge */
					color: #8D8D8D;
				}
				:-ms-input-placeholder { /* Internet Explorer 10-11 */
					color: #8D8D8D;
				}
				::placeholder {
					color: #8D8D8D;
				}
				.form-title {
					position: relative;
					float: none;
					display: inline;
					width: auto;
					padding-bottom: 6px;
					margin-bottom: 30px;
					font-family: 'Averta-Bold';
					font-size: 20px;
					line-height: 22px;
					text-transform: uppercase;
					color: #fff;
					//border-bottom: 1px solid #C9C9C9;
				}
				label {
					padding-right: 0;
					font-family: Arial;
					font-size: 16px;
					line-height: 17px;
					color: #fff;
				}
				.form-control {
					font-size: 15px;
					&:focus {
						color: #333;
						background-color: #fff;
						outline: 0;
						box-shadow: none;
						border: 1px solid #333;
					}
				}
				textarea {
					max-height: 200px;
					min-height: 100px;
				}
				.btn-submit {
					position: relative;
					display: inline-block;
					padding: 7px 13px;
					border: 1px solid #FFFFFF;
					border-radius: 3px;
					background: transparent;
					font-family: 'Averta-Semibold';
					color: #fff;
					font-size: 14px;
					text-align: center;
					text-decoration: none;
					outline: none;
					box-shadow: none;
				}
			}
		}
		@media (max-width: 991px) {
			.col-left {
				width: 100%;
				float: none;
				padding-right: 0;
				margin-bottom: 40px;
			}
			.col-right {
				width: 100%;
				float: none;
				padding: 15px;
			}
		}
		@media (max-width: 767px) {
			.col-left {
				.contact-title {
					font-size: 24px;
				}
				.contact-info {
					i, svg {
						width: 40px;
					}
				}
			}
			.col-right {
				padding: 20px 15px 10px;
			}
		}
		@media (max-width: 370px) {
			.col-left {
				.contact-title {
					font-size: 20px;
				}
				.contact-title-2 {
					font-size: 16px;
				}
			}
		}
	}

	.contact-maps-area {
		position: relative;
		display: block;
		margin-top: 30px;
		margin-bottom: 30px;
		@media (min-width: 992px) {
			padding-top: 45px;
			margin-top: 50px;
			margin-bottom: 50px;
			border-top: 1px dashed rgba(112, 112, 112, .2);
		}
		.tabs-maps-area {
			.nav {
				display: flex;
				justify-content: flex-end;
				border: none;
				li {
					margin-left: 23px;
				}
				.nav-link {
					padding: 8px 0;
					border: none;
					background: none;
					&.active {
						font-weight: bold;
					}
				}
			}
			.tab-content {
				margin-top: 10px;
			}
		}
	}
}