.globe-hethang {
    position: absolute;
    z-index: 3;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    &:hover {
        background-color: rgba(0, 0, 0, 0.8);
    }
    &:after {
        content: 'Ht hng';
        position: absolute;
        width: 60px;
        height: 60px;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding-top: 14px;
        margin: auto;
        border-radius: 50%;
        font-family: tahoma;
        font-size: 12px;
        line-height: 16px;
        color: #242424;
        background-color: #fff;
        text-align: center;
        text-transform: uppercase;
        word-spacing: 5px;
    }
}
.globe-sapban {
    position: absolute;
    z-index: 3;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    &:hover {
        background-color: rgba(0, 0, 0, 0.8);
    }
    &:after {
        content: 'Sp m bn';
        position: absolute;
