.tag-ship {
    position: absolute;
    z-index: 2;
    bottom: 0;
    left: 0;
    display: inline-block;
    max-width: 100%;
    width: 160px;
    height: auto;
    vertical-align: middle;
    background: transparent;
    color: #fff;
    pointer-events: none;
    text-align: left;
    font-family: 'Arial';
    * {
        white-space: nowrap;
    }
    .tag-ship__label {
        position: relative;
        display: inline-block;
        float: left;
        width: 70px;
        height: 35px;
        line-height: 35px;
        vertical-align: middle;
        background: #D52329;
        font-size: 12px;
        font-weight: bold;
        text-align: center;
    }
    .tag-ship__value {
        position: relative;
        display: inline-block;
        width: calc(100% - 70px);
        height: 35px;
        line-height: 35px;
        padding-left: 8px;
        vertical-align: middle;
        background: #242424;
        .txt-phiship {
            position: relative;
            display: inline-block;
            float: left;
            height: 35px;
            line-height: 35px;
            font-size: 11px;
            vertical-align: top;
            font-weight: normal;
        }
        .num-phiship {
            position: relative;
            display: inline-block;
            padding-left: 3px;
            height: 35px;
            line-height: 35px;
            font-size: 14px;
            vertical-align: top;
        }
        sub, sup {
            font-size: 75%;
            line-height: 0;
            position: relative;
            vertical-align: baseline;
            font-weight: normal;
        }
    }
    @media (max-width: 355px) {
        .tag-ship__label {
            width: 58px;
            height: 26px;
            line-height: 26px;
            font-size: 10px;
            font-weight: normal;
        }
        .tag-ship__value {
            width: calc(100% - 58px);
            height: 26px;
            line-height: 26px;
            padding-left: 5px;
            font-size: 12px;
            .txt-phiship {
                height: 26px;
                line-height: 26px;
                font-size: 10px;
            }
            .num-phiship {
                height: 26px;
                line-height: 26px;
                font-size: 12px;
            }
        }
    }
}



