.agriTechExpo {


	// Start san pham noi bat
	.section-product-slide-fuild {
		position: relative;
		display: block;
		padding: 20px 0;
		overflow: hidden;
		.list-product-inner {
			display: block;
		}
		.product-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			background: #fff;
			&:before {
				content: "";
				position: absolute;
				width: 100%;
				height: 1px;
				bottom: 7px;
				left: 0;
				background: #F7F0EA;
			}
			.title-name {
				position: relative;
				display: table;
				font-family: 'Averta-Bold';
				font-size: 20px;
				line-height: 30px;
				margin-bottom: 20px;
				padding: 0 30px 0 0;
				text-transform: uppercase;
				background: inherit;
				max-width: calc(100% - 70px);
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.list-product {
			position: relative;
			display: block;
			vertical-align: top;
			width: 100%;
			max-width: 100%;
			.list-product-slide {
				position: relative;
				display: block;
				width: 100%;
				overflow: hidden;
				.product-item {
					position: relative;
					display: block;
					//width: 100%;
					max-width: 350px;
					padding: 0;
					margin: 0;
					.item-inner {
						position: relative;
						display: block;
						background: #FFFFFF;
						border: 1px solid #F7F0EA;
						border-radius: 3px;
						.item-image {
							position: relative;
							display: block;
							overflow: hidden;
							margin-bottom: 10px;
							text-align: center;
							.item-image-a {
								position: relative;
								display: block;
								margin: 0 auto;
								img {
									width: 100%;
									height: auto;
									max-height: 100%;
									object-fit: scale-down;
								}
							}
						}
						.item-info {
							position: relative;
							display: table;
							padding: 0 11px;
							.cate-name {
								font-size: 13px;
								line-height: 140%;
								color: #757575;
								line-height: 18px;
								font-family: 'Arial';
							}
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								margin: 0 0 10px;
								padding: 0;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									height: 43px;
									padding: 0;
									margin: 0;
									font-family: 'Averta-Semibold';
									font-size: 16px;
									line-height: 1.4;
									color: #242343;
									text-decoration: none;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									-webkit-line-clamp: 2;
									word-break: break-word;
								}
							}
							.box-price {
								position: relative;
								display: block;
								height: 25px;
								margin: 0 0 10px;
								overflow: hidden;
								white-space: nowrap;
								display: flex;
								align-items: center;
								.price-sale {
									position: relative;
									display: block;
									width: auto;
									float: left;
									font-size: 16px;
									font-family: 'Averta-Bold';
									color: #0BB451;
									line-height: 25px;
								}
								.price-old {
									position: relative;
									display: block;
									width: auto;
									text-decoration: line-through;
									float: left;
									color: #ABABAB;
									font-size: 12px;
									padding-left: 8px;
									line-height: 26px;
								}
								.percent-discount {
									position: relative;
									display: block;
									width: auto;
									min-width: 40px;
									float: left;
									font-size: 12px;
									font-family: 'Averta-Bold';
									margin-left: 5px;
									padding: 0 4px;
									height: 20px;
									line-height: 20px;
									background: #FF4343;
									border-radius: 3px;
									color: #fff;
									text-align: center;
								}
							}
						}
					}
					.item-inner:hover {
						.item-info {
							.btn-viewmore {
								background: #004989;
							}
						}
					}
				}
			}
			.btn-viewall {
				position: relative;
				display: block;
				width: 100px;
				height: 36px;
				line-height: 36px;
				color: #fff;
				background: #0BB451;
				border: 0;
				border-radius: 3px;
				font-size: 12px;
				text-align: center;
				text-decoration: none;
				margin: 30px auto 15px;
				text-transform: uppercase;
			}
		}
		.swiper-pagination-product-hot {
			position: relative;
			display: block;
			text-align: center;
			margin-top: 15px;
			display: none;
			.swiper-pagination-bullet {
				width: 8px;
				height: 8px;
				margin: 4px;
				display: inline-block;
				border-radius: 50%;
				background: #d7d7d7;
				opacity: 1;
			}
			.swiper-pagination-bullet-active {
				opacity: 1;
				background: var(--swiper-pagination-color,var(--oFair-btn-color));
			}
		}

		.group-bnt-control {
			position: absolute;
			display: table;
			top: 6px;
			right: 0;
			background: #fff;
			height: 30px;
			width: 70px;
			.swiper-button-next-product-hot, .swiper-button-prev-product-hot {
				position: absolute;
				top: 0;
				width: 28px;
				height: 28px;
				line-height: 28px;
				z-index: 6;
				cursor: pointer;
				outline: none;
				background: #F7F0EA;
				opacity: .8;
				text-align: center;
				border-radius: 50%;
				&:after {
					content: "";
					position: absolute;
					display: block;
					top: 0;
					right: 0;
					width: 28px;
					height: 28px;
					line-height: 28px;
					text-align: center;
					vertical-align: middle;
					z-index: 2;
					font-family: 'Font Awesome 5 Pro';
					font-size: 21px;
					font-weight: 300;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					display: inline-block;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
				}
				&:hover {
					background: #0BB451;
					color: #fff;
				}
				&.swiper-button-disabled {
					opacity: .3;
				}
			}
			.swiper-button-prev-product-hot {
				top: 0;
				right: 30px;
				&:after {
					content: "\f104";
				}
			}
			.swiper-button-next-product-hot {
				top: 0;
				right: 0;
				&:after {
					content: "\f105";
				}
			}

		}
		@media (max-width: 991px) {
			.list-product {
				.list-product-slide {
					.product-item {
						.item-inner {
						}
					}
				}
			}
		}
		@media (max-width: 767px) {
			.list-product {
				.list-product-slide {
					.product-item {
					}
				}
			}
			.product-box-title .title-name {
				font-size: 18px;
			}
		}
		@media (max-width: 575px) {
			.list-product {
				.list-product-slide {
					overflow: visible;
				}
			}
			.product-box-title .title-name {
				max-width: calc(100% - 60px);
				font-size: 15px;
				padding-right: 10px;
			}
		}


	}
