	// Start san pham noi bat

	.wrapper-product-category {
		position: relative;
		display: block;
		width: 100%;
		overflow: hidden;
		clear: both;
		padding-top: 30px;
		.wrapper-product-category-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			text-align: center;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 24px;
				line-height: 31px;
				margin-bottom: 30px;
				text-transform: uppercase;
				@media (max-width: 370px) {
					font-size: 20px;
				}
				&:after {
					content: "";
					position: absolute;
					display: block;
					width: 100%;
					height: 5px;
					background: #0BB451;
					bottom: -10px;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
	}
	.wrapper-content {
		margin-top: 30px;
	}
	.col-page-left {
		position: relative;
		display: block;
		width: 240px;
		float: left;
		overflow: hidden;
		height: auto;
		padding-right: 12px;
		margin-bottom: 20px;
		::-webkit-scrollbar {
			width: 5px;
			background-color: #f2f3f5;
		}
		::-webkit-scrollbar-track {
			-webkit-box-shadow: none;
			-moz-box-shadow: none;
			box-shadow: none;
			background-color: #f2f3f5;
		}
		::-webkit-scrollbar-thumb {
			background-color: #ddd;
			border-radius: 2.5px;
		}
		.title-module {
			position: relative;
			display: block;
			overflow: hidden;
			clear: both;
			height: 34px;
			line-height: 34px;
			margin: 0;
			margin-bottom: 15px;
			font-family: 'Averta-Semibold';
			font-size: 16px;
			border-bottom: 3px solid #0BB451;
			text-transform: uppercase;
			&[data-toggle="collapse"] {
				cursor: pointer;
				&:after {
					position: absolute;
					top: 0;
					right: 0;
					width: 34px;
					height: 34px;
					text-align: center;
					content: "\f068";
					font-family: 'Font Awesome 5 Pro';
					font-weight: 300;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					display: inline-block;
					font-size: 18px;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
					line-height: 34px;
					color: #c3c3c3;
				}
				&.collapsed {
					cursor: pointer;
					&:after {
						content: "\f067";
					}
				}
			}
		}
		.module-current-filter {
			position: relative;
			display: block;
			overflow: hidden;
			clear: both;
			margin-bottom: 15px;
			padding: 0 10px 10px;
			background: #F7F0EA;
			.item-current-filter {
				position: relative;
				display: block;
				overflow: hidden;
				clear: both;
				padding: 0 5px;
				color: #2964c1;
				border-radius: 4px;
				.fal,.fa {
					position: relative;
					display: block;
					width: 20px;
					height: 20px;
					line-height: 20px;
					margin-top: 2px;
					margin-right: 5px;
					float: left;
					background: #eee;
					border-radius: 50%;
					color: #2964c1;
					font-weight: 100;
					text-align: center;
				}
				&:hover {
					background: #f2f2f2;
					color: #d52329;
					.fal,.fa {
						color: #d52329;
						border: 1px solid #d52329;
					}
				}
				&.delete-all{
					color: #666;
					text-align: center;
					&:hover {
						color: #d52329;
					}
				}
			}
		}
		.module-category-list {
			position: relative;
			display: block;
			overflow: hidden;
			clear: both;
			margin-bottom: 10px;
			padding: 0 10px 10px;
			background: #F7F0EA;
			color: #242343;
			a {
				color: #242343;
			}
			.input-search {
				position: relative;
				display: block;
				width: 100%;
				height: 32px;
				padding: 0 10px;
				margin-bottom: 5px;
				border-radius: 4px;
				border: 1px solid #dddddd;
				outline: none;
				position: sticky;
				top: 0;
				z-index: 2;
				&:hover,
				&:focus,
				&:active {
					border: 1px solid #66afe9;
				}
			}
			.list-item-cate {
				position: relative;
				display: block;
				width: 100%;
				max-height: 300px;
			}
			.item-category-child {
				position: relative;
				display: block;
				overflow: hidden;
				clear: both;
				padding: 3px 5px;
				margin-bottom: 3px;
				font-family: 'Arial';
				line-height: 16px;
				&:hover {
					color: #0BB451;
				}
				&.active {
					font-weight: bold;
				}
				&.lv2 {
					padding-left: 16px;
				}
				&.lv3 {
					padding-left: 28px;
				}
				&.lv4 {
					padding-left: 40px;
				}
			}
		}
		.module-price-filter {
			position: relative;
			display: block;
			overflow: hidden;
			clear: both;
			margin-bottom: 10px;
			padding: 0 10px 10px;
			background: #F7F0EA;
			.form-price-filter {
				position: relative;
				display: block;
				width: 100%;
				padding-top: 5px;
				.form-group {
					position: relative;
					display: block;
					width: 100%;
					overflow: hidden;
					margin-bottom: 0;

					input[type="radio"] {
						margin: 2px 8px 0 0;
						background-color: #ddd;
						border-radius: 50%;
						cursor: pointer;
						line-height: normal;
						outline: 0;
						border: 1px solid #cccccc;
						-webkit-box-shadow: none;
						-moz-box-shadow: none;
						box-shadow: none;
						vertical-align: baseline;
						box-sizing: border-box;
						-webkit-appearance: none;
					}
					input[type="radio"]:after {
						background-color: #fff;
						border-radius: 50%;
						// box-shadow: inset 0 0 0 1px hsla(0,0%,0%,.4), 0 1px 1px hsla(0,0%,100%,.8);
						content: '';
						display: block;
						position: absolute;
						height: 9px;
						width: 9px;
						margin: auto;
						top: 0;
						bottom: 0;
						right: 0;
						left: 0;
					}
					input[type="radio"].active:after,
					input[type="radio"]:checked:after {
						background-color: #0BB451;
						//box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.4), inset 0 2px 2px rgba(255, 255, 255, 0.4), 0 1px 1px rgba(255, 255, 255, 0.8), 0 0 2px 2px rgba(0, 51, 199, 0.2);
					}
					.checkbox, .radio {
						position: relative;
						display: block;
						margin-top: 10px;
						margin-bottom: 10px;
						font-family: 'Arial';
					}
					.checkbox+.checkbox, .radio+.radio {
						margin-top: -5px;
					}
					input {
						position: relative;
						display: block;
						width: 15px;
						float: left;
						margin: 5px 5px 0 0;
						line-height: 25px;
						height: 15px;
					}
					label {
						position: relative;
						display: flex;
						align-items: center;
						padding: 0;
						margin-bottom: 0;
						font-weight: initial;
						white-space: nowrap;
						cursor: pointer;
					}
				}
				.ui-widget-header {
					border: 0;
					background: #0BB451;
					color: #333;
					font-family: 'Averta-Bold';
				}
				.ui-state-active, .ui-widget-content .ui-state-active, .ui-widget-header .ui-state-active, a.ui-button:active, .ui-button:active, .ui-button.ui-state-active:hover {
					border: 1px solid #242424;
					background: #0BB451;
					font-weight: normal;
					color: #fff;
				}
				.search_prices {
					.ui-slider {
						position: relative;
						display: block;
						margin-bottom: 10px;
						background: #E2E2E2;
						border: 0;
						border-radius: 3px;
						height: 3px;
						.ui-slider-handle {
							width: 11px;
							height: 11px;
							top: -4px;
							margin-left: -5.5px;
							background: #ffffff;
							border-radius: 50%;
							cursor: e-resize;
							// border: 0;
						}
					}
					.input-view {
						position: relative;
						display: block;
						width: 100%;
						clear: both;
						height: 30px;
						line-height: 1;
						border: 1px solid #c5c5c5;
						padding: 0 10px;
						color: #0BB451;
						border-radius: 4px;
						outline: none;
					}
				}
			}
			@media (max-width: 991px) {
					display: none;
			}
		}
		.multiple-checkboxes-filter {
			position: relative;
			display: block;
			overflow: hidden;
			clear: both;
			margin-bottom: 10px;
			padding: 0 10px 10px;
			background: #F7F0EA;
			.form-filter {
				position: relative;
				display: block;
				width: 100%;
				padding-right: 3px;
				height: auto;
				max-height: 185px;
				overflow: hidden;
				overflow-y: auto;
				.input-search {
					position: relative;
					display: block;
					width: 100%;
					height: 32px;
					padding: 0 10px;
					margin-bottom: 5px;
					border-radius: 4px;
					border: 1px solid #dddddd;
					outline: none;
					position: sticky;
					top: 0;
					z-index: 2;
					&:hover,
					&:focus,
					&:active {
						border: 1px solid #66afe9;
					}
				}
				.form-group {
					position: relative;
					display: block;
					width: 100%;
					margin-bottom: 0;
					overflow: hidden;
					border-radius: 5px;
					input[type=checkbox], input[type=radio] {
						position: relative;
						display: block;
						width: 15px;
						float: left;
						margin: 0;
						margin-right: 5px;
						line-height: 25px;
						height: 25px;
					}
					label {
						position: relative;
						display: block;
						float: left;
						margin-bottom: 0;
						font-weight: initial;
						cursor: pointer;
						width: calc(100% - 20px);
					}
					&:hover {
						background: #f2f2f2;
					}
				}
			}
		}

		.module-ads {
			position: relative;
			display: block;
			width: 100%;
			clear: both;
			overflow: hidden;
			.box-img-ads {
				position: relative;
				display: block;
				width: 100%;
				clear: both;
				overflow: hidden;
				margin-bottom: 20px;
				.link {
					position: relative;
					display: block;
					width: 100%;
					clear: both;
					overflow: hidden;
					img {
						&:hover {
 							-webkit-filter: brightness(105%);
						}
					}
				}
			}
		}
	}
	// End col-page-left
	.col-page-primary {
		position: relative;
		display: block;
		width: calc(100% - 240px);
		float: left;
		overflow: hidden;
		height: auto;
		margin-bottom: 30px;
		@media (max-width: 991px) {
			width: 100%;
		}
	}
	.col-page-left,
	.col-page-primary {
		@media (max-width: 991px) {
			width: 100%;
			padding: 0;
			.wrapper-list-outer {
				padding: 0;
			}
		}
	}

	.box-title-page {
		width: 100%;
		padding: 0 0 10px;
		background: #fff;
		line-height: 40px;
		border-bottom: 1px solid #eeeeee;
		display: flex;
		justify-content: space-between;
		align-items: center;
		overflow: hidden;
		.col-left-title {
			position: relative;
			display: block;
			float: left;
			line-height: 40px;
			//height: 40px;
			.title-page {
				position: relative;
				display: inline-block;
				float: none;
				vertical-align: baseline;
				line-height: 40px;
				font-size: 16px;
				margin: 0;
				padding: 0;
				font-family: 'Averta-Bold';
			}
			.count-items {
				position: relative;
				display: inline-block;
				float: none;
				vertical-align: baseline;
				line-height: 40px;
				font-size: 16px;
			}
		}
		.col-right-title {
			position: relative;
			display: block;
			float: right;			
			.box-sort-product {
				position: relative;
				display: -webkit-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				label {
					margin: 0 10px 0;
					float: left;
					font-size: 16px;
				}
				.form-control {
					position: relative;
					display: block;
					float: left;
					flex: auto;
					width: auto;
					margin-top: 5px;
					height: 30px;
					padding: 4px 8px;
					font-family: 'Arial';
					font-size: 14px;
					-webkit-box-shadow: none;
					-moz-box-shadow: none;
					box-shadow: none;
				}
			}
		}
		@media (max-width: 575px) {
			display: table;
			.col-left-title {
				line-height: 1.2;
				padding-top: 8px;
				.title-page {
					line-height: 1.5;
				}
				.count-items {
					line-height: 1.5;
				}
			}
		}
	}

	// Start list cate product
		.wrapper-list-outer {
			position: relative;
			display: block;
			background: #fff;
			min-height: 400px;
			padding-left: 15px;
			&>h2 {
				padding: 0 15px;
				font-size: 21px;
			}
			.box-title {
				width: 100%;
				height: 40px;
				padding: 0;
				background: #fff;
				line-height: 40px;
				padding: 0 15px 10px;
				border-bottom: 1px solid #eeeeee;
				.title-article {
					line-height: 40px;
					font-size: 16px;
				}
			}
			.wrapper-list {
				position: relative;
				display: block;
				padding: 15px 0;
				.list-pro {
					position: relative;
					display: block;
					padding: 0;
					display: flex;
					flex-wrap: wrap;
					@include gap_4(2,8px);
			        @media (min-width: 768px) {
						@include gap_4(4,8px);
			        }
					.item-pro {
						position: relative;
						margin-bottom: 10px;
						border: 1px solid #F7F0EA;
						border-radius: 3px;
						* {
							transition: all 0.6s ease;
						}
						.box-image {
							position: relative;
							display: block;
							overflow: hidden;
							&>a{
								img {
									width: 100%;
									max-width: 100%;
									height: initial;
									max-height: 100%;
								}
							}
							.sale-label {
								position: absolute;
								top: 8px;
								right: 10px;
								display: inline-block;
								height: 32px;
								width: 32px;
								line-height: 32px;
								font-size: 12px;
								color: #d32027;
								text-align: center;
								background: #fff;
								border: 1px solid #d32027;
								border-radius: 50%;
								vertical-align: middle;
								display: none;
							}
							.freeship-label {
								position: absolute;
								top: 8px;
								right: 10px;
								display: inline-block;
								height: 32px;
								width: 32px;
								line-height: 32px;
								font-size: 12px;
								color: #d52329;
								text-align: center;
								// border: 1px solid #d32027;
								border-radius: 50%;
								vertical-align: middle;
								background: #fff;
								z-index: 3;
								overflow: hidden;
								&:after {
									content: "";
									position: absolute;
									top: 0;
									bottom: 0;
									left: 0;
									right: 0;
									margin: 0 auto;
									text-transform: uppercase;
									color: #d52329;
									font-size: 9px;
									line-height: 10px;
									background: url("../images/FreeShip.png");
									background-repeat: no-repeat;
									background-position: center center;
									background-size: initial;
								}
							}
							.gift-label {
								position: absolute;
								top: 40px;
								right: 10px;
								display: inline-block;
								height: 32px;
								width: 32px;
								line-height: 32px;
								font-size: 11px;
								color: #d32027;
								text-align: center;
								// background: #fff;
								// border: 1px solid #d32027;
								border-radius: 50%;
								vertical-align: middle;
								overflow: hidden;
								&:after {
									content: "\f06b";
									position: absolute;
									top: 0;
									bottom: 0;
									left: 0;
									right: 0;
									margin: 0 auto;
									text-transform: uppercase;
									color: #d52329;
									font-size: 20px;
									font-family: 'Font Awesome 5 Pro';
									font-weight: 700;
								}
							}
							.btn-buy-now {
								position: absolute;
								display: block;
								bottom: 0;
								width: 100%;
								height: 0;
								line-height: 30px;
								padding: 0;
								margin: 0;
								background: #d52329;
								color: #fff;
								vertical-align: middle;
								text-align: center;
								transform-origin:bottom;
								transition: height 0.25s ease;
								z-index: -2;
								font-size: inherit;
								font-weight: inherit;
								-ms-touch-action: manipulation;
								touch-action: manipulation;
								cursor: pointer;
								-webkit-user-select: none;
								-moz-user-select: none;
								-ms-user-select: none;
								user-select: none;
								background-image: none;
								border: 0 solid transparent;
								border-radius: 0;
								span,.fal {
									position: relative;
									display: inline-block;
									display: -webkit-inline-box;
									width: 20px;
									height: 30px;
									line-height: 30px;
									text-align: center;
									font-size: 18px;
									margin-right: 0;
									vertical-align: middle;
								}
								&.add_to_cart_disable {
									background: #8b8b8b;
									// pointer-events: none;
									cursor: default;
								}
							}
							.tooltip {
								transition: none;
							}
						}
						.box-title-item {
							position: relative;
							display: block;
							display: grid;
							width: 100%;
							padding: 10px 10px 0;
							.cate-name {
								font-size: 13px;
								line-height: 140%;
								color: #757575;
								line-height: 18px;
							}
							h3 {
								font-weight: normal;
							}
							.title-post-item {
								position: relative;
								display: block;
								display: -webkit-box;
								padding-top: 2px;
								margin: 0 0 5px;
								font-family: 'Averta-Semibold';
								font-size: 16px;
								line-height: 1.4;
								color: #242343;
								height: 68px;
								overflow: hidden;
								text-decoration: none;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 3;
								word-break: break-word;
								&:hover {
									color: #0BB451;
								}
								.icon-deal-hot {
									position: relative;
									padding: 0 5px;
									margin-right: 5px;
									background: #e83948;
									font-size: 10px;
									color: #fff;
									border-radius: 3px;
									line-height: 10px;
									vertical-align: middle;
								}
							}
							.box-price {
								display: inline-block;
								width: 100%;
								overflow: hidden;
								p {
									display: flex;
									flex-wrap: wrap;
									height: 50px;
									font-size: 14px;
									line-height: 14px;
									margin: 0 0 15px;
								}
								.price-current,
								.price-sale {
									width: 100%;
									float: left;
									margin-bottom: 3px;
									font-family: 'Averta-Bold';
									font-size: 16px;
									line-height: 25px;
									color: #0BB451;
								}
								.price-old {
									float: left;
									height: 20px;
									font-size: 13px;
									line-height: 20px;
									color: #ABABAB;
									text-decoration: line-through;
								}
								.percent-discount {
									position: relative;
									display: block;
									width: auto;
									float: left;
									font-size: 12px;
									color: #d52329;
									margin-left: 10px;
									padding: 0 4px;
									height: 20px;
									line-height: 20px;
									background: #FF4343;
									border-radius: 3px;
									color: #fff;
									text-align: center;
								}
							}
							.group-action {
								position: relative;
								display: block;
								clear: both;
								padding-top: 5px;
								.btn-buynow {
									position: relative;
									display: block;
									float: left;
									padding: 5px 15px;
									line-height: 20px;
									text-align: center;
									background: #d8232c;
									color: #ffffff;
									border-radius: 5px;
								}
								.remove-wishlist {
									position: relative;
									display: block;
									float: right;
									padding: 5px 15px;
									line-height: 20px;
									text-align: center;
									background: #bababa;
									color: #ffffff;
									border-radius: 5px;
								}
							}
							.btn-review-now {
								position: relative;
								display: block;
								float: left;
								padding: 5px 15px;
								line-height: 20px;
								text-align: center;
								background: #ffffff;
								color: #d8232c;
								border: 1px solid #d8232c;
								border-radius: 5px;
								font-family: 'Averta-Bold';
								transition: all 0.2s ease 0s;
							}
						}
						&:hover {
							.box-image {
								img {
									// transform: scale(1.1);
									@include transform(translateY(-10px));
								}
								.btn-buy-now {
									display: block;
									height: 30px;
									z-index: 2;
								}
							}
							.btn-review-now {
								background: #d8232c;
								color: #ffffff;
							}
						}
					}
				}
			}

			.box-readmore {
				padding: 20px 0;
				.btn-readmore {
					width: 150px;
					height: 30px;
					line-height: 30px;
					margin: auto;
					color: #000;
					font-size: 14px;
					text-transform: inherit;
					border-top: 1px solid transparent;
					background: #ebebeb;
					border-radius: 4px;
					&:focus {
						border: 1px solid #db2b35;
						outline: none;
						color: #db2b35;
					}
					span {
						position: relative;
					}
				}
			}
			.btn-viewall {
				position: relative;
				display: block;
				width: 100px;
				height: 36px;
				line-height: 36px;
				color: #fff;
				background: #2A3A6B;
				border: 0;
				border-radius: 3px;
				font-size: 12px;
				text-align: center;
				text-decoration: none;
				margin: 30px auto 15px;
				text-transform: uppercase;
			}
			@media (max-width: 767px) {
				.wrapper-list .list-pro .item-pro {
					.box-title-item .title-post-item {
						height: 59px;
						font-size: 14px;
					}
				}
			}
			@media (max-width: 575px) {
				.box-title-page .col-right-title .box-sort-product .form-control {
					width: 135px;
				}
			}
			@media (max-width: 350px) {
				.wrapper-list .list-pro .item-pro .box-title-item {
					padding: 10px 6px 0;
				}
			}
		}
	// End list cate product
}