
.agriTechExpo {

	.section-intro-home {
		position: relative;
		display: block;
		padding: 40px 0 50px;
		// background: #FCFDFF;
		overflow: hidden;
		z-index: 2;
		.intro-inner {
			display: block;
			.intro-box-title {
				position: relative;
				display: block;
				clear: both;
				width: 100%;
				text-align: center;
				.title-name {
					position: relative;
					display: inline-block;
					font-family: 'Averta-Bold';
					font-size: 20px;
					line-height: 31px;
					margin-bottom: 10px;
					@media (min-width: 992px) {
						font-size: 30px
					}
				}
				.title-sub {
					position: relative;
					display: block;
					width: 100%;
					font-size: 15px;
					line-height: 1.7;
					text-align: justify;
					margin: 0 auto;
					margin-bottom: 25px;
					max-width: 670px;
					text-align: center;
					color: #657195;
				}
			}
			.intro-box-content {
				display: flex;
				flex-direction: column;
				flex-wrap: wrap;
				@media (min-width: 992px) {
					flex-direction: row;
				}
				.col-item {
					width:100%;
					margin-bottom: 15px;
					&.content-image {
						flex: none;
						text-align: center;
						@media (min-width: 992px) {
							flex: auto;
							margin-bottom: 0;
						}
						svg,
						img {
							margin: 0 auto;
						}
					}
					&.content-txt {
						font-size: 14px;
						line-height: 1.5;
						color: #657195;
						@media (min-width: 992px) {
							padding-top: 65px;
						}
						h2,h3 {
							font-weight: bold;
							font-size: 20px;
							line-height: 1.5;
							text-align: center;
							color: #302B6B;
						}
						ul {
							padding: 0 0 0 15px;
							list-style: initial;
						}
					}
					@media (min-width: 992px) {
						width: percentage(1/3);
						&:nth-child(1) {
							order: 2;
						}
						&:nth-child(2) {
							order: 1;
						}
						&:nth-child(3) {
							order: 3;
						}
					}
				}
			}
		}
	}

	.banner-hall {
		position: relative;
		display: block;
		z-index: 1;
		width: 100%;
		clear: both;
		position: relative;
		overflow: hidden;
		padding: 25px 0;
		margin-top: -50px;
		.banner-hall-inner {
			position: relative;
			display: block;
			clear: both;
			position: relative;
			padding: 15px;
			background: #fff;
			border-radius: 14px;
			box-shadow: 2px 2px 10px #edf0f7;
			.banner-box-title {
				position: relative;
				display: block;
				width: 100%;
				.title-name {
					position: relative;
					display: inline-block;
					font-family: 'Averta-Bold';
					font-size: 20px;
					line-height: 1.4;
					margin-bottom: 15px;
					@media (min-width: 992px) {
						font-size: 30px
					}
				}
				.title-sub {
					position: relative;
					display: block;
					width: 100%;
					height: 103px;
					font-size: 18px;
					line-height: 1.4;
					text-align: justify;
					margin-bottom: 25px;
				}
				a {
					color: #302B6B;
				}
			}
			.wrap-banner {
				position: relative;
				height: 350px;
				@media (min-width: 1200px) {
					height: auto;
					min-height: 350px;
				}
				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: #EDF0F7;
					filter: blur(10px);
					border-radius: 14px;
					z-index: -1;
				}

				.banner-link {
					position: relative;
					display: block;
					height: 100%;
					border-radius: 14px;
					overflow: hidden;
					cursor: pointer;
					&:hover .icon-360-circle {
						background: #0a9e47;
					}
					.icon-360-circle {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						margin: auto;
						width: 74px;
						height: 74px;
						// padding: 10px;
						border-radius: 50%;
						background: rgba(#034e22,0.6);
						background-blend-mode: multiply;
						display: flex;
						justify-content: center;
						align-items: center;
						&:after {
							content: '';
							display: block;
							bottom: 0;
							right: 0;
							width: 80%;
							height: 80%;
							background-image: url('../images/icon-360-circle.svg');
							background-position: left top;
							background-repeat: no-repeat;
							background-size: 100% 100%;
						}
					}
				}

				.banner-layer {
					display: block;
					position: absolute;
					top: 0;
					right: 0;
					width: 295px;
					height: 100%;
					// padding: 15px;
					// background: rgba(#302B6B, 0.7);
					@media (min-width: 400px) {
						width: 350px;
					}
					.layer-content {
						position: relative;
						display: block;
						width: 100%;
						height: 100%;
						padding: 10% 5% 25% 30%;
						display: flex;
						align-items: center;
						color: #fff;
						border-radius: 14px;
						overflow: hidden;
						&:before {
							content: '';
							display: block;
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							background-image: url('../images/bg-layer-txt.png');
							background-position: left top;
							background-repeat: no-repeat;
							background-size: 100% 100%;
						}
						&:after {
							content: '';
							display: block;
							position: absolute;
							bottom: 0;
							right: 0;
							width: 146px;
							height: 103px;
							background-image: url('../images/icon-leaf.svg');
							background-position: left 8px top;
							background-repeat: no-repeat;
							background-size: 100% 100%;
							pointer-events: none;
						}
						.content-txt {
							position: relative;
							width: 100%;
							display: flex;
							flex-direction: column;
						}
						.txt-des {
							position: relative;
							display: block;
							width: 100%;
							margin-bottom: 20px;
						}
						.btn-join-now {
							padding: 10px 15px;
							width: 100px;
							height: 40px;
							color: #fff;
							background: #0BB451;
							border-radius: 8px;
							&:hover {
								background: #0a9e47;
							}
						}
					}
				}
			}
			img {
				max-width: 100%;
			}
		}
	}

	.section-video-live-pc {
		position: relative;
		display: block;
		clear: both;
		position: relative;
		padding: 20px 0 30px;
		overflow: hidden;
		.video-inner {
			display: block;
		}
		.video-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			overflow: hidden;
			background-repeat: no-repeat;
			background-position: top left;
			background-size: cover;
			padding: 10px 0 10px;
			margin-bottom: 15px;
			text-align: center;
			.box-title-inner {
				position: relative;
				display: block;
			}
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 20px;
				line-height: 31px;
				margin-bottom: 10px;
				@media (min-width: 992px) {
					font-size: 30px
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: auto;
				font-size: 16px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
				overflow: hidden;
				clear: both;
				flex: 0;
				display: none;
			}
		}
		.video-box-content {
			position: relative;
			display: flex;
			max-width: 100%;
			padding: 20px 10px 20px 20px;
			margin: 0 auto;
			background-color: #fff;
			border-radius: 14px;
			box-shadow: 2px 2px 10px #edf0f7;
			overflow: hidden;
			@media (max-width: 767px) {
				flex-direction: column;
			}
			iframe {
				max-width: 100%;
			}

			.slide-video-livestream {
				position: relative;
				display: block;
				overflow: hidden;
				width: percentage(6/10);
				@media (max-width: 767px) {
					width: 100%;
					margin-bottom: 15px;
				}
				.videoName {
					position: relative;
					margin-top: 10px;
					font-weight: bold;
					font-size: 20px;
					line-height: 1.4;
					color: #302B6B;
					&.live {
						padding-left: 28px;
						// background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3ccircle cx='10' cy='10' r='4' fill='%23E00000'/%3e%3cg filter='url(%23filter0_f)'%3e%3ccircle cx='10' cy='10' r='5.5' stroke='%23E00000'/%3e%3c/g%3e%3ccircle cx='10' cy='10' r='5.5' stroke='%23E00000' stroke-opacity='0.5'/%3e%3cdefs%3e%3cfilter id='filter0_f' x='0' y='0' width='20' height='20' filterUnits='userSpaceOnUse' color-interpolation-filters='sRGB'%3e%3cfeFlood flood-opacity='0' result='BackgroundImageFix'/%3e%3cfeBlend mode='normal' in='SourceGraphic' in2='BackgroundImageFix' result='shape'/%3e%3cfeGaussianBlur stdDeviation='2' result='effect1_foregroundBlur'/%3e%3c/filter%3e%3c/defs%3e%3c/svg%3e");
						// background-repeat: no-repeat;
						// background-position: left top 6px;
						&:before {
							content: '';
							position: absolute;
							left: 8px;
							top: 10px;
							width: 9px;
							height: 9px;
							margin-right: 5px;
							border-radius: 50%;
							background: #E00000;
							border: 1px solid #fff;
							box-shadow: 0 0 5px rgba(224,0,0,.8);
							pointer-events: none;
						}
						&:after {
							content: '';
							position: absolute;
							left: 7px;
							top: 9px;
							width: 11px;
							height: 11px;
							margin-right: 5px;
							border-radius: 50%;
							border: 1px solid #E00000;
							box-shadow: 0 0 0 rgba(231,141,172,.4);
							vertical-align: initial;
							animation: live-pulse 1s infinite;
							pointer-events: none;
						}
					}
				}
			}
			.list-thumbnai-video {
				position: relative;
				width: percentage(4/10);
				display: flex;
				flex-direction: column;
				padding-left: 20px;
				padding-right: 8px;
				max-height: 445px;
				overflow-y: auto;
				@media (max-width: 767px) {
					width: 100%;
					padding-left: 0;
				}
				.item-video {
					position: relative;
					width: 100%;
					padding: 10px 0;
					border-bottom: 1px solid #EEEEEE;
					&:first-child {
						padding-top: 0;
					}
					&:last-child {
						padding-bottom: 0;
						border-bottom: 0;
					}
					&.active {
						pointer-events: none;
						a.cliVeo {
							.video-image {
								&:before {
									visibility: hidden;
								}
								&:after {
									content: 'ang pht';
									background-color: rgba(0,0,0,.3);
									background-image: none;
								}
							}
						}
					}
					a.cliVeo {
						position: relative;
						display: flex;
						text-decoration: none;
						.video-image {
							position: relative;
							display: block;
							width: 120px;
							height: 68px;
							overflow: hidden;
							@media (min-width: 992px) {
								height: 75px;
							}
							&:before {
								content: '';
								position: absolute;
								top: 0;
								left: 0;
								width: 100%;
								height: 100%;
								background-color:rgba(255,255,255,0.25);
								background-image: 
									repeating-linear-gradient(
										45deg,
										rgba(0,0,0,0.25),
										rgba(0,0,0,0.25) 2px,
										transparent 0px,
										transparent 4px
									),
									repeating-linear-gradient(
										-45deg,
										rgba(0,0,0,0.2),
										rgba(0,0,0,0.2) 2px,
										transparent 0px,
										transparent 4px
									);
								filter: blur(1px);
								opacity: .8;
							}
							&:after {
								content: '';
								position: absolute;
								display: flex;
								justify-content: center;
								align-items: center;
								top: 0;
								left: 0;
								width: 100%;
								height: 100%;
								color: #fff;
								text-shadow: 0 1px 1px #636363;
								background-color: rgba(0,0,0,.3);
								background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3e%3cg class='fa-group'%3e%3cpath fill='eeeeee' d='M256 8C119 8 8 119 8 256s111 248 248 248 248-111 248-248S393 8 256 8zm115.7 272l-176 101c-15.8 8.8-35.7-2.5-35.7-21V152c0-18.4 19.8-29.8 35.7-21l176 107c16.4 9.2 16.4 32.9 0 42z'%3e%3c/path%3e%3cpath fill='%23aaaaaa' d='M371.7 280l-176 101c-15.8 8.8-35.7-2.5-35.7-21V152c0-18.4 19.8-29.8 35.7-21l176 107c16.4 9.2 16.4 32.9 0 42z'%3e%3c/path%3e%3c/g%3e%3c/svg%3e");
								background-repeat: no-repeat;
								background-position: center center;
								background-size: 30px 30px;
							}
							img {
								object-fit: cover;
								object-position: center;
								overflow: hidden;
							}
						}
						.video-title {
							width: calc(100% - 120px);
							max-height: 60px;
							padding-left: 10px;
							font-size: 14px;
							line-height: 20px;
							font-weight: normal;
							color: #657195;
							overflow: hidden;
							-webkit-box-orient: vertical;
							text-overflow: ellipsis;
							-webkit-line-clamp: 3;
							@media (max-width: 991px) {
								font-size: 14px;
							}
						}
					}
				}
				
			}
		}
	}

