					@media (max-width: 370px) {
						font-size: 20px
					}
					&:after {
						content: "";
						position: absolute;
						display: block;
						width: 100%;
						height: 5px;
						background: #0BB451;
						bottom: -10px;
					}

				}
			}

			.search-filter-box-list { 
				position: relative;
				display: block;
				width: 100%;
				padding: 20px 0;
				background: #F7F7F7;
				.search-filter-list { 
					position: relative;
					display: block;
					display: flex;
					flex-wrap: wrap;
					overflow: hidden;
					width: 100%;
					max-width: 750px;
					margin: 0 auto;

					.form-search-top {
						position: relative;
						display: block;
						width: 100%;
						height: auto;
						.form-group-inline {
							position: relative;
							display: flex;
							align-items: center;
							width: 100%;
							margin: 0 auto 20px;
							border-radius: 4px;
							border: 1px solid #EEEEEE;
							.input-search {
								width: calc(100% - 40px);
								height: 36px;
								padding: 0 10px;
								margin: 0;
								line-height: 36px;
								border: 0;
								box-shadow: none;
								outline: none;
								background-color: #fff;

								&::placeholder {
									font-size: 12px;
									color: #929292;
									font-style: italic;
								}
							}
							.btn-search {
								width: 40px;
								height: 36px;
								padding: 0 10px;
								margin: 0;
								line-height: 36px;
								border: 0;
								box-shadow: none;
								outline: none;
								background-color: #fff;
								background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.7594 8.12971C14.7594 11.7912 11.7912 14.7594 8.12971 14.7594C4.46822 14.7594 1.5 11.7912 1.5 8.12971C1.5 4.46822 4.46822 1.5 8.12971 1.5C11.7912 1.5 14.7594 4.46822 14.7594 8.12971ZM12.7195 14.8408C11.4133 15.7358 9.83266 16.2594 8.12971 16.2594C3.63979 16.2594 0 12.6196 0 8.12971C0 3.63979 3.63979 0 8.12971 0C12.6196 0 16.2594 3.63979 16.2594 8.12971C16.2594 9.83266 15.7358 11.4133 14.8408 12.7195L20.03 17.9086L17.9086 20.03L12.7195 14.8408Z' fill='%23109E4C'/%3e%3c/svg%3e");
								background-repeat: no-repeat;
								background-size: auto;
								background-position: center;
							}
						}
						select {
							width: 100%;
							padding: 0 10px;
							height: 36px;
							line-height: 36px;
							border: 0;
							box-shadow: none;
							outline: none;
							background-color: #fff;
						}
					}
				}
			}
		}
	}
