.agriTechExpo {
	.section-breadcrumb {
		background-color: #0BB451;
		color: #fff;
		nav {
			background: none;
		}
		.breadcrumb {
			background: none;
			margin: 0;
			padding: 20px 0.3rem 15px;
			flex-wrap: nowrap;
			white-space: nowrap;
			overflow-x: auto;
			.breadcrumb-item {
				&+.breadcrumb-item {
					padding-left: 1.1rem;
					&::before{
						content: "|";
						color: #fff;
						padding-right: 1.2rem;
					}
				}
				&.active {
					color: #fff;
				}
				a {
					color: #fff;
					text-decoration: none;
					&:hover {
						color: #fff;
					}
				}
			}
		}
	}



	.section-booth-list {
		position: relative;
		display: block;
		padding: 40px 0;
		background-color: #F7F7F7;
		.booth-inner {
			display: block;
		}
		.booth-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			color: #333333;
			text-align: center;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 24px;
				line-height: 31px;
				margin-bottom: 30px;
				text-transform: uppercase;
				@media (max-width: 370px) {
					font-size: 20px;
				}
				&:after {
					content: "";
					position: absolute;
					display: block;
					width: 100%;
					height: 5px;
					background: #0BB451;
					bottom: -10px;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
			.title-menu {
				position: relative;
				display: block;
				width: 100%;
				line-height: 31px;
				white-space: nowrap;
				margin-bottom: 30px;
				span {
					font-family: 'Averta-Bold';
					color: #333333;
					margin-right: 20px;
				}
				.menu-item {
					display: inline-block;
					padding: 0 13px;
					height: 24px;
					line-height: 24px;
					font-size: 15px;
					color: #878787;
					margin-right: 8px;
					text-decoration: none;
					&:last-child {
						margin-right: 0;
					}
					&:hover {
						background: #3DD8B32e;
					}
					&.active {
						background: #3DD8B3;
						color: #fff;
					}
				}
			}
		}
		.booth-box-list {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 100%;
			.booth-list {
				position: relative;
				display: block;
				overflow: hidden;

				.booth-item {
					position: relative;
					display: block;
					width: 100%;
					max-width: 350px;
					padding: 0;
					margin: 0 auto 30px;
					flex: auto;
					.item-inner {
						position: relative;
						display: block;
						padding: 0;
						background: #FFFFFF;
						border: 1px solid #E8E8E8;
						.item-image {
							position: relative;
							display: block;
							overflow: hidden;
							padding: 10px 15px;
							.item-image-a {
								position: relative;
								display: block;
								display: flex;
								justify-content: center;
								width: 80px;
								height: 80px;
								margin: 0 auto;
								img {
									width: 100%;
									height: auto;
									max-height: 100%;
									object-fit: scale-down;
								}
							}
						}
						.item-info {
							position: relative;
							display: block;
							padding: 20px 15px 12px;
							background: #F7F0EA;
							.ratings {
								font-size: 13px;
								line-height: 1.5;
								letter-spacing: -0.05em;
								color: #878787;
								.ratings-value {
									color: #ED1C94;
									font-family: 'Averta-Bold';
								}
							}
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								margin: 0 0 15px;
								padding: 0;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									height: 38px;
									padding: 0;
									margin: 0;
									font-family: 'Averta-Bold';
									font-size: 14px;
									line-height: 1.4;
									color: #242343;
									text-decoration: none;
									text-transform: uppercase;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									-webkit-line-clamp: 3;
									word-break: break-word;
									letter-spacing: -0.05em;
								}
							}
							.item-description {
								display: block;
								display: -webkit-box;
								height: 60px;
								padding: 0;
								margin-bottom: 10px;
								font-family: 'Arial';
								font-size: 14px;
								color: #666666;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 3;
							}
							.list-type-pro {
								position: relative;
								display: block;
								text-align: center;
								margin: 0;
								span {
									display: inline-block;
									max-width: 50px;
									width: 50px;
									height: 50px;
									line-height: 50px;
									margin-bottom: 5px;
									margin-right: 3px;
									overflow: hidden;
									background-color: #f8f8f8;
									color: #8e8e8e;
									font-size: 13px;
									text-align: center;
									text-transform: uppercase;
									vertical-align: top;
									&:last-child {
										margin-right: 0;
									}
									img {
										vertical-align: top;
									}
								}
							}
							// .btn-viewmore {
							// 	position: relative;
							// 	display: block;
							// 	width: 100%;
							// 	height: 50px;
							// 	line-height: 50px;
							// 	color: #fff;
							// 	background: #909CC2;
							// 	border: 1px solid #FFFFFF;
							// 	border-radius: 5px;
							// 	font-size: 18px;
							// 	text-align: center;
							// 	text-decoration: none;
							// }
						}
					}
					.item-inner:hover {
						.item-info {
							.btn-viewmore {
								background: #004989;
							}
						}
					}
				}
			}

			.btn-viewall {
				position: relative;
				display: block;
				width: 100px;
				height: 36px;
				line-height: 36px;
				color: #fff;
				background: #2A3A6B;
				border: 0;
				border-radius: 3px;
				font-size: 12px;
				text-align: center;
				text-decoration: none;
				margin: 0 auto;
				text-transform: uppercase;
			}
		}

	}



	.section-booth-list-col-3 {
		position: relative;
		display: block;
		padding: 40px 0;
		background: #EEF3FF;
		overflow: hidden;
		.booth-inner {
			display: block;
		}
		.booth-box-title {
			position: relative;
			display: block;
			width: 100%;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 20px;
				line-height: 1.4;
				margin-bottom: 15px;
				@media (min-width: 992px) {
					font-size: 30px
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.booth-box-list {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 100%;
			.booth-list {
				position: relative;
				display: block;
				display: flex;
				flex-wrap: wrap;
				overflow: hidden;
				margin: 0 -12.5px;
				.booth-item {
					position: relative;
					display: flex;
					width: 100%;
					max-width: 700px;
					padding: 12.5px;
					margin: 0;

					@media (min-width: 992px) {
						width: calc(100% / 2);
					}
					.item-inner {
						position: relative;
						display: block;
						width: 100%;
						// padding: 10px 15px 5px;
						padding: 10px 0 0;
						
						background: #FFFFFF;
						border-radius: 14px;
						box-shadow: 0 0 5px #EDF0F7;
						.header-item {
							position: relative;
							display: block;
							width: 100%;
							padding: 0 15px;
							margin-bottom: 10px;
							overflow: hidden;
						}
						.body-item {
							position: relative;
							display: block;
							width: 100%;
							padding: 0 15px;
						}

						.box-image {
							position: relative;
							display: block;
							width: 95px;
							height: 60px;
							float: left;
							.item-image-a {
								position: relative;
								display: block;
								padding: 5px;
								width: 100%;
								height: 100%;
								margin: 0 auto;
								background: #fff;
								border: 1px solid #EEEEEE;
								img {
									width: 100%;
									height: auto;
									max-height: 100%;
									object-fit: scale-down;
								}
							}
						}
						.box-info {
							position: relative;
							display: block;
							float: left;
							width: calc(100% - 100px);
							padding-left: 15px;
							.ratings {
								font-size: 13px;
								line-height: 1.5;
								color: #878787;
								.ratings-value {
									color: #109E4C;
									font-family: 'Averta-Bold';
								}
							}
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								padding: 0;
								margin: 0;
								margin-bottom: 5px;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									max-height: 48px;
									padding: 0;
									margin: 0;
									font-family: 'Averta-Bold';
									font-size: 16px;
									line-height: 22px;
									color: #242343;
									text-decoration: none;
									text-transform: uppercase;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									word-break: break-word;
									-webkit-line-clamp: 2;
									&:hover {
										color: #0BB451;
									}
								}
							}
							.item-description {
								display: block;
								margin-bottom: 0;
								font-family: 'Arial';
								font-size: 14px;
								color: #666666;
								white-space: nowrap;
								text-overflow: ellipsis;
								overflow: hidden;
								.total-count {
									color: #109E4C;
								}
								.tag-label {
									color: #242343;
									font-weight: bold;
									font-size: 12px;
								}
							}
						}

						.list-products {
							width: 100%;
							overflow: hidden;
							.title-slide {
								color: #302B6B;
								font-size: 14px;
								margin-bottom: 10px;
								font-weight: bold;
							}
							.list-type-pro {
								display: flex;
								flex-wrap: wrap;
								@include gap_2(2,10px);
								@media (min-width: 576px) {
									@include gap_2(4,15px);
								}
								@media (min-width: 992px) {
									@include gap_2(4,8px);
								}
								@media (min-width: 1200px) {
									@include gap_2(4,15px);
								}
								.item-product {
									display: block;
									max-width: 250px;
									margin-bottom: 5px;
									overflow: hidden;
									color: #8e8e8e;
									font-size: 12px;
									vertical-align: top;
									img {
										vertical-align: top;
									}
									.product-image {
										position: relative;
										display: block;
										margin-bottom: 10px;
										.percent-discount {
											position: absolute;
											top: 0;
											left: 0;
											display: block;
											width: auto;
											min-width: 40px;
											font-size: 12px;
											padding: 0 4px;
											height: 20px;
											line-height: 20px;
											background: #FF4343;
											border-radius: 3px;
											color: #fff;
											text-align: center;
										}
									}
									.product-name {
										display: -webkit-box;
										max-height: 44px;
										height: 44px;
										margin-bottom: 8px;
										font-size: 12px;
										line-height: 1.3;
										color: #242343;
										overflow: hidden;
										-webkit-box-orient: vertical;
										text-overflow: ellipsis;
										-webkit-line-clamp: 3;
										text-decoration: none;
										a {
											color: inherit;
										}
									}
									.box-price {
										display: inline-block;
										width: 100%;
										overflow: hidden;
										p {
											display: flex;
											flex-wrap: wrap;
											height: 40px;
											font-size: 14px;
											line-height: 14px;
											margin: 0;
										}
										.price-current,
										.price-sale {
											width: 100%;
											float: left;
											margin-bottom: 3px;
											font-weight: bold;
											font-size: 14px;
											line-height: 18px;
											color: #0BB451;
											white-space: nowrap;
											overflow: hidden;
											text-overflow: ellipsis;
										}
										.price-old {
											font-size: 12px;
											line-height: 16px;
											color: #ABABAB;
											text-decoration: line-through;
											white-space: nowrap;
											overflow: hidden;
											text-overflow: ellipsis;
										}
										.percent-discount {
											position: relative;
											display: block;
											width: auto;
											float: left;
											font-size: 12px;
											color: #d52329;
											margin-left: 10px;
											padding: 0 4px;
											height: 20px;
											line-height: 20px;
											background: #FF4343;
											border-radius: 3px;
											color: #fff;
											text-align: center;
										}
									}
								}
							}

						}


						.group-act {
							position: relative;
							display: flex;
							justify-content: center;
							align-items: center;
							width: 100%;
							height: 40px;
							line-height: 40px;
							background: #e0e9fe;
							border-radius: 0px 0px 14px 14px;
							overflow: hidden;
							.btn-item {
								width: 180px;
								display: flex;
								align-items: center;
								font-family: 'Averta-Bold';
								font-weight: bold;
								font-size: 12px;
								color: #302B6B;
								padding: 0 7.5px;
								border-radius: 0px 0px 14px 14px;
								overflow: hidden;
								&.btn-item-register {
