			.btn-viewall {
				position: relative;
				display: block;
				width: 100px;
				height: 36px;
				line-height: 36px;
				color: #fff;
				background-color: #2A3A6B;
				border: 0;
				border-radius: 3px;
				font-family: 'Averta-Semibold';
				font-size: 14px;
				text-align: center;
				text-decoration: none;
				margin: 30px auto 15px;
			}

		}

