	.section-booth-list-col-4 {
		position: relative;
		display: block;
		padding: 40px 0;
		background: #F7F7F7;
		.booth-inner {
			display: block;
		}
		.booth-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			color: #333333;
			text-align: center;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 24px;
				line-height: 31px;
				margin-bottom: 30px;
				text-transform: uppercase;
				@media (max-width: 370px) {
					font-size: 20px;
				}
				&:after {
					content: "";
					position: absolute;
					display: block;
					width: 100%;
					height: 5px;
					background: #0BB451;
					bottom: -10px;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.booth-box-list {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 100%;
			.booth-list {
				position: relative;
				display: block;
				display: flex;
				flex-wrap: wrap;
				overflow: hidden;
				margin: 0 -12.5px;
				.booth-item {
					position: relative;
					display: block;
					width: calc(100% / 2);
					max-width: 700px;
					padding: 12.5px;
					margin: 0;

					@media (max-width: 991px) {
						width: 100%;
					}
					.item-inner {
						position: relative;
						display: block;
						padding: 10px 15px 5px;
						background: #FFFFFF;
						border: 1px solid #E8E8E8;
						.header-item {
							position: relative;
							display: block;
							width: 100%;
							margin-bottom: 10px;
							overflow: hidden;
						}
						.body-item {
							position: relative;
							display: block;
							width: 100%;
						}

						.box-image {
							position: relative;
							display: block;
							width: 100px;
							height: 60px;
							float: left;
							.item-image-a {
								position: relative;
								display: block;
								padding: 5px;
								width: 100%;
								height: 100%;
								margin: 0 auto;
								background: #fff;
								border: 1px solid #EEEEEE;
								img {
									width: 100%;
									height: auto;
									max-height: 100%;
									object-fit: scale-down;
								}
							}
						}
						.box-info {
							position: relative;
							display: block;
							float: left;
							width: calc(100% - 100px);
							padding-left: 15px;
							.ratings {
								font-size: 13px;
								line-height: 1.5;
								color: #878787;
								.ratings-value {
									color: #109E4C;
									font-family: 'Averta-Bold';
								}
							}
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								padding: 0;
								margin: 0;
								margin-bottom: 5px;
								.item-title-a {
									position: relative;
									display: block;
									height: 25px;
									padding: 0;
									margin: 0;
									font-family: 'Averta-Bold';
									font-size: 18px;
									line-height: 25px;
									color: #242343;
									text-decoration: none;
									text-transform: uppercase;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									white-space: nowrap;
									word-break: break-word;
									&:hover {
										color: #0BB451;
									}
								}
							}
							.item-description {
								display: block;
								font-family: 'Arial';
								font-size: 14px;
								color: #666666;
								white-space: nowrap;
								text-overflow: ellipsis;
								overflow: hidden;
								.total-count {
									color: #109E4C;
								}
								.tag-label {
									color: #242343;
									font-weight: bold;
									font-size: 14px;
								}
							}
						}

						.list-products {
							width: 100%;
							overflow: hidden;
							.list-type-pro {
								display: flex;
								flex-wrap: wrap;
								justify-content: space-between;
								margin: 0 -7.5px;
								.item-product {
									display: inline-block;
									flex: auto;
									max-width: 250px;
									width: calc(100% / 4);
									padding: 0 7.5px;
									margin-bottom: 5px;
									overflow: hidden;
									color: #8e8e8e;
									font-size: 12px;
									vertical-align: top;
									&:last-child {
										margin-right: 0;
									}

									@media (max-width: 575px) {
										width: calc(100% / 2);
									}

									img {
										vertical-align: top;
									}
									.product-image {
										position: relative;
										display: block;
										margin-bottom: 10px;
										.percent-discount {
											position: absolute;
											top: 0;
											left: 0;
											display: block;
											width: auto;
											min-width: 40px;
											font-size: 12px;
											padding: 0 4px;
											height: 20px;
											line-height: 20px;
											background: #FF4343;
											border-radius: 3px;
											color: #fff;
											text-align: center;
										}
									}
									.product-name {
										margin-bottom: 8px;
										font-size: 12px;
										color: #242343;
										a {
											color: inherit;
										}
									}
									.box-price {
										display: inline-block;
										width: 100%;
										overflow: hidden;
										p {
											display: flex;
											flex-wrap: wrap;
											height: 40px;
											font-size: 14px;
											line-height: 14px;
											margin: 0;
										}
										.price-current,
										.price-sale {
											width: 100%;
