		}

		&.section-news-home {
			@media (max-width: 575px) {
				.news-box-list .news-list {
					.news-item:not(:nth-child(1)) {
						padding: 10px 10px 10px 0;
						margin: 10px 0;
						background: #fff;
						border: 0;
					}
				}
			}
		}
		.news-inner {
			display: block;
		}
		.news-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 30px;
				line-height: 1.3;
				margin-bottom: 10px;
				@media (max-width: 992px) {
					font-size: 20px;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.news-box-list {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 100%;
			.news-list {
				position: relative;
				display: block;
				overflow: hidden;
				margin: 0 -20px;
				.news-item {
					position: relative;
					display: block;
					width: 25%;
					max-width: 768px;
					float: left;
					padding: 15px 20px;
					margin: 0;
					&:nth-child(1) {
						width: 50%;
						.item-inner {
							.item-info {
								.item-title {
									.item-title-a {
										height: 62px;
										font-family: 'Averta-Semibold';
										font-size: 24px;
										line-height: 1.3;
										-webkit-line-clamp: 2;
										color: $colormain;
									}
								}
								.item-description {
									display: block;
									display: -webkit-box;
									height: 109px;
									-webkit-line-clamp: 5;
								}
							}
						}
					}
					.item-inner {
						position: relative;
						display: block;
						padding: 0;
						// background: #FFFFFF;
						// box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
						.item-image {
							position: relative;
							display: block;
							overflow: hidden;
							margin-bottom: 15px;
							.item-image-a {
								position: relative;
								display: block;
								margin: 0 auto;
								border-radius: 14px;
								overflow: hidden;
							}
						}
						.item-info {
							position: relative;
							display: block;
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								margin: 0 0 5px;
								padding: 0;
								font-family: 'Averta';
								font-weight: normal;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									height: 68px;
									padding: 0;
									margin: 0;
									font-size: 16px;
									line-height: 1.4;
									color: #657195;
									text-decoration: none;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									-webkit-line-clamp: 3;
									word-break: break-word;
								}
							}
							.item-description {
								display: none;
								height: 66px;
								margin: 0;
								color: #657195;
								font-size: 16px;
								padding: 0;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 3;
								word-break: break-word;
							}
						}
					}
					.item-inner:hover {
					}
				}
			}

			.btn-viewmore {
				position: relative;
				display: block;
				width: 138px;
				height: 42px;
				padding: 0 13px;
				margin: 20px auto;
				color: #fff;
				background-color: #2A3A6B;
				border: 1px solid #FFFFFF;
				border-radius: 8px;
				background: transparent;
				font-family: 'Averta-Semibold';
				font-size: 14px;
				line-height: 42px;
				text-align: center;
				text-decoration: none;
			}
		}
		@media (max-width: 991px) {
			.news-box-list {
				.news-list {
					margin: 0 -10px;
					.news-item {
						width: percentage(1/3);
						padding: 15px 10px;
						&:nth-child(1),
						&:nth-child(2) {
							width: 50%;
							.item-inner {
								.item-info {
									.item-title {
										.item-title-a {
											height: auto;
											font-size: 18px;
											line-height: 1.4;
											-webkit-line-clamp: unset;
										}
									}
									.item-description {
										display: block;
										display: -webkit-box;
										height: inherit;
										-webkit-line-clamp: 3;
									}
								}
							}
						}
						&:nth-child(3) {
							clear: both;
						}
						.item-inner {
							.item-info {
								.item-title {
									.item-title-a {
										height: 46px;
										font-size: 16px;
										-webkit-line-clamp: 2;
									}
								}
								.item-description {
									font-size: 16px;
								}
							}
						}
					}
				}
				.btn-viewmore {
					width: 145px;
					height: 36px;
					line-height: 36px;
					font-size: 12px;
				}
			}
		}
		@media (max-width: 767px) {
			.news-box-title .title-name {
				font-size: 24px;
			}
			.news-box-list {
