					margin: 0 -5px;
					.news-item {
						width: 50%;
						padding: 7px 5px;
						&:nth-child(1) {
							width: 100%;
						}
						&:nth-child(2) {
							.item-inner .item-info .item-title .item-title-a {
								height: 46px;
								font-size: 16px;
							}
							.item-inner .item-info .item-description {
								display: none;
							}
						}
						&:nth-child(3) {
							clear: unset;
						}
						.item-inner {
							.item-info {
								.item-description {
									height: 46px;
									font-size: 16px;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									-webkit-line-clamp: 2;
									word-break: break-word;
								}
							}
						}
					}
				}
			}
		}
