	.section-news-list {
		position: relative;
		display: block;
		.news-inner {
			display: block;
			padding-top: 20px;
			border-top: 1px solid #EEEEEE;
			display: flex;
			flex-wrap: wrap;
		}
		.news-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			color: #333;
			text-align: center;
			.title-name {
				position: relative;
				display: block;
				font-family: 'Averta-Bold';
				font-size: 28px;
				line-height: 29px;
				margin-bottom: 20px;
				padding: 0 0 25px;
				text-transform: uppercase;
				&:before {
					content: "";
					position: absolute;
					width: 175px;
					height: 15px;
					background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='176' height='15' viewBox='0 0 176 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cg clip-path='url(%23clip0)'%3e%3crect x='0.928925' y='6.57104' width='70' height='1' fill='%232575FC'/%3e%3crect x='88' width='10' height='10' transform='rotate(45 88 0)' fill='%232575FC'/%3e%3crect x='105.071' y='6.57104' width='70' height='1' fill='%232575FC'/%3e%3c/g%3e%3cdefs%3e%3cclipPath id='clip0'%3e%3crect x='0.928925' width='174.142' height='14.1421' fill='white'/%3e%3c/clipPath%3e%3c/defs%3e%3c/svg%3e ");
					bottom: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					pointer-events: none;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.news-box-list {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: calc(100% - 330px);
			margin-right: 30px;
			margin-bottom: 30px;
			.news-list {
				position: relative;
				display: block;
				overflow: hidden;
				.news-item {
					position: relative;
					display: block;
					clear: both;
					width: 100%;
					padding: 15px 0;
					margin: 0;
					border-bottom: 1px solid #EEEEEE;
					overflow: hidden;
					&:nth-child(1) {
						padding-top: 0;
					}
					.item-inner {
						position: relative;
						display: block;
						display: -webkit-box;
						display: -webkit-flex;
						display: -ms-flexbox;
						display: flex;
						-webkit-box-align: start;
						-webkit-align-items: start;
						-ms-flex-align: start;
						align-items: start;
						padding: 0;
						// background: #FFFFFF;
						// box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
						.item-image {
							position: relative;
							display: block;
							float: left;
							overflow: hidden;
							width: percentage(4/9);
							max-width: 245px;							
							-webkit-box-flex: 0;
							-webkit-flex: none;
							-ms-flex: none;
							flex: none;
							.item-image-a {
								position: relative;
								display: block;
								margin: 0 auto;
							}
						}
						.item-info {
							position: relative;
							display: block;
							float: left;
							-webkit-box-flex: 1;
							-webkit-flex: auto;
							-ms-flex: auto;
							flex: auto;
							background: #fff;
							width: percentage(5/9);
							padding: 0 0 0 15px;
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								margin: 0 0 5px;
								padding: 0;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									height: auto;
									padding: 0;
									margin: 0;
									font-family: 'Averta-Semibold';
									font-size: 18px;
									line-height: 1.4;
									color: #333333;
									text-decoration: none;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									-webkit-line-clamp: 2;
									word-break: break-word;
								}
							}
							.item-description {
								display: block;
								height: 66px;
								margin: 0;
								color: #333333;
								font-size: 16px;
								padding: 0;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 3;
								word-break: break-word;
							}
						}
					}
					.item-inner:hover {
					}
				}
			}

			.btn-viewmore {
				position: relative;
				display: block;
				width: 200px;
				height: 50px;
				line-height: 50px;
				margin: 20px auto;
				color: #ffffff;
				background: transparent;
				border: 1px solid #ffffff;
				border-radius: 5px;
				font-size: 18px;
				text-align: center;
				text-decoration: none;
			}
		}
		.col-right {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 300px;
			margin-bottom: 30px;
			.img-ads {
				text-align: center;
				position: sticky;
				top: 0px;
				margin-bottom: 20px;
				&.box-sticky {
					position: sticky;
					top: 80px;
					margin-bottom: 20px;
				}
				img {
					width: auto;
					margin: 0 auto;
					max-width: 100%;
				}
			}
		}

		@media (max-width: 991px) {
			.news-box-list {
				float: none;
				width: 100%;
				margin-right: 0;
			}
			.col-right {
				float: none;
				width: 100%;
			}
		}
		@media (max-width: 767px) {
			.news-box-list {
				.news-list {
					.news-item {
						.item-inner {
							.item-info {
								.item-description {
									height: 46px;
									font-size: 16px;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									-webkit-line-clamp: 2;
									word-break: break-word;
								}
							}
						}
					}
				}
			}
		}
		@media (max-width: 575px) {
			.news-box-list .news-list .news-item .item-inner .item-info .item-title .item-title-a {
				font-size: 16px;
			}
		}
		@media (max-width: 479px) {
			.news-box-list .news-list .news-item .item-inner .item-info {
				padding: 0 0 0 12px;
				.item-description {
					display: none;
				}
				.item-title {
					.item-title-a {
						height: auto;
						-webkit-line-clamp: 4;
					}
				}
			}
		}
		@media (max-width: 365px) {
			.news-box-list {
				.news-list {
					.news-item {
						.item-inner {
							.item-image {
								width: percentage(2/5);
								height: 85px;
								.item-image-a {
									height: 100%;
								}
							}
							.item-info {
								width: percentage(3/5);
								padding: 0 0 0 10px;
								.item-title {
									.item-title-a {
										font-size: 15px;
									}
								}
							}
						}
					}
				}
			}
		}
	}
