.agriTechExpo {
    position: relative;
    display: block;
    width: 100%;
    min-width: 314px;
    min-height: 1px;
    margin: 0;
    padding: 0;
    // overflow-x: hidden;
    &.menu-open {
        height: 100vh;
        overflow: hidden;
    }
}

.list {
    list-style: none;
    margin: 0px;
    padding: 0px;
}

::-moz-selection {
    color: #fff;
    background: #2e7d32;
}
::selection {
    color: #fff;
    background: #2e7d32;
}

:after,
:before {
    box-sizing: border-box;
}

.clearfix {
    clear: both;
}

.clearfix::before {
    content: " ";
    display: table;
}

.clearfix::after {
    content: " ";
    display: block;
    clear: both;
}
