/* Hover Animate Area css
============================================================================================ */
.mirror-effect {
    position: relative;
    display: block;
    overflow: hidden;
}

.mirror-effect::before {
    content: '';
    position: absolute;
    top: 0;
    left: -84%;
    z-index: 2;
    display: block;
    width: 50%;
    height: 100%;
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
    transform: skewX(-25deg);
    pointer-events: none;
}

.mirror-effect:hover::before {
    -webkit-animation: shine 1s;
    animation: shine 1s;
}

@-ms-keyframes shine {
    100% {
        left: 125%;
    }
}

@-moz-keyframes shine {
    100% {
        left: 125%;
    }
}

@-webkit-keyframes shine {
    100% {
        left: 125%;
    }
}

.aux-buy-button {
    transition-delay: 30ms;
    animation-delay: 2.05s;
}

.aux-sticky-button {
    transition: all .4s cubic-bezier(.46,.03,.52,.96);
    background-color: rgba(0,0,0,.45);
    background-image: linear-gradient(90deg ,rgba(255,255,255,0) 40%,rgba(255,255,255,.4) 50%,rgba(255,255,255,0) 60%);
    background-size: 300%;
    animation-duration: 5s;
    animation-delay: 2s;
    animation-name: AuxLightUpStickyBtns;
    animation-iteration-count: infinite;
    will-change: backgroud-position;
}

@keyframes AuxLightUpStickyBtns {
    0%{background-position:0 0}
    80%{background-position:0 0}
    100%{background-position:100% 0}
}

/* End Hover Animate Area css
============================================================================================ */

.load {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background: rgba(0, 0, 0, 0.7);
    z-index: 9999999999;
    display: none;

    .loading {
        position: absolute;
        z-index: 1;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        width: 70px;
        height: 70px;
        margin: auto;
        border: 12px solid #f3f3f3;
        border-radius: 50%;
        border-top: 12px solid #d52329;
        animation: spin 2s linear infinite;
    }

    img {
        position: absolute;
        width: 100px;
        top: 50%;
        left: 50%;
        animation-name: load;
        animation-timing-function: linear;
        animation-duration: 1s;
        animation-iteration-count: infinite;
    }
}

