@mixin hexagon($size, $color, $border: 0) {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: $size; 
    height: ($size * 0.577);
    background-color: $color;
    margin: ($size * 0.288) 0;
    border-left: $border;
    border-right: $border;

    &:before,
    &:after {
        content: "";
        position: absolute;
        @if $border == 0 {
              width: 0;
              left: 0;
              border-left: ($size/2) solid transparent;
              border-right: ($size/2) solid transparent;
        } @else {
              z-index: 1;
              width: ($size * 0.707);
              height: ($size * 0.707);
              -webkit-transform: scaleY(0.5774) rotate(-45deg);
              -ms-transform: scaleY(0.5774) rotate(-45deg);
              transform: scaleY(0.5774) rotate(-45deg);
              background-color: inherit;
              left: ($size * 0.129);
        }
    }

    &:before {
        @if $border == 0 {
            bottom: 100%;
            border-bottom: ($size * 0.288) solid $color;
        } @else {
            top: -($size * 0.353);
            border-top: $border;
            border-right: $border;
        }
    }

    &:after {
        @if $border == 0 {
            top: 100%;
            width: 0;
            border-top: ($size * 0.288) solid $color;
        } @else {
            bottom: -($size * 0.353);
            border-bottom: $border;
            border-left: $border;
        }
    }
}
.hexagon {
    $hexagon_width:         78px;
    $hexagon_edge_side:     #{78px/2};
    $hexagon_bg: rgba(#000000,0.6);

    @include hexagon($hexagon_width,$hexagon_bg,$border: 0);

    .icon-virtual-360 {
        display: block;
        width: 42px;
        height: 54px;
        background: url("../images/icon-virtual-360.svg");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-position: center;
        z-index: 2;
    }
}
