
.agriTechExpo {
	.product-detail {

		.box-info-supplier {
			position: relative;
			display: block;
			display: flex;
			width: 100%;
			padding: 3px 0;
			margin: 0;
			overflow: hidden;
			.info-address,
			.info-create-time,
			.info-sold {
				position: relative;
				display: block;
				width: auto;
				float: left;
				text-align: center;
				.info-label {
					color: #666666;
					font-size: 12px;
				}
				.info-value {
					color: inherit;
					font-size: 13px;
				}
			}
			.info-address {
				padding: 0 5px 0 0;
			}
			.info-create-time {
				padding: 0 8px;
				border-left: 1px solid #ccc;
				border-right: 1px solid #ccc;
			}
			.info-sold {
				padding: 0 0 0 8px;
			}
		}
		.tool-link-action {
			position: fixed;
			display: block;
			left: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			width: auto;
			height: 150px;
			text-align: center;
			z-index: 999;
			.btn-collapse {
				position: absolute;
				top: 0;
				left: 100%;
				display: block;
				width: 10px;
				height: 10px;
				line-height: 10px;
				text-align: center;
				//background: #fff;
				color: #242424;
				cursor: pointer;
				&:before {
					content: "\f00d";
				}
				&:hover {
					background: #000;
					color: #fff;
					&~.nav {
						display: block;
					}
				}
			}
			.nav {
				width: 54px;
				padding: 0;
				margin: 0 0 0 10px;
				background-color: #fff;
				box-shadow: -3px 3.5px 5px 0 rgba(0,0,0,.2);
				border-radius: 18px;
				overflow: hidden;
			}
			.item-link {
				position: relative;
				display: block;
				width: 100%;
				height: 50px;
				clear: both;
				color: #fff;
				&:not(:last-child) {
					border-bottom: 1px solid rgba(0,0,0,.1);
				}
				&:last-child {
					a {
						padding: 8px 2px 5px;
					}
				}
				&:hover {
					background: #d42027;
					a {
						color: #fff;
					}
				}
				&.active {
					background: #d42027;
					a {
						color: #fff;
					}
				}
				a {
					padding: 10px 2px 5px;
					color: #242424;
					background: none;
					font-size: .7em;
				}
				.fal {
					position: relative;
					display: block;
					width: 100%;
					margin-bottom: 1px;
					font-size: 18px;
				}
			}
		}
		.wrap-detail {
			position: relative;
			display: block;
			// display: -ms-flexbox!important;
			// display: flex;
			// flex-wrap: wrap;
			margin-bottom: 15px;
			.col-primary-detail {
				position: relative;
				display: block;
				float: left;
				width: 100%;
			}
			.col-right-detail {
				position: relative;
				display: block;
				float: left;
				width: calc(25% - 15px);
				margin-left: 15px;
				.box-guarantee {
					.body-guarantee {
						span {
							position: relative;
							display: inline-block;
							width: 20px;
							font-size: 16px;
						}
					}
					p {
						margin-bottom: 5px;
					}
				}
				.box-support {
					.body-support {
						span {
							position: relative;
							display: inline-block;
							width: 20px;
							font-size: 16px;
						}
					}
					p {
						margin-bottom: 5px;
					}
				}
				.wrap-products-related {
					position: relative;
					display: block;
					float: right;
					width: 100%;
					max-width: 350px;
					padding-right: 0;
					padding: 0 15px;
					background: #fff;
					.title-module {
						position: relative;
						display: block;
						height: 40px;
						line-height: 40px;
						text-transform: uppercase;
						font-family: 'Averta-Bold';
						font-size: 14px;
						border-bottom: 1px solid #f5f5f5;
						clear: both;
					}
					.box-title {
						.swiper-button-next-products-related, .swiper-button-prev-products-related {
							position: absolute;
							top: 0;
							width: 25px;
							height: 39px;
							line-height: 39px;
							z-index: 6;
							cursor: pointer;
							outline: none;
							background: #fff;
							opacity: .7;
							&:after {
								content: "";
								position: absolute;
								display: block;
								top: 0;
								right: 0;
								width: 25px;
								height: 39px;
								line-height: 39px;
								text-align: center;
								vertical-align: middle;
								z-index: 2;
								font-family: 'Font Awesome 5 Pro';
								font-size: 24px;
								font-weight: 300;
								-moz-osx-font-smoothing: grayscale;
								-webkit-font-smoothing: antialiased;
								display: inline-block;
								font-style: normal;
								font-variant: normal;
								text-rendering: auto;
							}
							&:hover {
								&:after {
									background: #eee;
								}
							}
						}
						.swiper-button-prev-products-related {
							top: 0;
							right: 25px;
							background: #fff;
							&:after {
								content: "\f104";
							}
						}
						.swiper-button-next-products-related {
							top: 0;
							right: 0;
							background: #fff;
							&:after {
								content: "\f105";
							}
						}
					}
				}
			}
		}
		.wrap-first-view-product {
			position: relative;
			display: block;
			width: 100%;
			padding: 20px 0;
			margin-bottom: 15px;
			// overflow: hidden;
			@media (max-width: 767px) {
				padding-top: 0;
			}
			.preview {
				position: relative;
				display: block;
				float: left;
				width: 412px;
				vertical-align: top;
			}
			.details {
				position: relative;
				display: block;
				float: left;
				width: calc(100% - 412px);
				vertical-align: top;
				padding-left: 38px;
			}
			@media (max-width: 991px) {
				.preview {
					float: none;
					clear: both;
					width: 412px;
					margin: 0 auto 10px;
				}
				.details {
					float: none;
					width: 100%;
					padding: 0;
					clear: both;
				}
			}
			@media (max-width: 480px) {
				.preview {
					width: 100%;
					.single-gallery {
						max-width: 413px;
					}
					.single-pro-img .slick-slide { 
					}
				}
			}
		}
		.wrap-first-view-product {
			.product-title {
				padding-bottom: 5px;
				margin: 0;
				font-size: 20px;
				line-height: 1.4;
				word-break: break-word;
				font-family: 'Averta-Semibold';
			}
			.box-price {
				position: relative;
				display: block;
				display: flex;
				align-items: center;
				width: 100%;
				margin-bottom: 10px;
				.price-sale,
				.price-current {
					position: relative;
					display: block;
					float: left;
					font-family: 'Averta-Bold';
					font-size: 26px;
					line-height: 30px;
					color: #109e4c;
				}
				.price-old {
					position: relative;
					display: block;
					float: left;
					text-decoration: line-through;
					height: 30px;
					line-height: 30px;
					margin-left: 15px;
					color: #ABABAB;
					font-size: 16px;
				}
				.percent-discount,
				.sale {
					position: relative;
					display: block;
					width: auto;
					min-width: 40px;
					float: left;
					font-size: 14px;
					margin-left: 15px;
					padding: 0 5px;
					height: 23px;
					line-height: 23px;
					background: #FF4343;
					border-radius: 3px;
					color: #fff;
					text-align: center;
				}
				@media (max-width: 575px) {
					.price-sale,
					.price-current {
						font-size: 21px;
					}
					.price-old {
						font-size: 14px;
					}
					.percent-discount,
					.sale {
						font-size: 12px;
					}
				}
			}
			.flash-info-detail {
				position: relative;
				display: block;
				width: 100%;
				margin-top: 5px;
				margin-bottom: 15px;
				background-color: #feeeee;
				border-radius: 5px;
				overflow: hidden;
				line-height: 30px;
				.col-label {
					position: relative;
					display: block;
					float: left;
					padding: 0 15px 0 10px;
					background: #ce2228;
					color: #fff;
					font-size: 15px;
					text-transform: uppercase;
					font-style: italic;
					&:before {
						content: "";
						position: absolute;
						display: block;
						top: 0;
						bottom: 0;
						right: -14px;
						width: 0;
						height: 0;
						margin: auto 0;
						z-index: 1;
						border-top: 30px solid #ce2228;
						border-right: 14px solid transparent;
						background: transparent;
					}
					&:after {
						content: "";
						position: absolute;
						display: block;
						top: 0;
						bottom: 0;
						right: -23px;
						width: 30px;
						height: 30px;
						margin: auto 0;
						z-index: 2;
						background-image: url("data:img/png;base64,");
						background-repeat: no-repeat;
						background-position: center center;
					}
				}
				.col-content {
					position: relative;
					display: block;
					float: left;
					padding: 0 10px 0 30px;
					font-size: 13px;
					color: #666666;
					&:before {
						content: "\f017";
						font-family: 'Font Awesome 5 Pro';
						font-weight: 400;
						-moz-osx-font-smoothing: grayscale;
						-webkit-font-smoothing: antialiased;
						display: inline-block;
						font-style: normal;
						font-variant: normal;
						text-rendering: auto;
						line-height: 1;
					}
					.flash-txt {
						position: relative;
						display: inline-block;
					}
					.flash-value {
						position: relative;
						display: inline-block;
						font-family: 'Averta-Bold';;
						color: #242424;
						padding-left: 5px;
						.attr-time {
							position: relative;
							display: inline-block;
							&:last-child {
								margin-right: 0;
							}
							.time-value {
								position: relative;
								display: inline-block;
							}
							.time-text {
								position: relative;
								display: inline-block;
							}
						}

					}
				}
			}
			.wrap-code-detail {
				position: relative;
				display: inline-block;
				width: 100%;
				margin-bottom: 5px;
				vertical-align: top;
				.col-label {
					position: relative;
					display: block;
					float: left;
					width: 90px;
					font-size: 14px;
					font-family: 'Averta-Bold';
				}
				.col-content {
					position: relative;
					display: block;
					float: left;
					width: calc(100% - 91px);
					padding: 0 0 0 5px;
					font-size: 13px;
					color: #666666;
					.flash-txt {
						position: relative;
						display: inline-block;
					}
					.item-code {
						position: relative;
						display: inline-block;
						min-width: 80px;
						padding: 0 10px;
						margin-right: 8px;
						margin-bottom: 8px;
						line-height: 24px;
						background-image: url("data:img/png;base64,");
						background-repeat: no-repeat;
						background-size: 100% 100%;
						color: #ce2228;
						white-space: nowrap;
						cursor: default;
						&:hover {
							background-color: #fddede;
							.onhover-item-promotion {
								display: block;
							}
						}
					}
					.onhover-item-promotion {
						display: none;
						position: absolute;
						top: 22px;
						right: -38px;
						width: 280px;
						z-index: 3;
						padding: 10px 0 0;
						.item-inner {
							width: 100%;
							background: #fff;
							border-radius: 4px;
							color: #242424;
							font-size: 13px;
							z-index: 3;
							padding: 10px 12px;
							box-shadow: 1px 1px 3px 0px #e0e0e0, -1px -1px 3px 0px #e0e0e0;
							&:before {
								content: "";
								position: absolute;
								display: block;
								width: 12px;
								height: 12px;
								top: 5px;
								right: 70px;
								box-shadow: 2px 2px 2px #e0e0e0;
								transform: rotate(-135deg);
								z-index: 2;
								background: #fff;
							}
							.promotion-info { 
								width: 100%;
								white-space: normal;
								.info-code {
									clear: both;
								}
								.info-description { 
									clear: both;
								}
								.info-time { 
									clear: both;
								}
							}
						}
					}
				}
			}
			.wrap-shipment-details {
				position: relative;
				display: inline-block;
				width: 100%;
				margin-bottom: 5px;
				.col-label {
					position: relative;
					display: block;
					float: left;
					width: 90px;
					font-size: 14px;
					line-height: 21px;
					font-family: 'Averta-Bold';
				}
				.col-content {
					position: relative;
					display: block;
					float: left;
					width: calc(100% - 91px);
					padding: 0 0 0 5px;
					.support-shipping {
						position: relative;
						display: inline-block;
						width: 100%;
						clear: both;
						line-height: 21px;
						padding-left: 30px;
						&:after {
							content: "\f0d1";
							position: absolute;
							display: inline-block;
							width: 25px;
							height: 25px;
							top: 2px;
							left: 0;
							bottom: 0;
							margin: 0 auto;
							font-family: 'Font Awesome 5 Pro';
							font-weight: 700;
							-moz-osx-font-smoothing: grayscale;
							-webkit-font-smoothing: antialiased;
							font-size: 15px;
							font-style: normal;
							font-variant: normal;
							text-rendering: auto;
							line-height: 1;
							color: #d7353a;
							text-align: center;
							// background: url("../images/FreeShip.png");
							// background-repeat: no-repeat;
							// background-position: center center;
							// background-size: initial;
						}
						p:nth-child(1) {
							font-size: 14px;
							color: #242424;
						}
						p:nth-child(2) {
							font-size: 12px;
							color: #666666;
						}
					}
					.address {
						position: relative;
						display: inline-block;
						line-height: 18px;
						padding-left: 30px;
						&:after {
							content: "\f3c5";
							position: absolute;
							display: block;
							top: 2px;
							left: 0;
							width: 25px;
							height: 25px;
							z-index: 2;
							border-radius: 3px;
							font-family: 'Font Awesome 5 Pro';
							font-weight: 700;
							color: #006738;
							font-size: 18px;
							overflow: hidden;
							text-align: center;
						}
					}
					.choose-other {
						position: relative;
						display: inline-block;
						font-style: italic;
						text-decoration: underline;
						color: #ce2228;
						cursor: pointer;
					}
					.shipping-fee {
						position: relative;
						display: block;
						padding-right: 18px;
						margin-top: 5px;
						line-height: 21px;
						overflow: hidden;
						letter-spacing: -.5px;
						&:hover ~ .onhover-shipment {
							display: block;
						}
						&:after {
							content: "\f107";
							position: absolute;
							right: 0;
							top: 0;
							color: #242424;
							vertical-align: middle;
							z-index: 2;
							font-family: 'Font Awesome 5 Pro';
							font-size: 24px;
							font-weight: 300;
							-moz-osx-font-smoothing: grayscale;
							-webkit-font-smoothing: antialiased;
							display: inline-block;
							font-style: normal;
							font-variant: normal;
							text-rendering: auto;
						}
					}
					.onhover-shipment {
						position: relative;
						display: none;
						width: 100%;
						.box-inner {
							position: absolute;
							display: block;
							top: 5px;
							left: 0;
							width: 100%;
							background: #fff;
							border-radius: 8px;
							color: #242424;
							font-size: 13px;
							z-index: 3;
							padding: 5px 12px;
							box-shadow: 1px 1px 3px 0px #adadad, -1px -1px 3px 0px #adadad;
							&:before {
								content: "";
								position: absolute;
								display: block;
								width: 12px;
								height: 12px;
								top: -5px;
								left: 10%;
								box-shadow: 2px 2px 2px #ababab;
								transform: rotate(-135deg);
								z-index: 2;
								background: #fff;
							}
						}
						.item-shipment {
							position: relative;
							display: block;
							width: 100%;
							padding-top: 5px;
							border-bottom: 1px solid #ebebeb;
							&:last-child {
								border-bottom: 0;
							}
							.logo {
								position: relative;
								display: block;
								width: 50%;
								float: left;
								padding-right: 5px;
							}
							.price {
								position: relative;
								display: block;
								width: 50%;
								float: left;
								padding-left: 5px;
								text-align: right;
								font-size: 14px;
							}
							.box-description {
								position: relative;
								display: block;
								width: 100%;
								clear: both;
								padding-top: 4px;
								p {
									line-height: 1.3;
									margin-bottom: 5px;
								}
							}
						}
					}
				}
			}
			.box-intro {
				position: relative;
				display: block;
				li {
					line-height: 1.4;
					margin-bottom: 8px;
				}
			}
			.box-color {
				position: relative;
				display: block;
				.colors {
					flex-grow: 1;
					display: flex;
					line-height: 30px;
					margin-top: 5px;
					margin-bottom: 5px;
					font-family: 'Arial';
					font-size: inherit;
					.name-color {
						padding-left: 10px;
						color: #333333;
						font-weight: normal;
						&.error {
							color: red;
							font-style: italic;
						}
					}
				}
				.similar-products {
					position: relative;
					display: block;
					width: 100%;
					clear: both;
					.item-pro {
						position: relative;
						display: block;
						width: 60px;
						height: 60px;
						line-height: 60px;
						float: left;
						margin-right: 10px;
						margin-bottom: 10px;
						background: #f4f4f4;
						text-align: center;
						cursor: pointer;
						&:last-child {
							margin-right: 0;
						}
						&.active {
							border: 0;
							outline: 3px solid #A5CD39;
							// img {
							// 	outline: 3px solid #A5CD39;
							// 	outline-offset: -3px;
							// 	border: 0;
							// }
						}
						img {
							position: relative;
							display: block;
							vertical-align: middle;
							width: 100%;
							height: 100%;
							object-fit: scale-down;
						}
					}
					// Update new HuyTQ -- show txt attribute
					.type {
						position: relative;
						display: block;
						float: left;
						width: auto;
						min-width: 34px;
						height: 30px;
						line-height: 30px;
						padding: 0 7px;
						margin: 0 5px 6px 0;
						border: 1px solid #ebebeb;
						cursor: pointer;
						text-align: center;
						font-size: 14px;
						font-weight: normal;
						&:before {
							content: "";
							position: absolute;
							display: block;
							top: 0;
							right: 0;
							width: 0;
							height: 0;
							border-top: 6px solid #ebebeb;
							border-left: 6px solid transparent;
							background: transparent;
						}
						&.active {
							outline: 2px solid #A5CD39;
							border: 0;
							&:before {
								border-top: 6px solid #A5CD39;
							}
						}
					}
				}
			}
			.box-sizes {
				position: relative;
				display: block;
				.sizes {
					-webkit-box-flex: 1;
					-webkit-flex-grow: 1;
					-ms-flex-positive: 1;
					flex-grow: 1;
					display: -webkit-box;
					display: -ms-flexbox;
					display: -webkit-flex;
					display: flex;
					-webkit-flex-wrap: wrap;
					flex-wrap: wrap;
					line-height: 30px;
					margin-top: 0;
					margin-bottom: 5px;
					font-family: 'Arial';
					font-size: inherit;
					.size {
						position: relative;
