				.rating-stars {
					position: relative;
					cursor: pointer;
					vertical-align: middle;
					display: inline-block;
					overflow: hidden;
					white-space: nowrap;
					margin-right: 5px;
				}
				.rating-input {
					position: absolute;
					cursor: pointer;
					width: 100%;
					height: 20px;
					bottom: 0;
					left: 0;
					font-size: 1px;
					border: none;
					background: 0 0;
					padding: 0;
					margin: 0;
					outline: none;
				}
				.star {
					display: inline-block;
					margin: 0 3px;
					text-align: center;
				}
				.empty-stars {
					position: relative;
					display: block;
					height: 20px;
					line-height: 20px;
					color: #aaa;
				}
				.filled-stars {
					position: absolute;
					left: 0;
					top: 0;
					margin: auto;
					color: #fde16d;
					height: 20px;
					line-height: 20px;
					white-space: nowrap;
					overflow: hidden;
					// -webkit-text-stroke: 1px #777;
					text-shadow: 0.5px 0.5px #acacac;
				}
				.clear-rating {
					color: #aaa;
					cursor: not-allowed;
					display: inline-block;
					vertical-align: middle;
					font-size: 60%;
					padding-right: 5px;
					line-height: 20px;
				}
				.caption {
					color: #999;
					display: inline-block;
					vertical-align: middle;
					font-size: 15px;
					margin-top: 0;
					margin-left: 5px;
					margin-right: 0;
					line-height: 20px;
					span {
						color: #fff;
						font-weight: inherit;
					}
				}
			}
			.rating-disabled {
				.rating-input {
					cursor: default;
				}
				.rating-stars {
					cursor: default;
				}
			}
			.rating-rtl {
				float: right;
				.filled-stars {
					left: auto;
					right: 0;
					@include transform(matrix(-1,0,0,1,0,0) translate3d(0,0,0));
				}
				.caption {
					margin-right: 5px;
					margin-left: 0;
				}
			}
			.rating-animate {
				.filled-stars {
					transition: width .25s ease;
					-o-transition: width .25s ease;
					-moz-transition: width .25s ease;
					-webkit-transition: width .25s ease;
				}
			}
			.rating-rtl.is-star {
				.filled-stars {
					right: .06em;
				}
			}
			.rating-rtl.is-heart {
				.empty-stars {
					margin-right: .07em;
				}
			}
			.rating-lg {
				font-size: 3.91em;
			}
			.rating-md {
				font-size: 16px;
			}
			.rating-sm {
				font-size: 2.5em;
			}
			.rating-xs {
				font-size: 2em;
			}
			.rating-xl {
				font-size: 4.89em;
			}
			.clear-rating-active {
				cursor: pointer !important;
				&:hover {
					color: #843534;
				}
			}
			.checked {
				color: #f5a100;
			}
		}

