
.agriTechExpo {
	// Start footer
	footer {
		position: relative;
		display: block;
		// background: #302B6B;
		// border-radius: 100px 100px 0px 0px;
		color: #fff;
		padding: 50px 0 16px;
		overflow: hidden;
		&:before {
			content: '';
			position: absolute;
			display: block;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='1920' height='1659' viewBox='0 0 1920 1659' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M0 100.253C0 43.3916 47.3505 -2.02706 104.163 0.340127L1824.16 72.0068C1877.73 74.2386 1920 118.311 1920 171.92V1659H0V100.253Z' fill='%23302B6B'/%3e%3c/svg%3e");
			background-repeat: no-repeat;
			background-size: 100% auto;
		}
		.footer-inner {
			position: relative;
			@media (min-width: 992px) {
				display: flex;
				align-items: center;
			}
		}
		a {
			color: #fff;
			text-decoration: none;
			&:hover {
				color: #242343;
			}
		}
		.footer-left { 
			position: relative;
			display: block;
			float: left;
			width: 230px;
			min-height: 1px;
			img {
				width: 102px;
			}
			.logo { 
				position: relative;
				display: block;
				vertical-align: top;
				outline: none;
				color: #fff;
				text-decoration: none;
				h4 {
					margin: 0;
					font-family: 'Averta-Bold';
					font-style: normal;
					font-size: 24px;
					line-height: 29px;
					letter-spacing: -0.05em;
					text-transform: uppercase;
					span { 
						white-space: nowrap;
						font {
							color: #109E4C;
						}
					}
					span:nth-child(2) {
						font-family: 'Averta-Black';
					}
				}
			}
		}

		.footer-right { 
			position: relative;
			display: block;
			float: left;
			width: calc(100% - 230px);
			min-height: 1px;
			padding-left: 44px;
			padding-top: 0;
			.wrap-txt-1 {
				position: relative;
				display: block;
				width: 205px;
				float: left;
				vertical-align: top;
			}
			.wrap-txt-2 {
				position: relative;
				display: block;
				width: calc(100% - 205px);
				float: left;
				vertical-align: top;
				padding-left: 50px;
				&:after {
					content: "";
					position: absolute;
					display: block;
					width: 1px;
					height: 46px;
					top: 0;
					left: 25px;
					background: rgba(255, 255, 255, 0.3);
				}
				strong {
					font-family: 'Averta-Semibold';
				}
			}
			p {
				font-size: 14px;
				line-height: 1.5;
				margin-bottom: 0;
			}
		}

		.footer-socail {
			width: 52px;
			height: 20px;
			margin: auto;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			.list-item-socail { 

				p { 
					margin: 0;
					a {
						margin-left: 15px;
						&:first-child {
							margin: 0;
						}
					}
				}
			}
		}
		@media (max-width: 991px) {
			.footer-left { 
				width: 100%;
				.logo { 
					h4 {
						font-size: 21px;
					}
				}
			}

			.footer-right {
				float: none;
				clear: both;
				width: 100%;
				padding-left: 0;
				padding-top: 8px;
				.wrap-txt-1 {
					width: 185px;
				}
				.wrap-txt-2 {
					width: calc(100% - 185px);
					&:after {
						height: 36px;
						top: 5px;
					}
				}
				p {
					font-size: 14px;
				}
			}

			.footer-socail {
				top: 5px;
				bottom: auto;
			}
		}
		@media (max-width: 767px) {
			.footer-left { 
				.logo { 
					width: 245px;
					h4 {
						font-size: 24px;
					}
				}
			}

			.footer-right {
				padding-top: 12px;
				.wrap-txt-1 {
					width: 100%;
					float: none;
					clear: both;
				}
				.wrap-txt-2 {
					width: 100%;
					float: none;
					clear: both;
					padding: 0;
					padding-top: 3px;
					&:after {
						display: none;
					}
				}
				// p {
				// 	font-size: 16px;
				// }
			}

		}
		@media (max-width: 420px) {
			.footer-left { 
				.logo { 
					h4 {
						font-size: 21px;
					}
				}
			}

			.footer-right {
				padding-top: 10px;
				p {
					strong {
						font-family: Averta;
						font-weight: normal;
					}
				}
			}

		}
		@media (max-width: 374px) {
			.footer-left { 
				.logo { 
					width: 190px;
					h4 {
						font-size: 18px;
					}
				}
			}
			.footer-right {
				.wrap-txt-2 {
					letter-spacing: unset;
					&:after {
						display: none;
					}
				}
				p {
					font-size: 14px;
				}
			}

		}

	}
	// End footer
}