
	.section-3d-virtual-list {
		position: relative;
		display: block;
		padding: 40px 0;
		background: #EEF3FF;
		overflow: hidden;
		.booth-inner {
			display: block;
		}
		.booth-box-title {
			position: relative;
			display: block;
			width: 100%;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 20px;
				line-height: 1.4;
				margin-bottom: 15px;
				@media (min-width: 992px) {
					font-size: 30px
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.booth-box-list {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 100%;
			.booth-list {
				position: relative;
				display: block;
				display: flex;
				flex-wrap: wrap;
				overflow: hidden;
				margin: 0 -12.5px;
				.booth-item {
					position: relative;
					display: flex;
					width: 100%;
					max-width: 700px;
					padding: 12.5px;
					margin: 0;

					@media (min-width: 768px) {
						width: calc(100% / 2);
					}
					@media (min-width: 992px) {
						width: calc(100% / 3);
					}
					.item-inner {
						position: relative;
						display: block;
						width: 100%;
						background: #FFFFFF;
						border-radius: 14px;
						box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
						overflow: hidden;

						.wrap-image {
							position: relative;
							display: block;
							width: 100%;
							>a {
								display: flex;
								justify-content: center;
								align-items: center;
							}
							img {
								width: 100%;
								max-width: 100%;
							}
							
						}
						.header-item {
							position: relative;
							display: flex;
							align-items: center;
							width: 100%;
							padding: 10px 15px;
							overflow: hidden;
						}

						.box-image {
							position: relative;
							display: block;
							width: 95px;
							height: 60px;
							float: left;
							.item-image-a {
								position: relative;
								display: block;
								padding: 5px;
								width: 100%;
								height: 100%;
								margin: 0 auto;
								background: #fff;
								border: 1px solid #EEEEEE;
								img {
									width: 100%;
									height: auto;
									max-height: 100%;
									object-fit: scale-down;
								}
							}
						}
						.box-info {
							position: relative;
							display: block;
							float: left;
							width: calc(100% - 100px);
							padding-left: 15px;
							.ratings {
								font-size: 13px;
								line-height: 1.5;
								color: #878787;
								.ratings-value {
									color: #109E4C;
									font-family: 'Averta-Bold';
								}
							}
							.item-title {
								position: relative;
								display: block;
								width: 100%;
								padding: 0;
								margin: 0;
								margin-bottom: 5px;
								.item-title-a {
									position: relative;
									display: block;
									display: -webkit-box;
									max-height: 48px;
									padding: 0;
									margin: 0;
									font-family: 'Averta-Bold';
									font-size: 14px;
									line-height: 1.35;
									color: #242343;
									text-decoration: none;
									text-transform: uppercase;
									overflow: hidden;
									-webkit-box-orient: vertical;
									text-overflow: ellipsis;
									word-break: break-word;
									-webkit-line-clamp: 2;
									@media (min-width: 768px) {
										font-size: 16px;
									}
									&:hover {
										color: #0BB451;
									}
								}
							}
							// .item-description {
							// 	display: block;
							// 	font-family: 'Arial';
							// 	font-size: 14px;
							// 	color: #666666;
							// 	white-space: nowrap;
							// 	text-overflow: ellipsis;
							// 	overflow: hidden;
							// 	.total-count {
