

/* Header Area css
============================================================================================ */
.agriTechExpo {
    .space-header-fixed {
        position: relative;
        display: block;
        width: 100%;
        height: 0;
    }

    header.header { 
        position: relative;
        display: block;
        width: 100%;
        background-color: #F5F8FF;
        color: #242343;
        padding: 15px 0 1px 0;
        z-index: 10;
        &.header-fixed {
            position: fixed;
            top: 0;
            box-shadow: 2px 1px 2px rgba(134, 134, 134, 0.5);
        }
        a {
            color: inherit;
            outline: none;
        }
        .header-top { 
            position: relative;
            display: block;
            width: 100%;
            clear: both;
            .menu-top-inner { 

                .group-btn {
                    display: inline-flex;
                    float: right;
                    font-size: 13px;
                    a {
                        color: inherit;
                        text-decoration: none;
                        &:hover {
                            color: #109E4C;
                        }
                    }
                    .btn-back { 
                        position: relative;
                        margin-right: 30px;
                        &:after {
                            content: "";
                            position: absolute;
                            top: 3px;
                            right: -15px;
                            width: 1px;
                            height: 13px;
                            background: #41689E;
                        }
                        a { 
                            .fal { 
                                padding-right: 5px;
                            }
                        }
                    }

                    .list-item-socail { 

                        p { 
                            margin: 0;
                            a {
                                margin-left: 15px;
                            }
                        }
                    }
                    .box-user-login {
                        position: relative;
                        padding-left: 30px;
                        &:before {
                            content: "";
                            position: absolute;
                            top: 3px;
                            left: 15px;
                            width: 1px;
                            height: 13px;
                            background: #41689E;
                        }
                        .user-not-logged-in {
                            .btn-sign-in {
                                cursor: pointer;
                                &:hover {
                                    color: #109E4C;
                                }
                            }
                        }
                        .user-logged {
                            display: inline-flex;
                            .user-name {
                                position: relative;
                                padding-left: 32px;
                                .avata {
                                    position: absolute;
                                    display: inline-block;
                                    top: -5px;
                                    left: 0;
                                    width: 26px;
                                    height: 26px;
                                    line-height: 26px;
                                    background: #BDBDBD;
                                    border-radius: 26px;
                                    text-align: center;
                                    white-space: nowrap;
                                    overflow: hidden;
                                    img {
                                        max-width: 100%;
                                        vertical-align: top;
                                    }
                                }
                                .name-value {
                                    position: relative;
                                    display: block;
                                    max-width: 120px;
                                    padding-right: 15px;
                                    white-space: nowrap;
                                    overflow: hidden;
                                    text-overflow: ellipsis;
                                    &:after {
                                        content: "\f0d7";
                                        position: absolute;
                                        display: block;
                                        display: inline-block;
                                        top: 0;
                                        right: 0;
                                        color: #C4C4C4;
                                        font-family: "Font Awesome 5 Pro";
                                        font-style: normal;
                                        font-variant: normal;
                                        text-rendering: auto;
                                        font-weight: 900;
                                    }
                                }
                                .dropdown-user {
                                    display: none;
                                    position: absolute;
                                    top: 100%;
                                    right: 0;
                                    z-index: 99;
                                    width: 175px;
                                    padding-top: 10px;
                                    &.collapse.in {
                                        display: block;
                                    }
                                    .myvne_user_status {
                                        position: absolute;
                                        display: block;
                                        list-style: none;
                                        width: 175px;
                                        padding: 5px 5px;
                                        margin: 0;
                                        right: -23px;
                                        top: 8px;
                                        color: #999;
                                        font-size: 13px;
                                        opacity: 1;
                                        transition: all 0.5s ease 0s;
                                        z-index: 9;
                                        background: #fff;
                                        box-shadow: 0 4px 6px 2px rgba(0, 0, 0, 0.2);
                                        border-top: 3px solid #109E4C;
                                        border-radius: 5px;
                                        &:before {
                                            content: "";
                                            position: absolute;
                                            display: block;
                                            top: -10px;
                                            left: auto;
                                            right: 20px;
                                            margin: auto;
                                            width: 0;
                                            height: 0;
                                            border-left: 7px solid transparent;
                                            border-right: 7px solid transparent;
                                            border-bottom: 7px solid #109E4C;
                                            z-index: 10;
                                        }
                                        li {
                                            position: relative;
                                            display: block;
                                            width: 100%;
                                            height: 30px;
                                            clear: both;
                                            margin: 0;
                                            padding: 0;
                                            a {
                                                position: relative;
                                                display: inline-block;
                                                width: 100%;
                                                padding: 0 5px;
                                                white-space: nowrap;
                                                overflow: hidden;
                                                line-height: 30px;
                                                border-radius: 3px;
                                                color: #242424;
                                                &:hover {
                                                    background: #f5f5f5;
                                                    color: #109E4C;
                                                }
                                            }
                                        }
                                    }
                                }
                                &:hover{
                                    .dropdown-user {
                                        display: block;
                                    }
                                }
                            }
                            .user-notify {
                                position: relative;
                                margin-left: 15px;
                                text-align: right;
                                display: none;
                                .notify-number {
                                    position: absolute;
                                    left: 10px;
                                    display: block;
                                    white-space: nowrap;
                                    min-width: 16px;
                                    top: -6px;
                                    background: #ed1c94;
                                    padding: 3px;
                                    font-size: 12px;
                                    line-height: 10px;
                                    border-radius: 12px;
                                    text-align: center;
                                    color: #fff !important;
                                }
                            }
                        }
                    }
                }
            }
        }

        .header-main { 
            position: relative;
            display: block;
            width: 100%;
            clear: both;
            z-index: 2;
            .header-left { 
                position: relative;
                display: block;
                float: left;
                width: 110px;
                min-height: 1px;
                img,
                svg {
                    // width: 102px;
                    height: 50px;
                }
                .logo { 
                    position: relative;
                    display: block;
                    vertical-align: top;
                    outline: none;
                    position: absolute;
                    top: -14px;
                    z-index: 2;
                    color: #fff;
                    text-decoration: none;
                    h1 {
                        margin: 0;
                        font-family: 'Averta-Bold';
                        font-style: normal;
                        font-size: 24px;
                        line-height: 29px;
                        letter-spacing: -0.05em;
                        text-transform: uppercase;
                        span { 
                            white-space: nowrap;
                            font {
                                color: #109E4C;
                            }
                        }
                    }
                }
            }

            .header-right { 
                position: relative;
                display: block;
                float: left;
                width: calc(100% - 110px);
                min-height: 1px;
                padding-top: 8px;
                padding-bottom: 8px;
                nav {
                    margin: 0;
                    padding: 0;
                }
                ul { 
                    list-style: none;
                    margin: 0;
                    padding: 0;
                    display: inline-flex;
                    float: right;
                    //transform: translateX(15px);
                    li { 
                        position: relative;
                        flex: auto;
                        margin-right: 10px;
                        &:last-child {
                            margin-right: 0;
                        }
                        a { 
                            position: relative;
                            display: block;
                            font-family: Averta;
                            font-style: normal;
                            font-weight: normal;
                            font-size: 14px;
                            line-height: 30px;
                            padding: 0 10px;
                            text-decoration: none;
                            text-transform: uppercase;
                            border-radius: 5px;
                            @media (max-width: 1199px) {
                                padding: 0 6px;
                            }
                            &:hover {
                                background-color: #109E4C;
                                color: #fff;
                            }
                            &.active {
                                background-color: #109E4C;
                                color: #fff;
                            }
                            &.item-cart {
                                padding: 0 8px;
                                &:hover {
                                    background-color: inherit;
                                    color: inherit;
                                }
                                .number-count {
                                    position: absolute;
                                    left: 70%;
                                    top: 0;
                                    min-width: 15px;
                                    height: 15px;
                                    padding: 0 5px;
                                    font-size: 12px;
                                    line-height: 15px;
                                    background: #FF6363;
                                    color: #fff;
                                    border-radius: 15px;
                                    white-space: nowrap;
                                    &.hidden {
                                        display: none;
                                    }
                                }
                            }
                            &.btn-show-search {
                                width: 30px;
                                height: 30px;
                                background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.7594 8.12971C14.7594 11.7912 11.7912 14.7594 8.12971 14.7594C4.46822 14.7594 1.5 11.7912 1.5 8.12971C1.5 4.46822 4.46822 1.5 8.12971 1.5C11.7912 1.5 14.7594 4.46822 14.7594 8.12971ZM12.7195 14.8408C11.4133 15.7358 9.83266 16.2594 8.12971 16.2594C3.63979 16.2594 0 12.6196 0 8.12971C0 3.63979 3.63979 0 8.12971 0C12.6196 0 16.2594 3.63979 16.2594 8.12971C16.2594 9.83266 15.7358 11.4133 14.8408 12.7195L20.03 17.9086L17.9086 20.03L12.7195 14.8408Z' fill='%23109E4C'/%3e%3c/svg%3e");
                                background-repeat: no-repeat;
                                background-size: auto;
                                background-position: center;
                                &.show {
                                    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M13 1L1 13M1 1L13 13' stroke='%23898898'/%3e%3c/svg%3e ");
                                }
                                &:hover {
                                    color: inherit;
                                    background-color: inherit;
                                }
                            }
                        }
                        .form-search-top {
                            position: absolute;
                            display: none;
                            top: 100%;
                            right: 0;
                            width: 300px;
                            height: 50px;
                            &.show {
                                display: block;
                            }
                            .form-group-inline {
                                position: relative;
                                display: flex;
                                align-items: center;
                                width: 100%;
                                padding: 10px;
                                background-color: #f5f8ff;
                                border-radius: 4px;
                                box-shadow: 0px -2px 4px #e6e6e6;
                                .input-search {
                                    width: calc(100% - 40px);
                                    height: 36px;
                                    padding: 0 10px;
                                    margin: 0;
                                    line-height: 36px;
                                    border: 1px solid #efefef;
                                    border-right: 0;
                                    box-shadow: none;
                                    outline: none;
                                    background-color: #fff;

                                    &::placeholder {
                                        font-size: 12px;
                                        color: #929292;
                                        font-style: italic;
                                    }
                                }
                                .btn-search {
                                    width: 40px;
                                    height: 36px;
                                    padding: 0 10px;
                                    margin: 0;
                                    line-height: 36px;
                                    border: 1px solid #efefef;
                                    box-shadow: none;
                                    outline: none;
                                    background-color: #fff;
                                    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.7594 8.12971C14.7594 11.7912 11.7912 14.7594 8.12971 14.7594C4.46822 14.7594 1.5 11.7912 1.5 8.12971C1.5 4.46822 4.46822 1.5 8.12971 1.5C11.7912 1.5 14.7594 4.46822 14.7594 8.12971ZM12.7195 14.8408C11.4133 15.7358 9.83266 16.2594 8.12971 16.2594C3.63979 16.2594 0 12.6196 0 8.12971C0 3.63979 3.63979 0 8.12971 0C12.6196 0 16.2594 3.63979 16.2594 8.12971C16.2594 9.83266 15.7358 11.4133 14.8408 12.7195L20.03 17.9086L17.9086 20.03L12.7195 14.8408Z' fill='%23109E4C'/%3e%3c/svg%3e");
                                    background-repeat: no-repeat;
                                    background-size: auto;
                                    background-position: center;
                                }
                            }
                        }
                    }
                }
            }
        }


        @media (max-width: 991px) {
            padding-bottom: 5px;
            .header-main { 
                .header-left { 
                    float: none;
                    width: 100%;
                    .logo {
                        top: -25px;
                        h1 {
                            font-size: 21px;
                        }
                    }
                }

                .header-right {
                    float: none;
                    width: 100%;
                    padding-top: 25px;
                    ul { 
                        display: flex;
                        float: none;
                        margin: 0 -11px;
                        transform: none;
                        justify-content: space-between;
                        li { 
                            flex: auto;
                            text-align: center;
                            margin-right: 3px;
                            a {
                                font-size: 13px;
                            }
                        }
                    }
                }
            }
        }

        @media (max-width: 767px) {
            padding: 0;
            .header-top {
                background: #00315d;
                padding: 5px 0;
                display: none;
                .menu-top-inner .group-btn {
                    display: flex;
                    float: none;
                    justify-content: space-between;
                }
            }
            .header-main { 
                padding: 8px 0;
                .header-left { 
                    float: none;
                    width: 100%;
                    .logo {
                        position: relative;
                        top: 0;
                        display: inline-block;
                        h1 {
                            font-size: 21px;
                            line-height: 29px;
                            letter-spacing: -0.05em;
                        }
                    }
                }

                .header-right {
                    float: none;
                    width: 100%;
                    display: none;
                    ul { 
                        display: flex;
                        float: none;
                        margin: 0 -15px;
                        overflow-x: auto;
                        li { 
                            flex: auto;
                            white-space: nowrap;
                        }
                    }
                }
            }
        }
    }


