            //overflow: hidden;
            .toggle-btn {
                display: block;
                cursor: pointer;
                position: absolute;
                left: 0;
                top: 0;
                width: 50px;
                height: var(--height-header);
                line-height: 67px;
                padding: 0;
                padding-left: 5px;
                background-color: transparent;
                color: #242343;
                font-size: 22px;
                text-align: center;
                z-index: 3;
                span {
                    width: 26px;
                    height: 25px;
                    line-height: 25px;
                    color: transparent;
                    vertical-align: baseline;
                    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='30' height='15' viewBox='0 0 30 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M0 0H30V3H0V0Z' fill='%230BB451'/%3e%3cpath d='M0 12H30V15H0V12Z' fill='%230BB451'/%3e%3cpath d='M0 6H20V9H0V6Z' fill='%230BB451'/%3e%3c/svg%3e");
                    background-repeat: no-repeat;
                    background-position: center;
                    user-select: none;
                    &:before {
                        content: '';
                        display: none;
                    }
                }
            }
            .brand { 
                position: relative;
                display: block;
                width: 90px;
                min-height: 1px;
                text-align: center;
                left: 50px;
                img {
                    height: 45px;
                }
                svg {
                    height: 45px;
                }
                .logo { 
                    position: relative;
                    display: block;
                    padding-top: 9px;
                    vertical-align: top;
                    outline: none;
                    position: relative;
                    color: #fff;
                    text-decoration: none;
                    h1 {
                        margin: 0;
                        font-family: 'Averta-Bold';
                        font-style: normal;
                        font-size: 24px;
                        line-height: 29px;
                        letter-spacing: -0.05em;
                        text-transform: uppercase;
                        span { 
                            white-space: nowrap;
                            font {
                                color: #F5A623;
                            }
                        }
                    }
                }
            }
            .box-user-login {
                position: absolute;
                display: flex;
                align-items: center;
                right: 0;
                top: 18px;
                padding-right: 15px;
                .user-not-logged-in {
                    .btn-sign-in {
                        position: relative;
                        display: block;
                        white-space: nowrap;
                        padding: 0 15px;
                        width: 93px;
                        height: 37px;
                        line-height: 37px;
                        background: #242343;
                        color: #fff;
                        border-radius: 3px;
                        cursor: pointer;
                        top: -5px;
                        text-transform: uppercase;
                    }
                }
                .user-logged {
                    display: inline-flex;
                    height: 28px;
                    .user-name {
                        position: relative;
                        padding-left: 30px;
                        cursor: pointer;
                        .avata {
                            position: absolute;
                            display: inline-block;
                            top: 0;
                            left: 0;
                            width: 28px;
                            height: 28px;
                            line-height: 28px;
                            border-radius: 28px;
                            background: #BDBDBD;
                            text-align: center;
                            white-space: nowrap;
                            overflow: hidden;
                            img {
                                max-width: 100%;
                                vertical-align: top;
                            }
                        }
                        .name-value {
                            position: relative;
                            display: block;
                            max-width: 80px;
                            padding-right: 15px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            display: none;
                            &:after {
                                content: "\f0d7";
                                position: absolute;
                                display: block;
                                display: inline-block;
                                top: 0;
                                right: 0;
                                color: #C4C4C4;
                                font-family: "Font Awesome 5 Pro";
                                font-style: normal;
                                font-variant: normal;
                                text-rendering: auto;
                                font-weight: 900;
                            }
                        }
                        .dropdown-user {
                            display: none;
                            position: absolute;
                            top: 100%;
                            right: 0;
                            z-index: 99;
                            width: 180px;
                            padding-top: 10px;
                            cursor: auto;
                            &.show {
                                display: block;
                            }
                            &.collapse.in {
                                display: block;
                            }
                            .myvne_user_status {
                                position: absolute;
                                display: block;
                                list-style: none;
                                width: 180px;
                                padding: 5px 5px;
                                margin: 0;
                                right: -15px;
                                top: 15px;
                                color: #999;
                                font-size: 13px;
                                opacity: 1;
                                transition: all 0.5s ease 0s;
                                z-index: 9;
                                background: #fff;
                                box-shadow: 0 4px 6px 2px rgba(0, 0, 0, 0.2);
                                border-top: 3px solid #109e4c;
                                border-radius: 5px;
                                &:before {
                                    content: "";
                                    position: absolute;
                                    display: block;
                                    top: -10px;
                                    left: auto;
                                    right: 24px;
                                    margin: auto;
                                    width: 0;
                                    height: 0;
                                    border-left: 7px solid transparent;
                                    border-right: 7px solid transparent;
                                    border-bottom: 7px solid #109e4c;
                                    z-index: 10;
                                }
                                li {
                                    position: relative;
                                    display: block;
                                    width: 100%;
                                    height: 30px;
                                    clear: both;
                                    margin: 0;
                                    padding: 0;
                                    a {
                                        position: relative;
                                        display: inline-block;
                                        width: 100%;
                                        padding: 0 5px;
                                        white-space: nowrap;
                                        overflow: hidden;
                                        line-height: 30px;
                                        border-radius: 3px;
                                        color: #242424;
                                        &:hover {
                                            background: #f5f5f5;
                                            color: #109e4c;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .user-notify {
                        position: relative;
                        margin-left: 15px;
                        text-align: right;
                        display: none;
                        .notify-number {
                            position: absolute;
                            left: 10px;
                            display: block;
                            white-space: nowrap;
                            min-width: 16px;
                            top: -6px;
                            background: #242343;
                            padding: 3px;
                            font-size: 12px;
                            line-height: 10px;
                            border-radius: 12px;
                            text-align: center;
                            color: #fff !important;
                        }
                    }
                }
            }
            .wrap-btn-cart {
                position: relative;
                display: block;
                width: 59px;
                padding-right: 15px;
                .item-cart {
                    position: relative;
                    display: block;
                    top: 0;
                    padding: 0 8px;
                    &:hover {
                        background: inherit;
                        color: inherit;
                    }
                    .icon-shopping {
                        position: relative;
                        display: block;
                        width: 28px;
                        height: 28px;
                        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='28' height='28' viewBox='0 0 28 28' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cg clip-path='url(%23clip2)'%3e%3cpath d='M9.07812 18.1018H23.8987C24.2657 18.1018 24.5876 17.8591 24.6869 17.5066L27.9684 6.02203C28.0389 5.77444 27.9906 5.50848 27.8353 5.30276C27.6798 5.09769 27.438 4.97656 27.1799 4.97656H7.18436L6.59796 2.33789C6.51465 1.96213 6.1814 1.69531 5.79688 1.69531H0.820312C0.367004 1.69531 0 2.06232 0 2.51562C0 2.96915 0.367004 3.33594 0.820312 3.33594H5.13849L8.1008 16.666C7.22922 17.045 6.61719 17.9125 6.61719 18.9221C6.61719 20.279 7.72119 21.383 9.07812 21.383H23.8987C24.3522 21.383 24.719 21.0162 24.719 20.5627C24.719 20.1094 24.3522 19.7424 23.8987 19.7424H9.07812C8.62631 19.7424 8.25781 19.3748 8.25781 18.9221C8.25781 18.4694 8.62631 18.1018 9.07812 18.1018Z' fill='%23109E4C'/%3e%3cpath d='M8.25781 23.8438C8.25781 25.2009 9.36182 26.3047 10.719 26.3047C12.0759 26.3047 13.1799 25.2009 13.1799 23.8438C13.1799 22.4868 12.0759 21.3828 10.719 21.3828C9.36182 21.3828 8.25781 22.4868 8.25781 23.8438Z' fill='%23109E4C'/%3e%3cpath d='M19.7971 23.8438C19.7971 25.2009 20.9011 26.3047 22.2581 26.3047C23.6152 26.3047 24.719 25.2009 24.719 23.8438C24.719 22.4868 23.6152 21.3828 22.2581 21.3828C20.9011 21.3828 19.7971 22.4868 19.7971 23.8438Z' fill='%23109E4C'/%3e%3c/g%3e%3cdefs%3e%3cclipPath id='clip2'%3e%3crect width='28' height='28' fill='white'/%3e%3c/clipPath%3e%3c/defs%3e%3c/svg%3e");
                        background-repeat: no-repeat;
                        background-position: center;
                        user-select: none;
                    }
                    .number-count {
                        position: absolute;
                        left: 70%;
                        top: 0;
                        min-width: 15px;
                        height: 15px;
                        padding: 0 5px;
                        font-size: 12px;
                        line-height: 16px;
                        background: #FF6363;
                        color: #fff;
                        border-radius: 15px;
                        white-space: nowrap;
                        z-index: 2;
                        user-select: none;
                        &.hidden {
                            display: none;
                        }
                    }
                }
            }



            .box-search-mb {
                position: absolute;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                right: 60px;
                top: 18px;
                padding-right: 0;
                .btn-show-search {
                    width: 30px;
                    height: 30px;
                    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.7594 8.12971C14.7594 11.7912 11.7912 14.7594 8.12971 14.7594C4.46822 14.7594 1.5 11.7912 1.5 8.12971C1.5 4.46822 4.46822 1.5 8.12971 1.5C11.7912 1.5 14.7594 4.46822 14.7594 8.12971ZM12.7195 14.8408C11.4133 15.7358 9.83266 16.2594 8.12971 16.2594C3.63979 16.2594 0 12.6196 0 8.12971C0 3.63979 3.63979 0 8.12971 0C12.6196 0 16.2594 3.63979 16.2594 8.12971C16.2594 9.83266 15.7358 11.4133 14.8408 12.7195L20.03 17.9086L17.9086 20.03L12.7195 14.8408Z' fill='%23109E4C'/%3e%3c/svg%3e");
                    background-repeat: no-repeat;
                    background-size: auto;
                    background-position: center;
                    &.show {
                        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M13 1L1 13M1 1L13 13' stroke='%23898898'/%3e%3c/svg%3e ");
                    }
                }

                .form-search-top {
                    position: absolute;
                    display: none;
                    top: calc(100% + 12px);
                    right: -20px;
                    width: 300px;
                    height: 50px;
                    @media (max-width: 375px) {
                        right: -50px;
                    }
                    &.show {
                        display: block;
                    }
                    .form-group-inline {
                        position: relative;
                        display: flex;
                        align-items: center;
                        width: 100%;
                        padding: 10px;
                        background-color: #EEEEEE;
                        border-radius: 4px;
                        box-shadow: 0px -2px 4px #e6e6e6;
                        .input-search {
                            width: calc(100% - 40px);
                            height: 36px;
                            padding: 0 10px;
                            margin: 0;
                            line-height: 36px;
                            border: 0;
                            box-shadow: none;
                            outline: none;
                            background-color: #fff;

                            &::placeholder {
                                font-size: 12px;
                                color: #929292;
                                font-style: italic;
                            }
                        }
                        .btn-search {
                            width: 40px;
                            height: 36px;
                            padding: 0 10px;
                            margin: 0;
                            line-height: 36px;
                            border: 0;
                            box-shadow: none;
                            outline: none;
                            background-color: #fff;
                            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.7594 8.12971C14.7594 11.7912 11.7912 14.7594 8.12971 14.7594C4.46822 14.7594 1.5 11.7912 1.5 8.12971C1.5 4.46822 4.46822 1.5 8.12971 1.5C11.7912 1.5 14.7594 4.46822 14.7594 8.12971ZM12.7195 14.8408C11.4133 15.7358 9.83266 16.2594 8.12971 16.2594C3.63979 16.2594 0 12.6196 0 8.12971C0 3.63979 3.63979 0 8.12971 0C12.6196 0 16.2594 3.63979 16.2594 8.12971C16.2594 9.83266 15.7358 11.4133 14.8408 12.7195L20.03 17.9086L17.9086 20.03L12.7195 14.8408Z' fill='%23109E4C'/%3e%3c/svg%3e");
