.agriTechExpo {
    &.modal-open {
        // position: fixed;
        height: 100%;
    }

    .modal-seminor {
        font-size: 14px;
        ::-webkit-input-placeholder { /* Edge */
            color: #8D8D8D;
        }
        :-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #8D8D8D;
        }
        ::placeholder {
            color: #8D8D8D;
        }
        .modal-header {
            padding: 40px 30px 20px;
            .close {
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                right: 0;
                top: 0;
                margin: 0;
                padding: 0;
                width: 40px;
                height: 40px;
                border-radius: 0 0 0 5px;
                border: 0;
                background: #dee2e6;
                box-shadow: 0 0 2px #626366;
                outline: none;
                font-family: auto;
                font-weight: unset;
                font-size: 30px;
            }
        }
        .modal-body {
            padding: 20px 30px 20px;
            .container-fluid {
                padding: 0;
                .row:not(.form-group) {
                    margin-left: 0;
                    margin-right: 0;
                }
                p {
                    font-family: 'Arial';
                }
            }
        }
        .modal-title {
            font-family: 'Averta-Bold';
            font-size: 26px;
            line-height: 1.3;
            text-transform: uppercase;
            color: #333333;
        }
        .form-group {
            .col-form-label  {
                font-family: 'Averta-Bold';
                font-size: 14px;
            }
        }
        .form-control {
            font-size: 14px;
            font-family: 'Arial';
            &:focus {
                color: #333;
                background-color: #fff;
                outline: 0;
                box-shadow: none;
                border: 1px solid #333;
            }
        }

        textarea {
            min-height: 80px;
            max-height: 120px;
        }

        .wrap-options-hour { 
            width: 100%;
            clear: both;
            margin-top: 10px;
            .wrap-options-hour-list {
                display: -ms-flexbox;
                display: flex;
                -ms-flex-wrap: wrap;
                flex-wrap: wrap;
                margin-right: -4px;
                margin-left: -4px;
                .item-option {
                    -ms-flex: 0 0 percentage(1/5);
                    flex: 0 0 percentage(1/5);
                    max-width: percentage(1/5);
                    vertical-align: top;
                    padding: 0 4px;
                    text-align: center;
                    label { 
                        position: relative;
                        width: 100%;
                        height: 65px;
                        left: 0px;
                        top: 0px;
                        background: #FFFFFF;
                        color: #8D8D8D;
                        border: 1px solid #BDBDBD;
                        border-radius: 3px;
                        margin-bottom: 8px;
                        cursor: pointer;
                        span { 
                            position: absolute;
                            width: 45px;
                            height: 36px;
                            left: 0;
                            top: 0;
                            margin: auto;
                            bottom: 0;
                            right: 0;
                            font-family: 'Arial';
                            font-size: 15px;
                            line-height: 80%;
                            word-spacing: 40px;
                        }
                    }
                    .form-check-input { 
                        width: 0;
                        height: 0;
                        padding: 0;
                        margin: 0;
                        &:checked ~ label {
                            background-color: #A5CD39;
                            color: #FFFFFF;
                            border: 0;
                        }
                        &:disabled ~ label {
                            background: #DEDEDE;
                            pointer-events: none;
                            cursor: not-allowed;
                        }
                    }
                    &.item-option-01 {
                        label {
                            background: #A5CD39;
                            color: #FFFFFF;
                            border: 0;
                        }
                    }
                    &.item-option-02 {
                        label {
                            background: #FFFFFF;
                        }
                        // .form-check-input {
                        //  &:checked ~ label {
                        //      background-color: #A5CD39;
                        //      color: #FFFFFF;
                        //      border: 0;
                        //  }
                        // }
                    }
                    &.item-option-03 {
                        label {
                            background: #DEDEDE;
                            pointer-events: none;
                            cursor: not-allowed;
                        }
                        .form-check-input {
                            pointer-events: none;
                        }
                    }
                }
            }

            .wrap-options-hour-note { 
                width: 100%;
                clear: both;
                display: flex;
                flex-wrap: wrap;
                padding-top: 10px;
                border-top: 1px solid #EEEEEE;
                .item-note { 
                    display: flex;
                    align-items: center;
                    width: 50%;
                    font-size: 14px;
                    line-height: 80%;
                    color: #8D8D8D;
                    margin-bottom: 8px;
                    font-family: 'Arial';
                    span { 
                        float: left;
                        &:first-child {
                            width: 23px;
                            height: 23px;
                            margin-right: 8px;
                            border-radius: 3px;
                        }
                    }
                    &.item-note-01 {
                        span {
                            &:first-child {
                                background: #A5CD39;
                            }
                        }
                    }
                    &.item-note-02 {
                        span {
                            &:first-child {
                                background: #FFFFFF;
                                border: 1px solid #BDBDBD;
                            }
                        }
                    }
                    &.item-note-03 {
                        span {
                            &:first-child {
                                background: #DEDEDE;
                            }
                        }
                    }
                }
            }
        }
        .wrap-upload-file { 
            width: 100%;
            clear: both;
            margin-top: 10px;
            position: relative;
            .input-file-upload {
                width: 0;
                height: 0;
                padding: 0;
                margin: 0;
                position: absolute;
                // width: 100%;
                // height: 100%;
                // padding: 0;
                // margin: 0;
                // position: absolute;
                // z-index: 4;
            }

            .input-file-label { 
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                width: 100%;
                height: 105px;
                border: 1px dashed #BDBDBD;
                border-radius: 3px;
                text-align: center;
                font-size: 14px;
                color: #8D8D8D;
                cursor: pointer;
                span { 
                    max-width: 179px;
                    margin-bottom: 9px;
                    i,svg { 
                        font-size: 36px;
                        color: #DEDEDE;
                    }
                }
            }
            #fileList {
                position: relative;
                width: 100%;
                height: auto;
                ul {
                    position: relative;
                    width: 100%;
                    height: auto;
                    max-height: 145px;
                    background: #fff;
                    border: 1px solid #ced4da;
                    overflow-y: auto;
                    text-align: left;

                    padding-left: 0;
                    list-style-position: inside;
                    list-style-position: outside;
                    text-indent: 0;
                    margin: 0;
                    padding: 5px 5px 5px 25px;
                    border-radius: 3px;
                    li {
                        width: 100%;
                    }
                }
                .file-name {
                    width: auto;
                    font-size: 13px;
                    color: #53555e;
                }
                .btn-remove {
                    font-size: 13px;
                    color: red;
                    padding-left: 5px;
                }
            }
        }
        .btn-primary {
            background-color: #5B8C51;
            border-color: #5B8C51;
        }

        @media (min-width: 992px) {
            font-size: 15px;
            .modal-header .close {
                width: 32px;
                height: 32px;
                top: -16px;
                right: -16px;
                background: white;
                border-radius: 50%;
            }
            .modal-dialog {
                max-width: 614px;
            }
            .form-control {
                font-size: 15px;
            }
        }
        @media (max-width: 991px) {
            .modal-title {
                font-size: 18px;
            }
        }
        @media (max-width: 380px) {
            .modal-header {
                padding: 40px 15px 20px;
            }
            .modal-body {
                padding: 20px 15px 20px;
            }
        }
    }

    span.help-block {
        position: relative;
        display: none;
        clear: both;
        margin-top: 5px;
        margin-bottom: 0;
        color: #BF0000;
        font-size: 13px;
        font-style: italic;
        line-height: 21px;
    }


    .tag-chatbot {
        position: fixed;
        z-index: 999;
        display: block;

        width: 0;
        height: 66px;
        right: 0;
        top: 25%;
        transition: all .5s;

        background: #5B8C51;
        border-radius: 5px 0px 0px 5px;
        box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.25);
        &.show {
            display: block;
            width: 242px;
        }
        .btn-call-chatbot {
            padding: 10px 18px;
            display: flex;
            align-items: center;
            white-space: nowrap;
            text-decoration: none;
            color: #FFFFFF;
            .btn-icon {

            }
            .btn-txt {
                padding-left: 15px;
                font-size: 16px;
            }
        }
    }



    .back-to-top-badge {
        display: none !important;
        position: fixed;
        z-index: 7;
        right: 18px;
        bottom: 5px;
        width: 34px;
        height: 34px;
        font-size: 12px;
        line-height: 32px;
        color: #5b5b5b;
        background: rgba(255, 255, 255, 0.8);
        border: 1px solid #bfbfbf;
        border-radius: 4px;
        text-align: center;
        transform: translateY(40px);
        transition: all 250ms ease;
        cursor: pointer;
        &:hover {
            color: $border-title-highlight;
            background: #fff;
            border-color: $border-title-highlight;
        }
        i {
            height: 32px;
            width: 32px;
            text-align: center;
            font-size: 26px;
            line-height: 32px;
            pointer-events: none;
        }
        &.back-to-top-badge-visible {
            pointer-events: all;
            transform: translateY(-55px);
            transition: all 250ms ease;
        }
        &.back-to-top-small {
            overflow: hidden;
            bottom: 0;
            font-size: 0;
        }
        @media (max-width: 991px) {
            right: 12px;
        }
    }



    $btn_size: 58px;
    $btn_size_mb: 40px;
    $btn_radius: 10px;

    .group-action-control-bottom {
        position: fixed;
        display: block;
        bottom: 100px;
        right: 30px;
        width: 70px;
        height: auto;
        z-index: 8;
        color: #000000;
        font-family: inherit;
        user-select: none;
        @media (max-width: 767px) {
            width: $btn_size_mb;
            bottom: 100px;
            right: 10px;
        }
        &:before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.16);
            filter: blur(10px);
            border-radius: 10px;
        }
        &-inner {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            overflow: hidden;
            background: #fff;
            padding: 6px;
            border-radius: 10px;
        }
        a {
            color: #302B6B;
        }
        .btn-control {
            position: relative;
            display: flex;
            justify-content: center;
            width: 100%;
            padding: 24px 0 10px;
            margin-bottom: 20px;
            background-color: transparent;
            color: #302B6B;
            font-family: 'Averta-Bold';
            font-size: 10px;
            line-height: 1.3;
            text-align: center;
            text-transform: uppercase;
            border-radius: 6px;
            word-break: normal;
            cursor: pointer;
            &:last-child {
                margin-bottom: 0;
            }
            &:after {
                content: '';
                position: absolute;
                display: block;
                width: 100%;
                height: 1px;
                background: #EEF3FF;
                bottom: -10px;
            }
            @media (min-width: 768px) {
                padding: 38px 0 10px;
                border-radius: $btn_radius;
            }
            &:hover {
                background-color: rgba(#bbbbbb,0.1);
                box-shadow: 0px 2px 10px 0px rgba(0,0,0,0.08);
            }

            .txt-label {
                display: none;
                @media (min-width: 768px) {
                    display: block;
                }
            }
        }
