/* Popup dialog area css
============================================================================================ */
.modal.popup-dialog {
    .modal-dialog {
        margin-top: 2rem;
        @media (min-width: 576px) {
            max-width: 600px;
            margin-top: 4rem;
        }
        @media (min-width: 1010px) {
            max-width: 900px;
            margin-top: 6rem;
        }
    }
    .modal-header {
        padding: .7rem 1rem;
        background: #0BB451;
        font-weight: bold;
        font-size: 20px;
        line-height: 23px;
        color: #FFFFFF;
        border-radius: 0;
        .modal-title {
            padding-right: 35px;
            font-size: 16px;
            line-height: 1.4;
            @media (min-width: 992px) {
                font-size: 18px;
            }
        }
        .bootbox-close-button {
            position: absolute;
            display: block;
            right: 0;
            top: 0;
            width: 32px;
            height: 32px;
            padding: 0;
            margin: 0;
            line-height: 32px;
            background-color: #FFFFFF;
            background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1 1L13 13M13 1L1 13' stroke='%239B9B9B'/%3e%3c/svg%3e");
            background-repeat: no-repeat;
            background-position: center;
            font-size: 0;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            border: 0;
            border-radius: 50%;
            outline: none;
            opacity: 1;
            transform: translate(50%, -50%);
        }
    }
    .modal-content {
        background: #FFFFFF;
        border: 0;
        border-radius: 0px;
        .bootbox-body {
            padding: 15px 0 0;
            text-align: center;

            //Start - form register page payment area
            .wrap-frm-register {
                font-size: 14px;
                text-align: left;
                .frmRegister {
                    padding: 0 40px;
                    @media (max-width: 767px) {
                        padding: 0;
                    }
                }
                p {
                    margin-bottom: 5px;
                }
