.styleAnimteBanner {
    .animateBanner {
        a {
            position: relative;
            display: block;
            overflow: hidden;
            border: 1px solid #f0f0f0;
        }
        a:after, 
        a:before {
            content: "";
            position: absolute;
            top: 10px;
            bottom: 10px;
            left: 10px;
            right: 10px;
            z-index: 2;
            border-color: rgba(255,255,255,.7);
            border-width: 2px;
            border-style: solid;
            transition: all .5s ease;
        }
        a:before {
            transform: scale(0,1);
            border-left-color: transparent;
            border-right-color: transparent;
        }
        a:after {
            border-top-color: transparent;
            border-bottom-color: transparent;
            transform: scale(1,0);
        }

        &:hover {

            a:before {
                transform: scale(1,1);
            }

            a:after {
                opacity: 1;
                filter: alpha(opacity=100);
                transform: scale(1,1);
            }
        }
    }
}

