.page-sitemaps {
	position: relative;
	display: block;
	padding-top: 30px;
	padding-bottom: 30px;
	overflow: hidden;
	overflow-y: auto;
	font-size: 16px;
	.index__title { 
		position: relative;
		display: block;
		font-size: 30px;
		margin-bottom: 10px;
		color: #6e6e6e;
		border-bottom: 3px solid #F2F5F8;
		&:after {
			content: "";
			position: absolute;
			bottom: -2px;
			left: 0;
			width: 120px;
			height: 3px;
			background: #40A691;
		}
	}

	.index__section { 

		.index__section-heading { 
			display: none;
		}

		.index__list { 
			list-style: none;
			width: 100%;
			padding: 0;
			margin: 0;
			.index__item { 
				position: relative;
				display: block;
				clear: both;
				width: 100%;
				margin: 0;
				border-bottom: 1px solid #DFE3ED;
				.index__item-link { 
					position: relative;
					display: block;
					padding: 8px 0;
					font-size: inherit;
					color: #757575;
					text-decoration: none;
					background-color: transparent;
					transition: all .01s;
					&:hover {
						color: #40A691;
						font-weight: 700;
						background-color: rgba(64,166,145,0.08);
					}
				}
			}
		}
	}
}