
.agriTechExpo {
	.wrap-cart-page {
		padding-top: 30px;
		@media (max-width: 767px) {
			&:not(.m-giohang) .container {
				max-width: 100%;
			}
		}

		.cart-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			color: #333333;
			text-align: center;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 24px;
				line-height: 31px;
				margin-bottom: 30px;
				text-transform: uppercase;
				&:after {
					content: "";
					position: absolute;
					display: block;
					width: 100%;
					height: 5px;
					background: #0BB451;
					bottom: -10px;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
	}
	.pc-cart-step-1 {
		position: relative;
		display: block;
		display: -webkit-flex;
		display: flex;
		flex-wrap: wrap;
		-webkit-flex-wrap: wrap;
		padding-top: 30px;
		margin-bottom: 30px;
		background: #F7F7F7;
		.col-left-cart {
			position: relative;
			display: block;
			float: left;
			width: calc(100% - 370px);
			margin-right: 20px;
			@media (max-width: 1199px) {
				width: calc(100% - 320px);
			}
			@media (max-width: 991px) {
				width: 100%;
				margin-right: 0;
			}
		}
		.col-right-cart {
			position: relative;
			display: block;
			float: left;
			width: 350px;
			padding-right: 20px;
			@media (max-width: 1199px) {
				width: 300px;
			}
			@media (max-width: 991px) {
				width: 100%;
				padding-left: 20px;
			}
		}
		.cart-not-item {

		}
		.col-fluid-cart {
			position: relative;
			display: block;
			float: none;
			width: 100%;
			clear: both;
			background: #fff;
			margin-bottom: 30px;
			.cart-not-item {
				position: relative;
				display: block;
				width: 100%;
				padding: 40px 15px;
				text-align: center;
				p {
					position: relative;
					margin-bottom: 15px;
				}
				.btn-continue-shoping {
					position: relative;
					display: block;
					max-width: 250px;
					width: 100%;
					height: 40px;
					margin: 15px auto 0;
					text-align: center;
					color: #ffffff;
					background: #d42027;
					font-size: 14px;
					line-height: 40px;
					font-weight: bold;
					text-transform: uppercase;
					border-radius: 6px;
				}
			}
		}
	}



	.wrap-orders {
		position: relative;
		display: block;
		border-left: 0;
		border-right: 0;
		overflow: hidden;
		font-family: Arial;
		p {
			font-size: 14px;
			line-height: 14px;
			margin-bottom: 10px;
		}
		form.form-order {
			position: relative;
			display: block;
			padding: 0;
			margin: 0;
			border: 0;
			outline: 0;
		}
		.scroll_box {
			position: relative;
			width: 100%;
			overflow: hidden;
			overflow-x: overlay;
			margin-bottom: 10px;
		}
		table.table-order {
			position: relative;
			display: table;
			width: 100%;
			min-width: 464px;
			margin: 0;
			margin-bottom: 15px;
			font-size: 14px;
			letter-spacing: -0.5px;
			border: 0;
			border-style: double;
			background: none;
			tr,th,td {
				padding: 0;
				margin: 0;
				border: 0;
				font-weight: initial;
				font-size: 14px;
			}
			thead {
				position: relative;
				width: 100%;
				tr {
					padding: 0;
					margin: 0;
					th {
						vertical-align: middle;
						border-bottom: 0;
						padding: 10px 8px;
						background: none;
						border-bottom: 1px solid #ebebeb;
						font-weight: bold;
						&:first-child {
							padding-left: 15px;
						}
						&:last-child {
							padding-right: 20px;
						}
						.btn-select-all-pro {
							position: relative;
							display: block;
							width: 100%;
							text-align: left;
							border-radius: 8px;
							box-shadow: none;
							-webkit-box-shadow: none;
							-moz-box-shadow: none;
							cursor: pointer;
							&.active {
								.cell-checkbox {
									&:before {
										content: "";
										visibility: visible;
									}
									&:after {
										content: "";
										visibility: visible;
									}
								}
							}
							.cell-checkbox {
								position: relative;
								display: block;
								float: left;
								max-width: 18px;
								width: 18px;
								height: 18px;
								margin-top: 0;
								text-align: center;
								line-height: 18px;
								color: #242424;
								background: #fff;
								border: 1px solid #dddddd;
								border-radius: 3px;
								&:before {
									position: absolute;
									display: block;
									top: 7px;
									left: 4px;
									content: "";
									width: 2px;
									height: 5px;
									background: #242424;
									-webkit-transform: rotateZ(-45deg);
									-moz-transform: rotateZ(-45deg);
									-o-transform: rotateZ(-45deg);
									transform: rotateZ(-45deg);
									visibility: hidden;
								}
								&:after {
									position: absolute;
									display: block;
									top: 3px;
									left: 9px;
									content: "";
									width: 2px;
									height: 10px;
									background: #242424;
									-webkit-transform: rotateZ(45deg);
									-moz-transform: rotateZ(45deg);
									-o-transform: rotateZ(45deg);
									transform: rotateZ(45deg);
									visibility: hidden;
								}
							}
							.cell-text {
								position: relative;
								display: block;
								float: left;
								width: calc(100% - 20px);
								font-size: 13px;
								padding-left: 10px;
								span {
									position: relative;
									display: inline-block;
									line-height: 18px;
									font-size: 14px;
									color: #242424;
								}
								span.count-item-pro {
									color: #666666;
									font-size: 13px;
									line-height: 15px;
								}

							}
						}
					}
					td {
						vertical-align: middle;
						border-bottom: 1px solid #efefef;
						padding: 8px 8px;
					}
				}
			}
			tbody {
				position: relative;
				width: 100%;
				tr {
					padding: 0;
					margin: 0;
					&.no-boder {
						td {
							border: 0;
						}
					}
					&:first-child {
						td {
							border-top: 0;
						}
					}
					&.product-out-of-stock {
						td {
							a.name-product,
							.amount,
							.unit-price {
								color: #ccc;
							}
						}
					}
					td {
						vertical-align: top;
						border: 0;
						border-bottom: 0;
						padding: 15px 8px;
						&:first-child {
							padding-left: 15px;
							padding-right: 2px;
						}
						&:last-child {
							padding-right: 20px;
						}
						&.group-cart-name {
							padding: 10px 15px !important;
							border: 0;
							border-bottom: 1px solid #ebebeb;
							.title-order {
								.cell-checkbox {
									margin-right: 10px;
								}
								.title {
									font-weight: bold;
								}
								.time-oder {
									display: inline-block;
									vertical-align: top;
									color: #666666;
									padding-left: 5px;
								}
								.btn-unset-cart {
									float: right;
									color: #666666;
									font-size: 13px;
									text-decoration: underline;
									cursor: pointer;
								}
							}
						}
						.cell-checkbox {
							position: relative;
							display: block;
							float: left;
							width: 20px;
							vertical-align: middle;
							cursor: pointer;
							.btn-checkbox {
								position: relative;
								display: block;
								max-width: 20px;
								width: 20px;
								height: 20px;
								text-align: center;
								line-height: 20px;
								color: #242424;
								background: #fff;
								border: 1px solid #dddddd;
								border-radius: 3px;
								&.active {
									&:before {
										content: "";
										visibility: visible;
									}
									&:after {
										content: "";
										visibility: visible;
									}
								}
								&:before {
									position: absolute;
									display: block;
									top: 8px;
									left: 5px;
									content: "";
									width: 2px;
									height: 6px;
									background: #242424;
									-webkit-transform: rotateZ(-45deg);
									-moz-transform: rotateZ(-45deg);
									-o-transform: rotateZ(-45deg);
									transform: rotateZ(-45deg);
									visibility: hidden;
								}
								&:after {
									position: absolute;
									display: block;
									top: 4px;
									left: 10px;
									content: "";
									width: 2px;
									height: 11px;
									background: #242424;
									-webkit-transform: rotateZ(45deg);
									-moz-transform: rotateZ(45deg);
									-o-transform: rotateZ(45deg);
									transform: rotateZ(45deg);
									visibility: hidden;
								}
							}
						}
						.wrap-img {
							position: relative;
							display: block;
						}
						.img-item-cart {
							position: relative;
							display: block;
							text-align: center;
							width: 100px;
							height: 100px;
							background: #f2f3f5;
							overflow: hidden;
							img {
								max-width: 100%;
								margin: auto;
								object-fit: contain;
							}
						}
						&:last-child {
							padding-left: 0;
						}
						a.name-product {
							position: relative;
							display: block;
							line-height: 18px;
							padding-top: 3px;
							color: #242424;
							margin-bottom: 10px;
							word-break: break-word;
						}
						.warning-quantity {
							color: #d42027;
							font-size: 12px;
							font-style: italic;
						}
						.unit-price,.amount {
							line-height: 26px;
							font-size: 14px;
						}
						.supplier {
							position: relative;
							display: block;
							color: #666666;
							font-size: 14px;
						}
						.group-action {
							position: relative;
							display: block;
							a,
							.delete {
								color: #242424;
								cursor: pointer;
								&:hover {
									color: #d42027;
								}
							}
						}
						.group-attach-sales {
							position: relative;
							display: block;
							width: 100%;
							padding: 2px 0px;
							padding-left: 135px;
							overflow: hidden;
							&:before {
								content: "";
								position: absolute;
								display: block;
								width: 1px;
								height: 100%;
								top: -26px;
								left: 70px;
								bottom: 0;
								background: #eee;
								z-index: 2;
							}
							.item-sale {
								position: relative;
								display: block;
								width: 100%;
								clear: both;
								display: table;
								&:not(:last-child) {
									margin-bottom: 10px;
								}
								&:before {
									content: "";
									position: absolute;
									display: block;
									width: 50px;
									height: 1px;
									left: -65px;
									top: 0;
									bottom: 0;
									margin: auto;
									background: #eee;
									z-index: 2;
								}
								.info {
									position: relative;
									display: block;
									float: left;
									width: calc(100% - 90px);
									.box-img-attach {
										position: relative;
										width: 50px;
										height: 50px;
										float: left;
										margin-right: 10px;
									}
									.name {
										color: #242424;
									}
									p:first-child:before {
										content: "+";
										position: relative;
										display: block;
										width: 25px;
										height: 30px;
										float: left;
										text-align: center;
									}
									p.i {
										font-size: 14px;
									}
								}
								.price {
									position: relative;
									display: block;
									float: left;
									width: 90px;
									text-align: right;
								}
							}
						}
						&.line-dashed {
							padding: 0 15px !important;
							margin: 0;
							border-bottom: 1px dashed #ebebeb;
						}
						.wrap-note-order {
							position: relative;
							display: block;
							width: 100%;
							.label-box {
								position: relative;
								display: inline-block;
								vertical-align: top;
								width: 65px;
								line-height: 40px;
								font-weight: bold;
								color: #242424;
							}
							.area-box {
								position: relative;
								display: inline-block;
								vertical-align: top;
								width: calc(100% - 70px);
								min-height: 40px;
								max-height: 120px;
								border: 1px solid #ebebeb;
								border-radius: 5px;
								padding: 8px 10px;
								line-height: 18px;
								font-size: 14px;
								color: #242424;
							}
						}
					}
					.input-quantity {
						position: relative;
						display: block;
						float: none;
						clear: both;
						width: 80px;
						height: 34px;
						padding: 0;
						margin: 0;
						border-radius: 0;
						border: 1px solid #cccccc;
						border-radius: 3px;
						overflow: hidden;
						outline: none;
						.up_num, .down_num {
							position: absolute;
							display: inline-block;
							height: 100%;
							line-height: 32px;
							width: 24px;
							vertical-align: middle;
							font-family: 'Font Awesome 5 Pro';
							font-weight: 500;
							font-size: 12px;
							text-align: center;
							text-rendering: auto;
							-webkit-font-smoothing: antialiased;
							-moz-osx-font-smoothing: grayscale;
							cursor: pointer;
							z-index: 2;
							color: #666;
							&:hover {
								background: #ddd;
								color: #d42027;
							}
						}
						.down_num {
							top: 0;
							left: 0;
							border-right: 1px solid #eee;
							&:before {
								content: "\f068";
							}
						}
						.up_num {
							top: 0;
							right: 0;
							border-left: 1px solid #eee;
							&:before {
								content: "\f067";
							}
						}

						input[type="number"] {
							position: relative;
							-webkit-appearance: textfield;
						}	
						input[type="number"]::-webkit-outer-spin-button, 
						input[type="number"]::-webkit-inner-spin-button {
							-moz-appearance: number-input;
							-webkit-appearance: none;
							background: none;
							width: 14px;
							border-left: 0;
							opacity: .4;
							position: absolute;
							display: block;
							top: 0;
							right: 0;
							bottom: 0;
							margin: 0;
							cursor:pointer;
							color: #000;
							text-align:center;
							visibility: hidden;
							-webkit-appearance: textfield;
							&:hover,
							&:active{
								box-shadow: 0 0 2px #e7e7e7;
								opacity: .8;
								background: none;
							}
						}
						.number-selected {
							position: relative;
							display: block;
							width: 100%;
							line-height: 34px;
							text-align: center;
							color: #242424;
							background: #ebebeb;
						}
					}
					.choose-quantity,.select-quality {
						float: left;
						width: 78px;
						padding: 0 27px 0 25px;
						border: 0;
						height: 34px;
						font-size: 14px;
						text-align: center;
						box-shadow: none;
						-webkit-box-shadow: none;
						-moz-box-shadow: none;
						outline: none;
					}
					.show-selected-quantity {
						width: 60px;
						line-height: 26px;
						font-size: 14px;
						text-align: center;
					}
					.bao-het-hang {
						position: relative;
						display: block;
						float: none;
						clear: both;
						width: 70px;
						line-height: 26px;
						font-size: 14px;
						padding: 0;
						margin: 0;
						color: #d42027;
					}
				}
			}
			tfoot {
				position: relative;
				width: 100%;
				font-size: 14px;
				border-top: 1px solid #ebebeb;
				strong {
					font-weight: bold;
				}
				tr {
					td {
						vertical-align: middle;
						border-bottom: 0;
						padding: 8px 10px;
						.box-check-code {
							position: relative;
							display: block;
							width: 100%;
							padding: 6px 0;
							.box_coupon {
								position: relative;
								display: block;
								width: 100%;
								height: 40px;
								padding: 0;
								margin: 0;
								border-radius: 4px;
								overflow: hidden;
								border: 1px solid #ebebeb;
								input {
									position: relative;
									display: block;
									float: left;
									width: 100%;
									height: 40px;
									padding: 0 10px;
									border: 0;
									border-radius: 0;
									border: 1px solid #ebebeb;
								}
								button {
									position: relative;
									display: block;
									float: left;
									width: 84px;
									height: 40px;
									background: #f2f3f5;
									color: #333;
									border: 0;
									&.clear-code {
										background: #d8d8d8;
										color: #242424;
									}
								}
							}
						}
						.col-left {
							position: relative;
							display: block;
							float: left;
							width: 200px;
							padding: 6px 5px;
							padding-right: 20px;
							text-align: left;
							font-size: 14px;
							.checkboxes-order {
								position: relative;
								display: block;
								line-height: 24px;
								vertical-align: middle;
								input {
									position: relative;
									vertical-align: middle;
									margin: 0;
								}
							}
						}
						.col-right {
							position: relative;
							display: block;
							float: left;
							width: calc(100% - 200px);
							padding: 6px 5px;
							line-height: 18px;
							p {
								line-height: 24px;
								margin-bottom: 8px;
							}
							span {
								text-align: right;
								float: right;
							}
							.hr {
								position: relative;
								display: block;
								width: 100%;
								height: 0;
								margin: 5px 0;
								border-bottom: 1px dashed #efefef;
							}
							.total-amount {
								font-weight: bold;
							}
							.total-invoice {
								position: relative;
								display: block;
								font-size: 16px;
								p {
									font-size: 16px;
									line-height: 18px;
									font-weight: bold;
									margin: 0;
								}
								.value {
									font-size: 18px;
									color: #242424;
									float: right;
								}
								.check-vat {
									text-align: right;
									color: #999999;
									font-size: 14px;
								}
							}
						}
					}
				}
			}
		}
		.btn-delete-all {
			position: relative;
			display: block;
			clear: both;
			width: 100%;
			padding: 10px 15px;
			background: #fff;
			text-align: center;
			color: #d42027;
			text-decoration: underline;
			border-radius: 10px;
			&:hover {
				background: #d42027;
				color: #fff;
			}
		}
	}
	.col-right-cart {
		padding-right: 0;
		.box-inner {
			position: relative;
			display: block;
			width: 100%;
			&.amount-fixed {
				position: -webkit-sticky;
				position: sticky;
				top: 100px;
				margin-bottom: 20px;
				// right: 0;
				// width: 480px;
				// max-width: 480px;
				// height: auto;
			}
		}
	}
	.invoice-cart {
		position: relative;
		display: block;
		width: 100%;
		padding: 5px 15px 10px;
		clear: both;
		background: rgba(11, 180, 81, 0.05);
		.hr {
			position: relative;
			display: block;
			width: 100%;
			height: 0;
			margin: 5px 0;
			border-bottom: 1px dashed #cccccc;
		}
		p {
			line-height: 30px;
			margin-bottom: 8px;
			span {
				float: right;
			}
		}

		.total-price {
			font-weight: bold;
		}
		.total-amount {
			font-size: 15px;
		}
		.total-invoice {
			position: relative;
			display: block;
			padding-top: 5px;
			font-size: 16px;
			p {
				font-size: 16px;
				line-height: 18px;
				font-weight: bold;
			}
			.value {
				font-size: 18px;
				color: #242424;
				float: right;
			}
			.check-vat {
				text-align: right;
				color: #999999;
				font-size: 14px;
			}
		}
	}

	.btn-checkout {
		position: relative;
		display: block;
		width: 100%;
		height: 60px;
		text-align: center;
		color: #ffffff;
		background: #0BB451;
		font-family: 'Averta-Semibold';
		font-size: 18px;
		line-height: 60px;
		text-transform: uppercase;
		border-radius: 3px;
		text-decoration: none;
		&:hover,&:focus {
			background: #0f8e44;
			color: #fff;
		}
		&.disable {
			background: #5a5a5a;
			color: #fff;
		}
	}

	.wrap-total-checkout {
		.group-btn-click {
			position: unset;
			display: block;
			width: 100%;
			overflow: hidden;
			background: #fff;
			margin-bottom: 15px;
			i {
				font-size: 16px;
			}
		}
		.btn-show-more {
			position: relative;
			display: block;
			width: 100%;
			height: 50px;
			line-height: 50px;
			padding: 0;
			margin: 0 auto;
			cursor: pointer;
			color: #242424;
			text-align: center;
			z-index: 3;
			background: #ffffff;
			&:hover,&:focus {
				background: #d7d7d7;
			}
		}
		.btn-hide-more {
			position: relative;
			display: none;
			width: 100%;
			height: 50px;
			line-height: 50px;
			padding: 0 5px;
			margin: 0 auto;
			cursor: pointer;
			color: #242424;
			text-align: center;
			z-index: 3;
			background: #fff;
			&:hover,&:focus {
				background: #d7d7d7;
			}
		}
		&.active {
			padding: 0;
			box-sizing: unset;
			.btn-show-more {
				display: none;
			}
			.btn-hide-more {
				display: block;
			}
		}
	}

}