
// Start page gio hang
	.m-giohang {
		visibility: visible;
		.wrap-orders {
			position: relative;
			display: block;
			margin-bottom: 10px;
			border-left: 0;
			border-right: 0;
			overflow-x: hidden;
			&.wrap-orders-step-1 {
				.table-order .list-item-cart .item-cart .info-item {
					position: relative;
					display: block;
					float: left;
					width: calc(100% - 55px - 30px);
					padding-left: 12px;
					vertical-align: middle;
				}
			}
			p {
				font-size: 14px;
				line-height: 16px;
				margin-bottom: 8px;
			}
			.form-order {
				position: relative;
				display: block;
				padding: 0;
				margin: 0;
				border: 0;
				outline: 0;
			}

			.table-order {
				position: relative;
				display: block;
				float: none;
				clear: both;
				width: 100%;
				.list-item-cart {
					position: relative;
					display: block;
					float: none;
					clear: both;
					width: 100%;
					.title-wrap-order {
						position: relative;
						display: block;
						padding: 0;
						margin: 12px 0;
						background: #fff;
						.title {
							position: relative;
							display: block;
							height: 38px;
							line-height: 38px;
							font-size: 15px;
							font-weight: bold;
							text-transform: uppercase;
						}
						.btn-delete-all {
							position: absolute;
							top: 0;
							right: 0;
							width: 100px;
							height: 36px;
							line-height: 36px;
							padding-left: 10px;
							padding-right: 5px;
							z-index: 2;
							cursor: pointer;
							&:hover {
								color: #d52329;
							}
						}
					}
					.cell-checkbox {
						position: relative;
						display: block;
						float: left;
						width: 30px;
						vertical-align: middle;
						.btn-checkbox {
							position: relative;
							display: block;
							max-width: 20px;
							width: 20px;
							height: 20px;
							text-align: center;
							line-height: 20px;
							color: #d52329;
							background: #fff;
							border: 1px solid #ebebeb;
							border-radius: 3px;
							&.active {
								&:before {
									content: "";
									visibility: visible;
								}
								&:after {
									content: "";
									visibility: visible;
								}
							}
							&:before {
								position: absolute;
								display: block;
								top: 8px;
								left: 5px;
								content: "";
								width: 2px;
								height: 6px;
								background: #242424;
								-webkit-transform: rotateZ(-45deg);
								-moz-transform: rotateZ(-45deg);
								-o-transform: rotateZ(-45deg);
								transform: rotateZ(-45deg);
								visibility: hidden;
							}
							&:after {
								position: absolute;
								display: block;
								top: 4px;
								left: 10px;
								content: "";
								width: 2px;
								height: 11px;
								background: #242424;
								-webkit-transform: rotateZ(45deg);
								-moz-transform: rotateZ(45deg);
								-o-transform: rotateZ(45deg);
								transform: rotateZ(45deg);
								visibility: hidden;
							}
						}
					}
					.title-order {
						position: relative;
						display: block;
						padding: 0;
						font-weight: bold;
						font-size: 14px;
						overflow: hidden;
						line-height: 34px;
						margin-top: 12px;
						background: #ffffff;
						border-bottom: 1px dashed #ebebeb;
						.title {
							position: relative;
							display: block;
							float: left;
							width: calc(100% - 30px);
							line-height: 46px;
							white-space: nowrap;
							overflow: hidden;
							text-overflow: ellipsis;
						}
						.btn-checkbox {
							margin-top: 12px;
						}
					}
					.border-dot {
						position: relative;
						display: block;
						height: 0;
						border-bottom: 1px dashed #efefef;
					}
					.item-cart {
						position: relative;
						display: block;
						float: none;
						clear: both;
						width: 100%;
						padding: 10px 0;
						background: #ffffff;
						&.product-out-of-stock {
							.info-item {
								.name-product {
									color: #ccc;
								}
								.list-inline {
									.amount-item {
										color: #ccc;
									}
									.btn-delete {
										color: #aeaeae;
									}
								}
							}
						}
						.btn-checkbox {
							margin-top: 15px;
						}
						.img-item-cart {
							position: relative;
							display: block;
							float: left;
							width: 55px;
							height: 55px;
							vertical-align: middle;
							background: #f2f3f5;
							border: 1px solid #f4f4f4;
							border-radius: 4px;
							overflow: hidden;
							z-index: 3;
							a {
								position: relative;
								display: block;
								overflow: hidden;
							}
							img {
								position: relative;
								display: block;
								width: 100%;
								object-fit: contain;
							}
						}
						.info-item {
							position: relative;
							display: block;
							float: left;
							width: calc(100% - 55px);
							padding-left: 12px;
							vertical-align: middle;
							.name-product {
								position: relative;
								display: block;
								clear: both;
								width: 100%;
								margin-bottom: 5px;
								color: #242424;
								font-size: 14px;
								line-height: 18px;
							}
							.warning-quantity {
								position: relative;
								display: block;
								clear: both;
								margin-bottom: 5px;
								font-size: 13px;
								line-height: 16px;
								font-style: italic;
								color: #666666;
								span {
									color: #d52329;
								}
							}
							.list-inline {
								position: relative;
								display: block;
								width: 100%;
								padding-top: 5px;
								float: none;
								clear: both;
								display: inline-block;
								display: flex;
								.delete,
								.btn-delete {
									position: relative;
									display: block;
									float: left;
									width: 32px;
									height: 26px;
									line-height: 26px;
									text-align: center;
									color: #d8d8d8;
									font-size: 21px;
									flex: auto;
								}
								.choose-quantity {
									float: left;
									width: 73px;
									padding: 0 24px 0 24px;
									border: 0;
									height: 32px;
									line-height: 32px;
									font-size: 14px;
									background: #fff;
									color: #242424;
									text-align: center;
									box-shadow: none;
									-webkit-box-shadow: none;
									-moz-box-shadow: none;
									outline: none;
								}
								.select-quality,
								.select-quantity {
									position: relative;
									display: block;
									float: left;
									width: 53px;
									height: 26px;
									line-height: 26px;
									padding: 0;
									margin-left: 20px;
									margin-left: 4%;
									border-radius: 4px;
									background: #fff;
									color: #242424;
									border: 1px solid #ddd;
									box-shadow: none;
									-webkit-box-shadow: none;
									-moz-box-shadow: none;
									outline: none;
									white-space: nowrap;
								}
								.input-quantity {
									position: relative;
									display: block;
									float: left;
									width: 75px;
									max-width: 75px;
									height: 32px;
									padding: 0;
									margin: 0;
									border-radius: 0;
									border: 1px solid #d8d8d8;
									border-radius: 3px;
									overflow: hidden;
									outline: none;
									flex: auto;
									.up_num, .down_num {
										position: absolute;
										display: inline-block;
										height: 100%;
										line-height: 32px;
										width: 24px;
										vertical-align: middle;
										font-family: 'Font Awesome 5 Pro';
										font-weight: 500;
										font-size: 12px;
										text-align: center;
										text-rendering: auto;
										-webkit-font-smoothing: antialiased;
										-moz-osx-font-smoothing: grayscale;
										cursor: pointer;
										z-index: 2;
										color: #666;
										&:hover {
											background: #ddd;
											color: #d42027;
										}
									}
									.down_num {
										top: 0;
										left: 0;
										border-right: 1px solid #ebebeb;
										&:before {
											content: "\f068";
										}
									}
									.up_num {
										top: 0;
										right: 0;
										border-left: 1px solid #ebebeb;
										&:before {
											content: "\f067";
										}
									}

									input[type="number"] {
										position: relative;
										-webkit-appearance: textfield;
									}	
									input[type="number"]::-webkit-outer-spin-button, 
									input[type="number"]::-webkit-inner-spin-button {
										-moz-appearance: number-input;
										-webkit-appearance: none;
										background: none;
										width: 14px;
										border-left: 0;
										opacity: .4;
										position: absolute;
										display: block;
										top: 0;
										right: 0;
										bottom: 0;
										margin: 0;
										cursor:pointer;
										color: #000;
										text-align:center;
										visibility: hidden;
										-webkit-appearance: textfield;
										&:hover,
										&:active{
											box-shadow: 0 0 2px #e7e7e7;
											opacity: .8;
											background: none;
										}
									}
									.number-selected {
										position: relative;
										display: block;
										width: 100%;
										line-height: 32px;
										text-align: center;
										color: #242424;
										background: #ebebeb;
									}
								}

								.bao-het-hang {
									position: relative;
									display: block;
									float: left;
									width: 60px;
									max-width: 75px;
									height: 26px;
									line-height: 26px;
									padding: 0;
									margin-left: 20px;
									margin-left: 9%;
									font-size: 14px;
									white-space: nowrap;
									flex: auto;
									color: #d42027;
								}
								.amount-item {
									position: relative;
									display: block;
									float: right;
									height: 26px;
									line-height: 26px;
									font-size: 14px;
									color: #242424;
									font-weight: bold;
									flex: auto;
									text-align: right;
									white-space: nowrap;
								}
								.show-selected-quantity {
									position: relative;
									display: block;
									float: left;
									width: 95px;
									height: 26px;
									line-height: 26px;
									padding: 0;
									margin-left: 0;
									background: #fff;
									color: #242424;
									p {
										display: inline-block;
									}
									span {
										font-weight: bold;
									}
								}
							}
						}
						.group-attach-sales {
							position: relative;
							display: block;
							float: none;
							width: 100%;
							padding: 10px 0px 0;
							padding-left: 40px;
							vertical-align: middle;
							&:before {
								content: "";
								position: absolute;
								display: block;
								width: 1px;
								height: 100%;
								top: -22px;
								left: 30px;
								bottom: 0;
								background: #f4f4f4;
								z-index: 2;
							}
							.item-sale {
								position: relative;
								display: block;
								display: table;
								display: -webkit-flex;
								display: flex;
								width: 100%;
								clear: both;
								&:not(:last-child) {
									margin-bottom: 10px;
								}
								&:before {
									content: "";
									position: absolute;
									display: block;
									width: 10px;
									height: 1px;
									left: -10px;
									top: 0;
									bottom: 0;
									margin: auto;
									background: #f4f4f4;
									z-index: 2;
								}
								.box-img-attach {
									position: relative;
									width: 45px;
									height: 45px;
									float: left;
									img {
										position: relative;
										display: block;
										width: 100%;
										object-fit: contain;
									}
								}
								.info {
									position: relative;
									display: block;
									float: left;
									width: calc(100% - 45px);
									padding-left: 10px;
									.name {
										position: relative;
										display: block;
										float: left;
										width: 100%;
										color: #242424;
										line-height: 18px;
									}
									p:first-child:before {
										content: "+";
										position: relative;
										display: block;
										width: 25px;
										height: 30px;
										float: left;
										text-align: center;
									}
									p {
										font-size: 14px;
										color: #999999;
									}
								}
								.price {
									position: relative;
									display: block;
									float: left;
									width: 60px;
									text-align: right;
								}
							}
						}
					}
					.wrap-note-order {
						position: relative;
						display: block;
						width: 100%;
						margin-bottom: 15px;
						padding: 0 15px;
						.label-box {
							position: relative;
							display: inline-block;
							vertical-align: top;
							width: 80px;
							line-height: 40px;
							color: #666;
						}
						.area-box {
							position: relative;
							display: inline-block;
							vertical-align: top;
							width: calc(100% - 85px);
							textarea {
								position: relative;
								display: block;
								width: 100%;
								min-width: 100%;
								max-width: 100%;
								min-height: 40px;
								max-height: 200px;
								border: 1px solid #ebebeb;
								border-radius: 5px;
								padding: 8px 10px;
								line-height: 18px;
								font-size: 14px;
								color: #242424;
								outline: none;
								box-shadow: none;
								-webkit-box-shadow: none;
								-moz-box-shadow: none;
							}
						}
					}
					.btn-delete-all {
						position: relative;
						display: block;
						clear: both;
						width: 100%;
						max-width: 200px;
						padding: 5px 15px;
						margin: 10px auto;
						background: #fff;
						text-align: center;
						color: #242424;
						text-decoration: underline;
						border-radius: 5px;
						&:focus {
							background: #d42027;
							color: #fff;
						}
					}
				}
			}
		}

		.wrap-title {
			position: relative;
			display: block;
			width: 100%;
			clear: both;
			.title-section {
				position: relative;
				display: block;
				width: 100%;
				height: 28px;
				line-height: 18px;
				margin: 0;
				text-transform: uppercase;
				font-size: 14px;
				border-bottom: 1px solid #f5f5f5;
				clear: both;
				font-weight: bold;
			}
		}


		.box-invoice {
			position: relative;
			display: block;
			width: 100%;
			background: #fff;
			margin-bottom: 12px;
			.title-box {
				position: relative;
				display: block;
				width: 100%;
				height: 40px;
				line-height: 40px;
				padding: 0 15px;
				border-bottom: 1px solid #efefef;
				h3 {
					margin: 0;
					font-size: 15px;
					line-height: 40px;
					font-weight: bold;
					color: #333333;
					text-transform: uppercase;
				}
			}
			.content-box {
				position: relative;
				display: block;
				width: 100%;
				padding: 10px 15px;
				line-height: 18px;
				font-size: 14px;
				overflow: hidden;
				.content-invoice {
					position: relative;
					display: block;
					width: 100%;
					p {
						line-height: 21px;
						margin-bottom: 8px;
					}
					span {
						text-align: right;
						float: right;
					}
					.hr {
						position: relative;
						display: block;
						width: 100%;
						height: 0;
						margin: 5px 0;
						border-bottom: 1px dashed #efefef;
					}
					.tien-hang {
						position: relative;
					}
					.tong-tien-vat-y {
						font-weight: 700;
						font-size: 18px;
						color: #242424;
					}
				}
				.export-invoice {
					position: relative;
					display: block;
					clear: both;
					padding-top: 8px;
					margin-bottom: 10px;
					z-index: 2;
					cursor: pointer;
					color: #242424;
					&.active {
						.cell-checkbox {
							&:before {
								content: "";
								visibility: visible;
							}
							&:after {
								content: "";
								visibility: visible;
							}
						}
					}
					.cell-checkbox {
						position: relative;
						display: block;
						float: left;
						max-width: 18px;
						width: 18px;
						height: 18px;
						margin: 0 5px 0 0;
						text-align: center;
						line-height: 18px;
						color: #d52329;
						background: #fff;
						border: 1px solid #ebebeb;
						border-radius: 3px;
						&:before {
							position: absolute;
							display: block;
							top: 7px;
							left: 4px;
							content: "";
							width: 2px;
							height: 5px;
							background: #242424;
							-webkit-transform: rotateZ(-45deg);
							-moz-transform: rotateZ(-45deg);
							-o-transform: rotateZ(-45deg);
							transform: rotateZ(-45deg);
							visibility: hidden;
						}
						&:after {
							position: absolute;
							display: block;
							top: 3px;
							left: 9px;
							content: "";
							width: 2px;
							height: 10px;
							background: #242424;
							-webkit-transform: rotateZ(45deg);
							-moz-transform: rotateZ(45deg);
							-o-transform: rotateZ(45deg);
							transform: rotateZ(45deg);
							visibility: hidden;
						}
					}
					.cell-text {
						position: relative;
						display: block;
						float: left;
						width: calc(100% - 20px);
						font-size: 13px;
						padding-left: 10px;
						span {
							position: relative;
							display: block;
							clear: both;
							line-height: 18px;
							font-size: 16px;
							color: #242424;
						}
						span.count-item-pro {
							color: #666666;
							font-size: 13px;
							line-height: 15px;
						}

					}
					.btn-view-invoice {
						color: #1784D3;
						font-style: italic;
						font-size: 12px;
						&:hover {
							text-decoration: underline;
						}
					}
				}
			}
		}

		.wrap-btn-page-giohang {
			// position: relative;
			display: block;
			width: 100%;
						// height: 110px;
			font-size: 14px;
			overflow-y: visible;
			margin-bottom: 40px;
			.box-inner {
						// position: fixed;
				display: block;
				z-index: 999;
				width: 100%;
				max-width: 1110px;
				min-width: 300px;
				height: 110px;
				padding: 0 6px;
				margin: 0 auto;
				bottom: 0;
				left: 0;
				right: 0;
				background: #fff;
				-moz-box-shadow: 0 0 3px rgba(0, 0, 0, 0.15);
				-webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.15);
				box-shadow: 0 0 3px rgba(0, 0, 0, 0.15);
			}
			.box-check-code {
				position: relative;
				display: block;
				width: 100%;
				height: 40px;
				margin-bottom: 10px;
				.title-check-code {
					position: relative;
					display: block;
					float: left;
					width: 95px;
					height: 40px;
					line-height: 40px;
					font-size: 13px;
				}
				.box-coupon {
					position: relative;
					display: block;
					float: left;
					width: calc(100% - 95px);
					font-size: 18px;
					border: 1px solid #d8d8d8;
					border-radius: 4px;
					overflow: hidden;
					input {
						position: relative;
						display: block;
						float: left;
						width: calc(100% - 40px);
						height: 40px;
						line-height: 40px;
						padding: 0 10px;
						text-align: center;
						font-weight: bold;
						border: 0;
						border-right: 0;
						outline: none;
						pointer-events: none;
						box-shadow: none;
						-webkit-box-shadow: none;
						-moz-box-shadow: none;
						&::placeholder {
							font-size: 14px;
							color: #666666;
							font-weight: normal;
						}

						&:-ms-input-placeholder {
							font-size: 14px;
							color: #666666;
							font-weight: normal;
						}

						&::-ms-input-placeholder {
							font-size: 14px;
							color: #666666;
							font-weight: normal;
						}
					}
					.btn-check-code {
						position: relative;
						display: block;
						float: left;
						width: 40px;
						height: 40px;
						line-height: 40px;
						text-align: center;
						color: #a2a2a2;
					}
				}
			}
			.total-cart {
				position: relative;
				display: block;
				width: 100%;
				height: 45px;
				line-height: 45px;
				color: 	#242424;
				margin-bottom: 13px;
				border-bottom: 1px dashed #ebebeb;
				.col-label {
					position: relative;
					display: block;
					float: left;
					width: 50%;
					font-size: 15px;
				}
				.col-value {
					position: relative;
					display: block;
					float: left;
					width: 50%;
					font-size: 18px;
					font-weight: 600;
					text-align: right;
					white-space: nowrap;
				}
			}
			.group-action-order {
				position: relative;
				display: block;
				width: 100%;
				height: 36px;
				line-height: 36px;
				margin: 0 auto;
				color: #fff;
				font-size: 16px;
				text-align: center;
				.col-left {
					position: relative;
					display: block;
					float: left;
					width: 46%;
					color: #242424;
					.btn-select-all-pro {
						position: relative;
						display: block;
						display: flex;
						align-items: center;
						width: 100%;
						height: 36px;
						line-height: 36px;
						text-align: left;
						border-radius: 8px;
						box-shadow: none;
						-webkit-box-shadow: none;
						-moz-box-shadow: none;
						&.active {
							.cell-checkbox {
								&:before {
									content: "";
									visibility: visible;
								}
								&:after {
									content: "";
									visibility: visible;
								}
							}
						}
						.cell-checkbox {
							position: relative;
							display: block;
							float: left;
							max-width: 20px;
							width: 20px;
							height: 20px;
							margin-top: 0;
							text-align: center;
							line-height: 20px;
							color: #0bb451;
							background: #fff;
							border: 1px solid #ebebeb;
							border-radius: 3px;
							&:before {
								position: absolute;
								display: block;
								top: 8px;
								left: 5px;
								content: "";
								width: 2px;
								height: 6px;
								background: #0bb451;
								-webkit-transform: rotateZ(-45deg);
								-moz-transform: rotateZ(-45deg);
								-o-transform: rotateZ(-45deg);
								transform: rotateZ(-45deg);
								visibility: hidden;
							}
							&:after {
								position: absolute;
								display: block;
								top: 4px;
								left: 10px;
								content: "";
								width: 2px;
								height: 11px;
								background: #0bb451;
								-webkit-transform: rotateZ(45deg);
								-moz-transform: rotateZ(45deg);
								-o-transform: rotateZ(45deg);
								transform: rotateZ(45deg);
								visibility: hidden;
							}
						}
						.cell-text {
							position: relative;
							display: block;
							float: left;
							width: calc(100% - 20px);
							font-size: 13px;
							padding-left: 10px;
							span {
								position: relative;
								display: block;
								clear: both;
								line-height: 18px;
								font-size: 14px;
								color: #242424;
							}
							span.count-item-pro {
								color: #666666;
								font-size: 13px;
								line-height: 15px;
							}

						}
					}
				}
				.col-right {
					position: relative;
					display: block;
					float: left;
					width: 54%;
					background: #fff;
					color: #242424;
					.btn-checkout-step-1 {
						position: relative;
						display: block;
						width: 100%;
						height: 36px;
						line-height: 36px;
						text-align: center;
						background: #0BB451;
						color: #fff;
						border-radius: 5px;
						border: 0;
						font-size: 14px;
						font-weight: unset;
						padding: 0 3px;
						outline: none;
						box-shadow: none;
						-webkit-box-shadow: none;
						-moz-box-shadow: none;
						&.disable {
							background: #5a5a5a;
							color: #fff;
						}
					}
				}
			}
		}
	}
	.m-giohang {

			.col-fluid-cart {
				position: relative;
				display: block;
				float: none;
				width: 100%;
				clear: both;
				background: #fff;
				.cart-not-item {
					position: relative;
					display: block;
					width: 100%;
					padding: 20px 15px;
					text-align: center;
					p {
						position: relative;
						margin-bottom: 15px;
					}
					.btn-continue-shoping {
						position: relative;
						display: block;
						max-width: 220px;
						width: 100%;
						height: 40px;
						margin: 15px auto 0;
						text-align: center;
						color: #ffffff;
						background: #d42027;
						font-size: 14px;
						line-height: 40px;
						font-weight: bold;
						text-transform: uppercase;
						border-radius: 6px;
					}
				}
			}
			
	}

