					line-height: 1.4;
				}
			}
			.seminor-content {
				position: relative;
				display: block;
				width: 100%;
				font-size: 16px;
				line-height: 1.5;
				color: #242732;
				overflow: auto;
				img {
					width: auto;
					max-width: 100%;
					height: auto;
				}
			}
			.btn-register {
				position: relative;
				display: inline-block;
				display: table;
				min-width: 85px;
				height: 34px;
				line-height: 34px;
				padding: 0 15px;
				margin: 15px auto;
				background: #5B8C51;
				color: #fff;
				border: 0;
				border-radius: 3px;
				text-align: center;
				text-decoration: none;
				text-transform: uppercase;
				outline: none;
				&.btn_registered {
					background: #C4C4C4;
					pointer-events: none;
				}
				&.btn_ongoing {
					background: #5B8C51;
				}
				&.btn_tookplace {
					background: #C4C4C4;
					pointer-events: none;
				}
			}
		}
		@media (max-width: 575px) {
			.seminor-box-content {
				position: relative;
				display: block;
				vertical-align: top;
				width: 100%;
				padding: 20px 0;
			}
		}
	}

