	.section-contact-booth-detail {
		position: relative;
		display: block;
		padding: 40px 0;
		background: #F7F0EA;
		.contact-inner {
			display: block;
			width: 100%;
			margin: 0 auto;
			overflow: hidden;
		}
		.contact-box-title {
			position: relative;
			display: block;
			width: 100%;
			margin-bottom: 25px;
			vertical-align: top;
			color: #242343;
			text-align: center;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 24px;
				line-height: 31px;
				margin-bottom: 30px;
				text-transform: uppercase;
				@media (max-width: 370px) {
					font-size: 20px;
					line-height: 1.4;
				}
				&:after {
					content: "";
					position: absolute;
					display: block;
					width: 100%;
					height: 5px;
					background: #0BB451;
					bottom: -10px;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
		}
		.col-left {
			width: 50%;
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			padding-right: 50px;
			font-size: 16px;
			line-height: 1.5;
			color: #242343;
			.contact-title {
				margin-bottom: 20px;
				font-family: 'Averta-Semibold';
				font-size: 20px;
				line-height: 30px;
				text-transform: uppercase;
				color: #242343;
				@media (max-width: 370px) {
					font-size: 16px;
					line-height: 1.4;
				}
			}
			.contact-title-2 {
				font-family: 'Averta-Semibold';
				font-size: 18px;
				line-height: 22px;
				text-transform: uppercase;
				color: #5B8C51;
				@media (max-width: 370px) {
					font-size: 16px;
					line-height: 1.4;
				}
			}
			.contact-info {
				ul {
					list-style: none;
					padding: 0;
					margin: 0;
					li {
						display: flex;
						clear: both;
						width: 100%;
						margin-bottom: 15px;
						&:last-child {
							.box-value {
								border: 0;
							}
						}
						a {
							color: inherit;
						}
					}
				}
				p {
					display: table;
					margin-bottom: 8px;
				}
				i, svg {
					display: table-cell;
					width: 25px;
					padding-left: 0;
					padding-top: 5px;
					color: #0BB451;
					font-size: 16px;
					text-align: center;
				}
				.box-icon {
					width: 35px;
					flex: none;
				}
				.box-label {
					width: 140px;
					flex: none;
					font-family: 'Averta-Semibold';
				}
				.box-value {
					width: 100%;
					padding-bottom: 10px;
					border-bottom: 1px solid rgba(196, 196, 196, 0.4);
					font-family: 'Arial';
				}
			}
		}
		.col-right {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 50%;
			&:before {
				content: "";
				position: absolute;
				display: block;
				left: 0;
				top: 0;
				width: 0;
				height: 100%;
				border-left: 1px dashed #242343;
				opacity: 0.5;
			}
			.contact-form {
				position: relative;
				display: block;
				width: 100%;
				font-size: 20px;
				line-height: 1.5;
				color: #242343;
				padding-left: 60px;
				::-webkit-input-placeholder { /* Edge */
					color: #8D8D8D;
				}
				:-ms-input-placeholder { /* Internet Explorer 10-11 */
					color: #8D8D8D;
				}
				::placeholder {
					color: #8D8D8D;
				}
				.form-title {
					position: relative;
					display: inline;
					width: auto;
					padding-bottom: 6px;
					margin-bottom: 25px;
					font-family: 'Averta-Semibold';
					font-size: 20px;
					line-height: 22px;
					text-transform: uppercase;
					color: #242343;
					//border-bottom: 1px solid #C9C9C9;
				}
				label {
					font-family: 'Averta-Semibold';
					font-size: 15px;
					line-height: 18px;
					color: #242343;
				}
				.form-control {
					font-size: 16px;
					&:focus {
						color: #333;
						background-color: #fff;
						outline: 0;
						box-shadow: none;
						border: 1px solid #333;
					}
				}
				textarea {
					max-height: 200px;
					min-height: 100px;
				}
				.btn-submit {
					position: relative;
					display: inline-block;
					padding: 7px 13px;
					border: 0;
					border-radius: 3px;
					background: #2A3A6B;
					font-family: 'Averta-Semibold';
					color: #fff;
					font-size: 14px;
					text-align: center;
					text-decoration: none;
					text-transform: uppercase;
					outline: none;
					box-shadow: none;
				}
			}
		}
		@media (max-width: 991px) {
			.col-left {
				width: 100%;
				float: none;
				padding-right: 0;
				margin-bottom: 40px;
			}
			.col-right {
				width: 100%;
				float: none;
				padding: 0;
				&:before {
					display: none;
				}
				.contact-form {
					padding: 0;
				}
			}
		}
		@media (max-width: 767px) {
			.col-left {
				.contact-info .box-label {
					width: 115px;
				}
			}
		}
	}


	// Start page Q&A
	.section-page-QA {
		position: relative;
		display: block;
		clear: both;
		padding: 30px 0;
		background: #F7F7F7;
		.faq-inner {
			display: block;
		}
		.faq-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			text-align: center;
			z-index: 2;
			.title-name {
				position: relative;
				display: block;
				font-family: 'Averta-Bold';
				font-size: 24px;
				line-height: 29px;
				margin-bottom: 20px;
				padding: 0 0 25px;
				text-transform: uppercase;
				&:before {
					content: "";
					position: absolute;
					width: 104px;
					height: 19px;
					background-image: url("images/bg-title.svg");
					background-repeat: no-repeat;
					background-position: top center;
					background-size: 100% 100%;
					bottom: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					pointer-events: none;
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				font-size: 15px;
				line-height: 17px;
				margin: 0 auto;
				margin-bottom: 40px;
				max-width: 670px;
				text-align: center;
			}
		}
		.faq-box-list {
			position: relative;
			display: block;
			width: calc(100% - 455px);
			float: left;
			vertical-align: top;
			padding-right: 30px;
			.faq-accordion {
				position: relative;
				display: block;
				overflow: hidden;
				width: 100%;
				.card {
					background: #FFFFFF;
					border: 1px solid #E8E8E8;
					border-radius: 0 3px 3px 0;
					margin-bottom: 7px;
				}
				.card-header {
					background: none;
					padding: 10px 0;
				}
				.card-body {
					position: relative;
					border-top: 0;
					background: #fff;
					color: #666666;
					font-size: 15px;
					&:before {
						content: "";
						position: absolute;
						width: 0;
						height: 0;
						left: 15px;
						top: -10px;
						border-left: 10px solid transparent;
						border-right: 10px solid transparent;
						border-bottom: 10px solid #fff;
						filter: drop-shadow(0px -1px 0px #ddd);
					}
				}
				.btn-link {
					position: relative;
					display: block;
					width: 100%;
					text-align: left;
					padding: 10px 50px 10px 20px;
					color: #302B6B;
					font-family: 'Averta-Semibold';
