
	.section-video-livestream-slide {
		position: relative;
		display: block;
		padding: 40px 0;
		background: #EEF3FF;
		overflow: hidden;
		.video-inner {
			display: block;
		}
		.video-box-title {
			position: relative;
			display: block;
			width: 100%;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 20px;
				line-height: 1.4;
				margin-bottom: 15px;
				@media (min-width: 992px) {
					font-size: 30px
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: 103px;
				font-size: 18px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
			}
			a {
				color: #302B6B;
			}
		}
		.video-box-list {
			position: relative;
			display: block;
			float: left;
			vertical-align: top;
			width: 100%;
			.video-list {
				position: relative;
				display: block;
				display: flex;
				flex-wrap: wrap;
				@media (min-width: 576px) {
					overflow: hidden;
				}

				.video-item {
					position: relative;
					display: flex;
					max-width: 700px;
					width: 100%;
					padding: 5px 0;
					margin: 0;
					&.video--live {
					}

					.item-inner {
						position: relative;
						display: block;
						width: 100%;
						background: #FFFFFF;
						border-radius: 14px;
						box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
						overflow: hidden;

						.wrap-image {
							position: relative;
							display: block;
							width: 100%;
							overflow: hidden;
							>a {
								display: flex;
								justify-content: center;
								align-items: center;
								&:hover {
									.icon-video {
										background: rgba(#0BB451, 0.8);
									}
								}
							}
							img {
								width: 100%;
								max-width: 100%;
							}
							.icon-video {
								position: absolute;
								display: flex;
								justify-content: center;
								align-items: center;
								width: 45px;
								height: 45px;
								background: rgba(#0BB451, 0.5);
								color: #fff;
								border-radius: 50%;
								overflow: hidden;
								&:after {
									content: '';
									width: 0;
									height: 0;
									border-top: 10px solid transparent;
									border-left: 15px solid #fff;
									border-bottom: 10px solid transparent;
									margin-left: 4px;
								}
							}
							
						}
						.wrap-info {
							.row-1 {
								position: relative;
								display: flex;
								align-items: center;
								width: 100%;
								overflow: hidden;
								font-family: Arial;
								font-size: 14px;
								line-height: 1.4;
								color: #657195;
								background: #EEF3FF;
								.info-time {
									position: relative;
									display: block;
									min-width: 65px;
									padding: 10px 15px;
									white-space: nowrap;
									font-size: 12px;
									font-weight: bold;
									&:after {
										content: '';
										position: absolute;
										right: 0;
										top: 0;
										bottom: 0;
										margin: auto;
										width: 1px;
										height: 15px;
										background: #B6C0DE;
									}
									
									.live-time {
										position: relative;
										padding-left: 25px;
										font-family: 'Averta-Semibold';
										font-size: 11px;
										color: #DF2F2F;
										&:before {
											content: '';
											position: absolute;
											left: 8px;
											top: 3px;
											width: 9px;
											height: 9px;
											margin-right: 5px;
											border-radius: 50%;
											background: #DF2F2F;
											border: 1px solid #fff;
											box-shadow: 0 0 5px rgba(#DF2F2F,.8);
											pointer-events: none;
										}
										&:after {
											content: '';
											position: absolute;
											left: 7px;
											top: 2px;
											width: 11px;
											height: 11px;
											margin-right: 5px;
											border-radius: 50%;
											// border: 1px solid #DF2F2F;
											box-shadow: 0 0 0 rgba(#DF2F2F,.4);
											vertical-align: initial;
											animation: live-pulse 1s infinite;
											pointer-events: none;
										}
									}
								}
								.info-txt {
									flex: auto;
									position: relative;
									display: block;
									padding: 10px 15px;
									white-space: nowrap;
									overflow: hidden;
									text-overflow: ellipsis;
								}
							}
						}

						.item-title {
							position: relative;
							display: block;
							width: 100%;
							padding: 9px 15px 10px;
							margin: 0;
							.item-title-a {
								position: relative;
								display: block;
								display: -webkit-box;
								max-height: 38px;
								height: 38px;
								padding: 0;
								margin: 0;
								font-family: 'Averta-Semibold';
								font-size: 14px;
								line-height: 1.35;
								color: #242343;
								text-decoration: none;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								word-break: break-word;
								-webkit-line-clamp: 2;
								@media (min-width: 768px) {
									max-height: 44px;
									height: 44px;
									font-size: 16px;
								}
								&:hover {
									color: #0BB451;
								}
							}
						}

					}
				}
			}

			.swiper-pagination-livestream {
				position: relative;
				z-index: 2;
				bottom: 0;
				display: block;
				text-align: center;
				left: 50%;
				transform: translateX(-50%);
				margin: 0 auto;
				.swiper-pagination-bullet {
					width: 8px;
					height: 8px;
					margin: 2px;
					background: rgba(#2A3A6B, 0.5);
					opacity: .5;
					box-shadow: 0 0px 1px rgba(122, 122, 122, 0.5);	
					border-radius: 4px;
					outline: none;
				}
				.swiper-pagination-bullet-active {
					width: 20px;
					background: #2A3A6B;
					border-radius: 3px;
					opacity: 1;
				}
				&.swiper-pagination-lock {
					cursor: default;
					opacity: 0;
				}
			}
			&:hover {
				.swiper-button-next-livestream, .swiper-button-prev-livestream {
					background: rgba(255,255,255,.8);
					//filter: drop-shadow(0px 1px 2px #000);
					opacity: .9;
				}
			}
			.swiper-button-next-livestream, .swiper-button-prev-livestream {
				display: none;
				position: absolute;
				z-index: 6;
				top: 50%;
				width: 35px;
				height: 35px;
				margin-top: -17px;
				background: rgba(255,255,255,.3);
				border-radius: 50%;
				outline: none;
				cursor: pointer;
				opacity: .7;
				box-shadow: 0 1px 5px 0 rgba(0,0,0,0.16), 0 1px 2px 0 rgba(0,0,0,0.26);
				&:after {
					content: "";
					position: absolute;
					display: block;
					top: 0;
					right: 0;
					width: 35px;
					height: 35px;
					line-height: 35px;
					text-align: center;
					vertical-align: middle;
					z-index: 2;
					font-family: 'Font Awesome 5 Pro';
					font-size: 24px;
					font-weight: 300;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					display: inline-block;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
				}
				&.swiper-button-disabled {
					cursor: default;
					opacity: .1;
				}
			}
			.swiper-button-prev-livestream {
				left: 10px;
				right: auto;
				&:after {
					content: "\f104";
				}
			}
			.swiper-button-next-livestream {
				right: 10px;
				left: auto;
				&:after {
					content: "\f105";
				}
			}

			.btn-viewall {
				position: relative;
				display: block;
				width: 100px;
				height: 36px;
				line-height: 36px;
				color: #fff;
				background-color: #2A3A6B;
				border: 0;
				border-radius: 3px;
				font-family: 'Averta-Semibold';
				font-size: 14px;
				text-align: center;
				text-decoration: none;
				margin: 30px auto 15px;
			}

		}

	}



