		padding-bottom: 40px;
		@media(min-width:991px) {
			padding-top: 20px;
		}
		.booth-about-inner {
			position: relative;
			width: 100%;
			.wrap-content {
				display: flex;
				flex-wrap: wrap;
				max-width: 900px;
				width: 100%;
				margin: 0 auto;
				.wrap-booth-first {
					position: relative;
					width: 100%;
					margin: 0 auto;
					padding-right: 0;
					flex: 1;
					font-weight: normal;
					font-size: 15px;
					line-height: 1.4;
					color: $xam;
					text-align: left;
					@media(min-width:991px) {
						width: 48%;
						padding-right: 50px;
						margin: 0 auto;
					}
					.box-title {
						position: relative;
						clear: both;
						overflow: hidden;
						margin-bottom: 15px;
						.logo {
							object-fit: contain;
							max-width: 80px;
							max-height: 80px;
						}
						.title {
							position: relative;
							width: 100%;
							margin-top: 12px;
							font-family: 'Averta-Bold';
							font-size: 20px;
							line-height: 1.3;
							line-height: 24px;
							text-transform: uppercase;
							color: #302B6B;
						}
						span {
							line-height: 1.3;
						}
						
					}
					p {
						font-family: 'Arial';
					}
				}
				.wrap-booth-second {
					position: relative;
					max-width: 100%;
					width: 100%;
					margin: auto;
					font-family: Arial;
					font-size: 14px;
					line-height: 1.5;
					color: #657195;
					@media(min-width:991px) {
						flex:1;
						width: 52%;
					}
					&.max_height_active {
						max-height: 280px;
						overflow: hidden;
						&:after {
							content: "...";
							position: absolute;
							bottom: 0;
							left: 0;
							background: #fff;
							height: 25px;
							width: 100%;
							display: block;
							font-size: 24px;
							line-height: 10px;
							box-shadow: 0 -5px 5px rgb(255, 255, 255), 0 -8px 8px rgb(255, 255, 255), 0 -10px 10px rgb(255, 255, 255);
						}
					}
					video {
						width: 100%;
						height: 390px;
						outline: none;
					}
					iframe {
						max-width: 100%;
					}
				}

				
			}
			.btn-detail {
				position: absolute;
				bottom: -30px;
				left: 0;
				right: 0;
				display: block;
				width: 107px;
				height: 34px;
				padding: 0;
				background: #0BB451;
				border-radius: 3px;
				margin: 0 auto;
				margin-top: 10px;
				color: #fff;
				//font-family: 'Averta-Semibold';
				font-size: 13px;
				line-height: 34px;
				text-align: center;
				&.showless {
					background: #eeeeee;
					color: #333339;
				}
			}
		}
	}