
.agriTechExpo {


	.banner-top {
		position: relative;
		display: block;
		width: 100%;
		overflow: hidden;
		.slide-home {
			position: relative;
			min-height: 140px;
			background-color: #000C24;
			background-repeat: no-repeat;
			background-size: 100% 100%;
			background-position: top center;
			background-size: cover;
			.item-slide {
				width: 100%;
				overflow: hidden;
				.item-slide-inner {
					position: relative;
					display: block;
					width: 100%;
					color: #fff;
					padding: 60px 0 0 0;
					min-height: 450px;
					.box-left {
						position: relative;
						display: block;
						float: left;
						width: 430px;
					}
					.box-right {
						position: absolute;
						display: block;
						bottom: 0;
						right: -100px;
						width: calc(100% - 370px);
						.img-01 {
							max-width: 760px;
						}
					}
					a.link-detail {
						position: absolute;
						display: block;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						cursor: pointer;
					}
				}
				.breadcrumb-event {
					margin-bottom: 10px;
					.breadcrumb-item {
						color: #fff;
						font-family: 'Averta-Black';
						font-size: 18px;
						line-height: 22px;
						text-decoration: none;
						&:before {
							color: #fff;
						}
						&.active {
							font-family: 'Averta';
							font-size: 14px;
						}
					}
				}
				.banner-title {
					width: 100%;
					height: auto;
					left: 0px;
					top: 52px;
					margin-bottom: 30px;
					font-family: 'Averta-Black';
					font-size: 38px;
					line-height: 1.3;
					text-transform: uppercase;
					color: #FFFFFF;
				}
				.banner-des {
					font-size: 16px;
					line-height: 1.5;
					text-align: justify;
					font-family: 'Arial';
					a {
						color: #FFFFFF;
						text-decoration: none;
						font-weight: bold;
						line-height: 22px;
					}
				}

				.intro-countdown {
					position: relative;
					display: block;
					width: 100%;
					color: #fff;
					.countdown-txt {
						position: relative;
						display: block;
						font-size: 15px;
						font-family: 'Averta-Bold';
						line-height: 1.3;
						margin-bottom: 5px;
						i, svg {
							padding-right: 5px;
						}
					}
					.countdown-value {
						position: relative;
						display: block;
						display: flex;
						padding-left: 25px;
						width: 100%;
						white-space: nowrap;
						.attr-time {
							position: relative;
							display: inline-block;
							margin-left: 30px;
							&:first-child {
								margin-left: 0;
								.time-value {
									&:before {
										content: "";
										display: none;
									}
								}
							}
							&:nth-last-child(3),
							&:nth-last-child(4) {
							}
							.time-value {
								position: relative;
								display: block;
								min-width: 60px;
								text-transform: uppercase;
								font-family: 'Averta-Black';
								font-size: 35px;
								line-height: 42px;
								text-align: left;
								&:before {
									content: "";
									position: absolute;
									display: block;
									width: 1px;
									height: 25px;
									background: #41689E;
									top: 11px;
									left: -25px;
									transform: rotate(30deg);
								}
							}
							.time-text {
								position: relative;
								display: block;
								clear: both;
								font-size: 13px;
								line-height: 19px;
								text-transform: lowercase;
							}
						}

					}
				}
			}
			.group-btn-slide {
				position: absolute;
				display: block;
				top: 0;
				bottom: 0;
				width: 100%;
				height: 100%;
				left: 0;
				right: 0;
			}
			.swiper-progress-slide-home {
				position: absolute;
				z-index: 5;
				width: 40px;
				height: 40px;
				left: 15px;
				top: 60%;
				margin: auto;
				transform: translate3d(0px,-80px,0);
				display: none; //old remove
			}
			.swiper-pagination-slide-home {
				position: absolute;
				z-index: 2;
				bottom: 10px;
				display: table;
				left: 50%;
				transform: translateX(-50%);
				margin: 0 auto;
				.swiper-pagination-bullet {
					width: 8px;
					height: 8px;
					margin: 2px;
					background: #fff;
					opacity: .5;
					box-shadow: 0 0px 1px rgba(122, 122, 122, 0.5);	
					border-radius: 4px;
					outline: none;
				}
				.swiper-pagination-bullet-active {
					width: 20px;
					background: #fff;
					opacity: 1;
				}
			}
			.swiper-pagination-slide-home-v {
				position: absolute;
				z-index: 5;
				left: 24px;
				top: 50%; //old 55
				margin: auto;
				display: none;
				.swiper-pagination-bullet {
					display: block;
					width: 10px;
					height: 10px;
					margin: 6px;
					background: #fff;
					opacity: .5;
					box-shadow: 0 0px 1px rgba(122, 122, 122, 0.5);	
				}
				.swiper-pagination-bullet-active {
					background: #fff;
					opacity: 1;
				}
			}
			&:hover {
				.swiper-button-next-slide-home, .swiper-button-prev-slide-home {
					background: rgba(255,255,255,.8);
					//filter: drop-shadow(0px 1px 2px #000);
					opacity: .9;
				}
			}
			.swiper-button-next-slide-home, .swiper-button-prev-slide-home {
				position: absolute;
				z-index: 6;
				top: 50%;
				width: 35px;
				height: 35px;
				margin-top: -17px;
				background: rgba(255,255,255,.3);
				border-radius: 50%;
				outline: none;
				cursor: pointer;
				opacity: .7;
				box-shadow: 0 1px 5px 0 rgba(0,0,0,0.16), 0 1px 2px 0 rgba(0,0,0,0.26);
				&:after {
					content: "";
					position: absolute;
					display: block;
					top: 0;
					right: 0;
					width: 35px;
					height: 35px;
					line-height: 35px;
					text-align: center;
					vertical-align: middle;
					z-index: 2;
					font-family: 'Font Awesome 5 Pro';
					font-size: 24px;
					font-weight: 300;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					display: inline-block;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
				}
				&.swiper-button-disabled {
					cursor: default;
					opacity: .1;
				}
			}
			.swiper-button-prev-slide-home {
				left: 10px;
				right: auto;
				&:after {
					content: "\f104";
				}
			}
			.swiper-button-next-slide-home {
				right: 10px;
				left: auto;
				&:after {
					content: "\f105";
				}
			}
			.item-slide a {
				height: 296px;
			}
		}
		@media (min-width: 992px) {
			.slide-home {
				height: 450px;
				.swiper-slide {
					/* Center slide text vertically */
					display: -webkit-box;
					display: -ms-flexbox;
					display: -webkit-flex;
					display: flex;
					-webkit-box-pack: center;
					-ms-flex-pack: center;
					-webkit-justify-content: center;
					justify-content: center;
					-webkit-box-align: center;
					-ms-flex-align: center;
					-webkit-align-items: center;
					align-items: center;
					overflow: hidden;
				}
			}
		}
		@media (max-width: 991px) {
			.slide-home {
				.item-slide {
					.item-slide-inner {
						padding: 50px 0 0;
						height: auto;
						overflow: hidden;
						.box-left {
							width: 100%;
							padding-top: 0;
						}
						.box-right {
							display: none;
						}
					}
					.banner-title {
						margin-bottom: 20px;
						font-size: 32px;
						line-height: 40px;
					}
					.intro-countdown {
						.countdown-value {
							.attr-time {
								position: relative;
								display: inline-block;
								margin-left: 25px;
								.time-value {
									min-width: 42px;
									padding-right: 5px;
									font-size: 28px;
									line-height: 40px;
									&:before {
										left: -22px;
									}
								}
								.time-text {
								}
							}

						}
					}
				}
				.group-btn-slide {
					.swiper-progress-slide-home {
						display: none;
					}
				}
				.swiper-pagination-slide-home-v {
					left: auto;
					right: auto;
					top: auto;
					bottom: 10px;
					margin: 0 auto;
					transform: none;
					.swiper-pagination-bullet {
						display: inline;
					}
				}
			}
		}
		@media (max-width: 991px) {
			.slide-home {
				.item-slide {
					.banner-title {
						font-size: 26px;
					}
				}
			}
		}
	}
	
	.banner-top-small {
		position: relative;
		display: block;
		width: 100%;
		overflow: hidden;
		display: none; //Da bo trong design - update T09
		.banner-img {
			position: relative;
			display: block;
			width: 100%;
			height: 140px;
			background-color: #698129;
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			display: flex;
			align-items: center;
			text-align: center;
			.title-banner {
				font-family: "Averta-Black";
				font-size: 38px;
				line-height: 46px;
				text-transform: uppercase;
				color: #FFFFFF;
				margin: 0;
			}
		}
		@media (max-width: 767px) {
			.banner-img .title-banner {
				font-size: 24px;
				line-height: 26px;
			}
		}
	}


}
