
/*===================================*
  16.START BREADCRUMB STYLE
*===================================*/
	.box-breadcrumb {
		position: relative;
		display: block;
		display: flex;
		align-items: center;
		flex-wrap: nowrap;
		width: 100%;
		height: 35px;
		background: #F7F0EA;
		font-size: 16px;
		padding: 0 30px;
		white-space: nowrap;
		overflow-x: auto;
		.item-breadcrumb {
			position: relative;
			margin-right: 30px;
			color: inherit;
			height: 100%;
			line-height: 35px;
			&+.item-breadcrumb {
				&:before {
					content: "|";
					color: #fff;
					position: absolute;
					top: 0;
					left: -15px;
				}
			}
			&:last-child {
				font-family: 'Averta-Semibold';
			}
			a {
				color: inherit;
				text-decoration: none;
			}
		}
	}
/*===================================*
  16.END BREADCRUMB STYLE
*===================================*/


/* Home Pagination Area css
============================================================================================ */

	.line-dashed {
		position: relative;
		display: block;
		width: 100%;
		height: 0;
		margin: 10px auto;
		border-bottom: 1px dashed #0BB451;
		opacity: 0.5;
	}
	// Start .pagination
	.box-pagination {
		text-align: center;
	}
	ul.pagination {
		display: block;
		display: inline-block;
		padding-left: 0;
		padding: 15px 0 5px;
		margin: 0;
		border-radius: 4px;
		list-style: none;
		float: none !important;
		li {
			display: inline;
			a {
				position: relative;
				width: auto !important;
				height: auto !important;
				line-height: 30px;
				float: left;
				padding: 0 11px !important;
				margin-left: -1px !important;
				color: #333 !important;
				text-decoration: none !important;
				border: 1px solid transparent !important;
				background: rgba(#2A3A6B, 0.2);
				border-radius: 3px;
				font-size: 16px;
				&:hover {
					background: rgba(#2A3A6B, 0.8);
					color: #fff !important;
				}
				&.active {
					background-color: #2A3A6B;
					color: #fff !important;
					pointer-events: none;
					cursor: default;
				}
				i {
					font-weight: 300;
				}
			}
			&:not(:last-child) {
				a {
					margin-right: 6px;
				}					
			}
		}
	}
	// End .pagination
/* Home Pagination Area css
============================================================================================ */




.ldp-hoz-scrollable-menu {
	position: relative;
	display: block;
	width: 100%;
	overflow: hidden;
	z-index: 8;
	top: 40px;
	@media (max-width: 991px) {
		display: none !important;
	}
    .ldp-pn-ProductNav_Wrapper {
		position: relative;
		height: 50px;
		@media (min-width: 1400px) {
			padding: 0 50px;
		}
	}

	.ldp-pn-ProductNav {
		position: relative;
		display: block;
		width: 100%;
		margin-bottom: 0;
		height: 50px;
		float: left;
		background-color: rgba(242, 114, 39, .9);
		overflow-x: auto;
		overflow-y: hidden;
		-webkit-overflow-scrolling: touch;
		white-space: nowrap;
		-ms-overflow-style: -ms-autohiding-scrollbar;
		&::-webkit-scrollbar {
			display: none;
		}
		@media (max-width: 767px) {
			height: 60px;
		}
	}

	.ldp-pn-ProductNav_Contents {
		position: relative;
		display: block;
		transition: transform 0.2s ease-in-out;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 5px 10px;
		margin: 0 auto;
		text-align: center;
		cursor: pointer;
		max-width: 100%;
		overflow-x: auto;
		@media (max-width: 767px) {
			margin: 3px auto 0;
		}
		&.active {
			background: rgba(255,255,255,0.1);
			cursor: grabbing;
			cursor: -webkit-grabbing;
		}
		&::-webkit-scrollbar {
			display: none;
		}
	}

	.ldp-pn-ProductNav_Contents-no-transition {
		transition: none;
	}

	.ldp-pn-ProductNav_Link {
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: 100px;
		height: 40px;
		padding: 0 5px;
		margin-right: 5px;
		font-size: 12.5px;
		text-align: center;
		text-decoration: none;
		white-space: nowrap;
		color: #fff;
		border: 1px solid transparent;
		border-radius: 34px;
		font-family: -apple-system, sans-serif;
		& + & {
			border-left-color: #eee;
		}
		&.active {
			font-weight: bold;
			color: #fee512;
			background: rgba(255, 255, 255, 0.2);
			svg path[fill] {
				fill: #fee512;
			}
		}
		&:not(.active):hover {
			color: #ffffff;
			background: rgba(255, 255, 255, 0.4);
		}
	}

	.ldp-pn-ProductNav_Indicator {
		position: absolute;
		bottom: 0;
		left: 0;
		height: 4px;
		width: 100px;
		background-color: transparent;
		transform-origin: 0 0;
		transition: transform 100ms ease-in-out, background-color 100ms ease-in-out;
	}

}
.ldp-menu-fixed {
	position: fixed !important;
	z-index: 9;
	width: 100%;
	height: 50px;
	margin: auto;
	background-color: rgba(242, 114, 39, .9);
	box-shadow: 0 1px 3px #00000038;
	top: 50px;
	@media (min-width: 992px) {
		top: 60px;
	}
	@media (max-width: 767px) {
		display: none !important;
	}
}
.space-ldp-nav-top-fixed {
	position: relative;
	display: none;
	width: 100%;
	clear: both;
	height: 50px;
	@media (max-width: 767px) {
		display: none !important;
	}
	&.show {
		display: block;
	}
}