:root {
  --oFair-theme-color: #004989;
  --oFair-btn-color: #0BB451;
}
$white: #f5f5f5;
$dark: #2c3e50;
$lighter: #32465a;
$lightest: #496886;
$light: #435f7a;
$red: #e74c3c;
$green: #2ecc71;
$yellow: #f1c40f;
$gray: #95a5a6;
$break: 767px;
$break-big: 991px;
$bg-aside: #EAF2FF;
@mixin transition($transition...) {
	-moz-transition:	$transition;
	-o-transition:	  $transition;
	-webkit-transition: $transition;
	transition:		 $transition;
}
.agriTechExpo {

	// css style default
	.section-sermina {
		position: relative;
		display: block;
		padding: 20px 0 30px;
		overflow: hidden;
		.sermina-inner {
			display: block;
		}
		.sermina-box-title {
			position: relative;
			display: block;
			width: 100%;
			vertical-align: top;
			color: #242343;
			overflow: hidden;
			background-repeat: no-repeat;
			background-position: top left;
			background-size: cover;
			padding: 10px 0 10px;
			margin-bottom: 15px;
			text-align: center;
			.box-title-inner {
				position: relative;
				display: block;
			}
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 24px;
				line-height: 31px;
				margin-bottom: 10px;
				text-transform: uppercase;
				&:after {
					content: "";
					position: absolute;
					display: block;
					width: 100%;
					height: 5px;
					background: #0BB451;
					bottom: -10px;
				}
				@media (max-width: 370px) {
					font-size: 20px
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				height: auto;
				font-size: 16px;
				line-height: 1.4;
				text-align: justify;
				margin-bottom: 25px;
				overflow: hidden;
				clear: both;
				flex: 0;
				display: none;
			}
			.btn-viewmore {
				position: relative;
				display: block;
				width: 100%;
				height: 50px;
				line-height: 50px;
				color: #fff;
				border: 1px solid #FFFFFF;
				border-radius: 5px;
				font-size: 18px;
				text-align: center;
				text-decoration: none;
			}
		}
