								align-self: center;
								.time-txt {
									position: relative;
									width: 100%;
									clear: both;
									display: block;
									// color: #ED1C94;
									&.registered-txt {
										color: #5b8c51;
									}
									&.ongoing-txt {
										color: #FF4343;
									}
									&.ended-txt {
										color: #828282;
									}
									
								}
							}
						}
						.item-title {
							position: relative;
							display: block;
							width: 100%;
							margin: 0 0 12px;
							padding: 0;
							.item-title-a {
								position: relative;
								display: block;
								display: -webkit-box;
								height: 43px;
								margin: 0;
								font-family: 'Averta-Bold';
								font-size: 14px;
								line-height: 1.5;
								color: #242343;
								text-decoration: none;
								text-transform: uppercase;
								overflow: hidden;
								-webkit-box-orient: vertical;
								text-overflow: ellipsis;
								-webkit-line-clamp: 2;
								word-break: break-word;
							}
						}
						.item-description {
							display: -webkit-box;
							height: 64px;
							margin: 0 0 15px;
							color: #666666;
							font-family: Arial;
							font-size: 14px;
							line-height: 1.5;
							overflow: hidden;
							-webkit-box-orient: vertical;
							text-overflow: ellipsis;
							-webkit-line-clamp: 4;
						}
						.btn-register {
							position: relative;
							display: table;
							width: auto;
							min-width: 85px;
							max-width: 100%;
							padding: 0 15px;
							height: 34px;
							line-height: 34px;
							background: #109E4C;
							color: #fff;
							border-radius: 3px;
							text-align: center;
							text-decoration: none;
							vertical-align: top;
							&.btn_registered {
								background: #C4C4C4;
								pointer-events: none;
							}
							&.btn_ongoing {
								background: #109E4C;
							}
							&.btn_tookplace {
								background: #C4C4C4;
								pointer-events: none;
							}
						}
					}
				}
			}

			&:hover {
				.swiper-button-prev-sermina, .swiper-button-next-sermina {
					background: #f7f0ea;
					//filter: drop-shadow(0px 1px 2px #000);
					opacity: 1;
				}
			}
			.swiper-pagination-sermina {
				text-align: center;
				.swiper-pagination-bullet {
					width: 8px;
					height: 8px;
					margin: 0 2.5px;
					background: #CFCFCF;
					border-radius: 4px;
					opacity: 1;
					outline: none;
				}
				.swiper-pagination-bullet-active {
					background: #0BB451;
					border-radius: 4px;
					width: 20px;
				}
			}
			.swiper-button-prev-sermina, .swiper-button-next-sermina {
				position: absolute;
				z-index: 6;
				top: 25%;
				width: 30px;
				height: 30px;
				margin-top: -15px;
				background: rgba(247, 240, 234,.3);
				outline: none;
				cursor: pointer;
				opacity: .7;
				background: #FFFFFF;
				box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.5);
				border-radius: 50%;
				&:after {
					content: "";
					position: absolute;
					display: block;
					top: 0;
					right: 0;
					width: 30px;
					height: 30px;
					line-height: 30px;
					text-align: center;
					vertical-align: middle;
					z-index: 2;
					font-family: 'Font Awesome 5 Pro';
					font-size: 24px;
					font-weight: 300;
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					display: inline-block;
					font-style: normal;
					font-variant: normal;
					text-rendering: auto;
				}
				&.swiper-button-disabled {
					cursor: default;
					opacity: 0;
				}
			}
			.swiper-button-prev-sermina {
				left: -15px;
				right: auto;
				&:after {
					content: "\f104";
				}
			}
			.swiper-button-next-sermina {
				right: -15px;
				left: auto;
				&:after {
					content: "\f105";
					text-indent: 3px;
				}
			}
		}


		@media (max-width: 1199px) {

			.sermina-box-title {
				.title-sub {
					font-size: 16px;
				}
			}
		}

		@media (max-width: 991px) {

			.sermina-inner {
				display: block;
			}
			.sermina-box-title {
				width: 100%;
				clear: both;
				margin-bottom: 0;
				.box-title-inner {
					position: relative;
					top: unset;
					transform: unset;
					padding: 0;
					align-items: center;
					display: flex;
					min-height: 60px;
				}
				.title-sub {
					height: auto;
					font-size: 16px;
					margin-bottom: 10px;
				}
			}
			.sermina-box-list {
				width: 100%;
				clear: both;
				.sermina-list .sermina-item .item-inner {
					.tag-time {
						height: 30px;
						.time-icon {
							width: 30px;
							height: 30px;
							line-height: 30px;
						}
						.time-date {
							font-size: 13px;
						}
					}
				}
			}
		}

		@media (max-width: 767px) {
			.sermina-box-title {
				.btn-viewmore {
					width: 120px;
					height: 40px;
					line-height: 40px;
					font-size: 16px;
				}
			}
