			// 		}
			// 	}
			// }
		}
		@media (max-width: 419px) {
			.sermina-box-list {
				.sermina-list .sermina-item {
					.item-inner {
						.item-image .tag-time {
							width: 52px;
							height: 40px;
							.time-day {
								font-size: 18px;
								line-height: 21px;
							}
							.time-month {
								font-size: 12px;
								line-height: 16px;
							}
						}
						.btn-register {
							margin: 0 auto;
						}
					}
				}
			}
		}
	}

