	.section-team {
		position: relative;
		display: block;
		padding: 40px 0;
		z-index: 0;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			// bottom: 0;
			left: 0;
			margin: auto;
			width: 100%;
			height: 100%;
			background: #EEF3FF;
			@media (min-width: 1400px) {
				top: -50px;
				height: 118%;
				background: url("../images/bg-isosceles-trapezoid-radius.png");
				background-repeat: no-repeat;
				background-size: 100% 100%;
				background-position: center;
			}
		}
		.team-inner {
			display: block;
		}
		.team-box-title {
			position: relative;
			display: block;
			clear: both;
			width: 100%;
			text-align: center;
			.title-name {
				position: relative;
				display: inline-block;
				font-family: 'Averta-Bold';
				font-size: 20px;
				line-height: 31px;
				margin-bottom: 10px;
				@media (min-width: 992px) {
					font-size: 30px
				}
			}
			.title-sub {
				position: relative;
				display: block;
				width: 100%;
				font-size: 15px;
				line-height: 1.7;
				text-align: justify;
				margin: 0 auto;
				margin-bottom: 25px;
				max-width: 670px;
				text-align: center;
				color: #666666;
			}
		}
		.team-box-list {
			position: relative;
			display: block;
			vertical-align: top;
			width: 100%;
			text-align: center;
			.team-list {
				display: block;
				overflow: hidden;
				text-align: center;
				display: flex;
				flex-wrap: wrap;
				justify-content: space-around;
				@include gap(2,20px);

				@media (min-width: 768px) {
					@include gap(4,40px);
				}
				.item-team {
					display: block;
					display: inline-block;
					vertical-align: middle;
					align-self: baseline;
					cursor: pointer;
					.item-inner {
						display: block;
						.item-image {
							display: block;
							overflow: hidden;
							.item-image-a {
								position: relative;
								display: block;
								margin: 0 auto 10px;
								max-width: 100%;
								svg {
									max-width: 100%;
									pointer-events: none;
								}
							}
							img {
								max-width: 100%;
								max-height: 150px;
								margin: auto;
							}
						
							.item-name {
								font-family: 'Averta-Bold';
								font-size: 16px;
								line-height: 19px;
								text-align: center;
								color: #333333;
							}
							.item-position {
								font-size: 12px;
								line-height: 1.5;
								text-align: center;
								color: #666666;
							}
						}
						.wrap-item-txt {
							display: block;
							opacity: 0;
							overflow: hidden;
							.space-item-txt {
								position: relative;
							}
							.item-txt {
								display: block;
								position: absolute;
								overflow: hidden;
								width: 100%;
								height: 0;
								background: #fff;
								padding: 15px 30px 15px 20px;
								text-align: left;
								left: 0;
								font-size: 15px;
								line-height: 1.6;
								color: #333333;
								border: 1px solid #DDDDDD;
								z-index: -1;
								will-change: height;
								cursor: auto;
								.close {
									position: absolute;
									z-index: 2;
									display: block;
									width: 30px;
									height: 30px;
									right: 0;
									top: 0;
									line-height: 30px;
									text-align: center;
									font-size: 18px;
									cursor: pointer;
								}
							}
							&.active {
								display: block;
								.item-txt {
									height: auto;
									//z-index: 3;
								}
							}
						}
					}

					&:hover {
						.item-inner .item-image .item-image-a {
						}
					}
					&.active {
						.item-inner {
							.wrap-item-txt {
								display: block;
								overflow: visible;
								opacity: 1;
								&:before {
									content: "";
									position: relative;
									display: block;
									z-index: 4;
									top: 3px;
									left: 0;
									right: 0;
									margin: 0 auto;
									width: 0;
									height: 0;
									border-left: 10px solid transparent;
									border-right: 10px solid transparent;
									border-bottom: 10px solid #fff;
									filter: drop-shadow(0px -2px 0px #ddd);
								}
								.item-txt {
									display: block;
									height: auto;
									z-index: 3;
								}
							}
						}
					}
				}
			}
		}

		@media (max-width: 991px) {
			.team-box-list {
				.team-list {
					.item-team {
						width: calc(100% / 2);
					}
				}
			}
		}
		@media (max-width: 479px) {
			.team-box-list {
				.team-list {
					.item-team {
						.item-inner .item-image .item-image-a {
							svg {
								width: 150px;
								height: 150px;
							}
						}
					}
				}
			}
		}
		@media (max-width: 374px) {
			.team-box-list {
				.team-list {
					.item-team {
						.item-inner .item-image .item-image-a {
							svg {
								width: 120px;
								height: 120px;
							}
						}
					}
				}
			}
		}

	}
