

/* Menu mobile area css
============================================================================================ */
.modal-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1040;
    background-color: #000;
    overflow: hidden;
}
.modal-nav-side-menu {
    padding: 0 !important;
    .modal-dialog {
        left: 0;
        float: left;
        margin: 0;
        height: 100%;
        width: 90%;
        max-width: 388px;
        .modal-content {
            height: 100%;
            max-height: 100%;
            overflow: auto;
            border: 0;
            border-radius: 0;
        }
    }

    .nav-side-menu {
        position: fixed;
        display: block;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: #5B8C51;
        background: linear-gradient(140deg, #5B8C51 0%, rgba(165, 205, 57, 0.90) 100%);
        color: #fff;
        font-size: 16px;
        z-index: 999;
        overflow: auto;
        transform: translate(0,0);
        pointer-events: auto;
        &.show {
            display: block;
        }
        @media all and (min-width: 400px) {
            width: 388px;
        }
        a {
            color: inherit;
            outline: none;
        }
        .btn-close-nav {
            position: fixed;
            display: block;
            width: 32px;
            height: 32px;
            padding: 3px;
            top: 0;
            right: 0;
            line-height: 28px;
            text-align: center;
            background: rgba(255, 255, 255, 0.56);
            color: #292929;
            font-size: 16px;
            z-index: 2;
            cursor: pointer;
            svg {
                vertical-align: top;
                path {
                    fill: transparent;
                    stroke: currentColor;
                    stroke-linejoin: round;
                    stroke-width: 3;
                }
            }
        }
        .menu-list {
            position: relative;
            display: block;
            width: 100%;
            overflow: hidden;
            overflow-y: auto;
            nav {
                margin: 0;
                padding: 0;
            }
            ul { 
                list-style: none;
                margin: 0;
                padding: 35px 15px 15px;
                display: flex;
                flex-direction: column;
                li { 
                    flex: auto;
                    margin-bottom: 5px;
                    a { 
                        position: relative;
                        display: block;
                        font-family: Averta;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 14px;
                        line-height: 40px;
                        padding: 0 15px;
                        text-decoration: none;
                        text-transform: uppercase;
                        &:hover {
                            background: #242343;
                            color: inherit;
                            border-radius: 5px;
                        }
                        &.active {
                            background: #242343;
                            border-radius: 5px;
                        }
                    }
                }
            }
        }

    }

}

